import { Component, OnInit } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ClientesService } from '../../../core/consultaService/clientes.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { PedidosService } from '../../../core/consultaService/pedidos.service';
import { AuthService } from '../../../core/consultaService/auth.service';
const swal = require('sweetalert');

@Component({
  selector: 'app-rutasPopUp',
  templateUrl: './rutasPopUp.component.html',
  styleUrls: ['./rutasPopUp.component.scss']
})
export class RutasPopUpComponent implements OnInit {
  origen: string;
  parametro: any;
  parametroDos: any;
  titulo: string;
  cargando = false;
  datos: Array <any> = [];
  request: any = {};
  rutaPedidos: any = {};
  user: any = {};
  rutcliente: any;
  rutoperador: any;
  constructor(private setearDatos: ConsultasService, public rutasModal: BsModalRef,
    private modalService: BsModalService, private consultaRutas: ClientesService,
    private rutasPedidos: PedidosService, private authService: AuthService) {
      if (this.consultaRutas.selectedCliente !== undefined) {
        this.rutcliente = this.consultaRutas.selectedCliente.cli_nrutcliente;
        this.rutoperador = this.consultaRutas.selectedCliente.cli_nrutoperador;
      }
     }

  ngOnInit() {
    this.cargando = true;
    this.user = this.authService.getDatosUser();
    switch (this.origen) {
      case 'lunes': {
        this.titulo =  'Rutas';
        this.request.nRutOperador = this.rutoperador;
        this.request.sDia = 'LUNES';
        this.setearDatos.getRutas(this.request)
        .subscribe((data: any) => {
            (this.datos = data.rutas);
            this.cargando = false;

            if (data.error.codError != 0) {
              this.rutasModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        },
        error => {
          this.rutasModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
      case 'martes': {
        this.titulo =  'Rutas';
        this.request.nRutOperador = this.rutoperador;
        this.request.sDia = 'MARTE';
        this.setearDatos.getRutas(this.request)
        .subscribe((data: any) => {
            (this.datos = data.rutas);
            this.cargando = false;
            if (data.error.codError != 0) {
              this.rutasModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        },
        error => {
          this.rutasModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
      case 'miercoles': {
        this.titulo =  'Rutas';
        this.request.nRutOperador = this.rutoperador;
        this.request.sDia = 'MIERC';
        this.setearDatos.getRutas(this.request)
        .subscribe((data: any) => {
            (this.datos = data.rutas);
            this.cargando = false;

            if (data.error.codError != 0) {
              this.rutasModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        },
        error => {
          this.rutasModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }case 'jueves': {
        this.titulo =  'Rutas';
        this.request.nRutOperador = this.rutoperador;
        this.request.sDia = 'JUEVE';
        this.setearDatos.getRutas(this.request)
        .subscribe((data: any) => {
            (this.datos = data.rutas);
            this.cargando = false;

            if (data.error.codError != 0) {
              this.rutasModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        },
        error => {
          this.rutasModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
      case 'viernes': {
        this.titulo =  'Rutas';
        this.request.nRutOperador = this.rutoperador;
        this.request.sDia = 'VIERN';
        
        this.setearDatos.getRutas(this.request)
        .subscribe((data: any) => {
          
            (this.datos = data.rutas);
            this.cargando = false;

            if (data.error.codError != 0) {
              this.rutasModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        },
        error => {
          this.rutasModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
      case 'sabado': {
        this.titulo =  'Rutas';
        this.request.nRutOperador = this.rutoperador;
        this.request.sDia = 'SABAD';
        
        this.setearDatos.getRutas(this.request)
        .subscribe((data: any) => {
          
            (this.datos = data.rutas);
            this.cargando = false;

            if (data.error.codError != 0) {
              this.rutasModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        },
        error => {
          this.rutasModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
      case 'rutaPedido': {
        this.titulo =  'Rutas';
        this.rutaPedidos.codSucursal = this.parametro;
        this.rutaPedidos.fhoRecorrido = this.parametroDos.replace(/-/g, '');
        this.rutaPedidos.rutOperador = this.user.nrutoperador;
        console.log(this.rutaPedidos);
        this.rutasPedidos.getRutassPedidos(this.rutaPedidos)
        .subscribe((data: any) => {
            
            (this.datos = data.buscaRutas);
            this.cargando = false;

            if (data.error.codError !== 0) {
              this.rutasModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        },
        error => {
          this.rutasModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
   }
  }
  enviarInfo(dato) {
    this.setearDatos.setDatosGeo(dato);
    this.rutasModal.hide();
  }


}
