import { UtilService } from './../../../services/util.service';
import { DocumentosService } from './../../../core/consultaService/documentos.service';
import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl
} from '@angular/forms';
import { ClientesService } from '../../../core/consultaService/clientes.service';
import { id } from '@swimlane/ngx-datatable/release/utils';


@Component({
  selector: 'app-cliente-o-local-pop-up',
  templateUrl: './clienteOlocalPopUp.component.html',
  styleUrls: ['./clienteOlocalPopUp.component.scss']
})
export class ClienteOlocalPopUpComponent implements OnInit {

  origen: string;
  titulo: string;
  forma: FormGroup;
  forma2: FormGroup;
  clientes: any;
  clientesData: any;
  cliente: any;
  cargando = false;

  //request
  request: any = {};
  nombre: any;
  rutOperador: any;
  rutClientes: any;
  requestLocal = this.documentosService.localRequest;
  localesArray: any = [];
  iterado: Array<any> = [];
  modalFor = this['modalFor'];

  constructor(
    public bsModalRef: BsModalRef,
    private clientesService: ClientesService,
    private consultasService: ConsultasService,
    private documentosService: DocumentosService,
    private utilService:UtilService
  ) {
    this.forma2 = new FormGroup({
      nombreCliente: new FormControl(),

    });
  }

  ngOnInit() {
    this.request.rutOperador = JSON.parse(localStorage.getItem('DatosUsuario'))[
      'nrutoperador'
    ];
    this.request.rutClientes = 0;
    console.log('requestLocal', this.requestLocal);
    this.getLocales()
  }

  searchCliente() {
    this.cargando = true;

if (this.forma2.controls.nombreCliente.value != null || this.forma2.controls.nombreCliente.value == '') {
  this.request.nombre = this.forma2.controls.nombreCliente.value;
} else {
  this.request.nombre = ""
}
    this.clientesService.getClientes(this.request).subscribe(data => {
      this.cargando = false;
      this.clientes = data;
      this.clientesData = this.clientes['cliente'];
      this.iterado = [];
      this.iterado =  this.clientesData
    });
  }

  getLocales() {
    this.clientesService.getLocales(this.requestLocal).subscribe(data => {
      console.log('data', data);
      console.log('data', data['locales'])

      this.localesArray = data['locales'];
      console.log('this.localesArray', this.localesArray)
    });
  }

  enviarInfo(dato) {
    console.log(dato);
    this.consultasService.setDatos(dato);
    this.bsModalRef.hide();
  }

  cerrarModal(){
    this.bsModalRef.hide();
  }
}

