import { Component, OnInit } from '@angular/core';
import * as _ from 'lodash';
import { HttpClient } from '@angular/common/http';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';

import { NgxSpinnerService } from 'ngx-spinner';
import { AuthService } from '../../../core/consultaService/auth.service';
import { Router } from '@angular/router';
import { AdministracionOperativaService } from '../../../core/consultaService/administracionOperativa.service';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';

declare var jQuery: any;
declare var $: any;

const swal = require('sweetalert');

@Component({
  selector: 'app-tarifasPopUp',
  templateUrl: './tarifasPopUp.component.html',
  styleUrls: ['./tarifasPopUp.component.scss']
})
export class TarifasPopUpComponent implements OnInit {
  forma: FormGroup;
  tiposTarifas: Array<any> = [
    {
      sCodTipoRecorrido: null,
      descTarifa: '(Todos)'
    },
    {
      sCodTipoRecorrido: 'MAYOR',
      descTarifa: 'Mayorista'
    },
    {
      sCodTipoRecorrido: 'NORMA',
      descTarifa: 'Normal'
    },
    {
      sCodTipoRecorrido: 'XDOCK',
      descTarifa: 'X-Dock'
    }
  ];
  user: any = {};
  request: any = {};
  tarifas: Array <any> = [];
  titulo: string;
  origen: string;
  constructor(public http: HttpClient, fb: FormBuilder,
    private tarifasService: AdministracionOperativaService,
    public loader: NgxSpinnerService, private authService: AuthService,
    public tarifasModal: BsModalRef, private modalService: BsModalService) {
      this.forma = new FormGroup ({
        nRutOperador: new FormControl(''),
        sCodTipoRecorrido: new FormControl(''),
      });
     }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
    switch (this.origen) {
      case 'tarifasRutas': {
        this.titulo =  'Tarifas';
        this.listarTarifas ();
      break;
      }
    }
  }
  listarTarifas () {
    this.forma.controls.nRutOperador.setValue(Number(this.user.nrutoperador));
    this.forma.controls.sCodTipoRecorrido.setValue(null);
    this.request = this.forma.value;
    this.tarifasService.getTarifasTransportistas(this.request)
        .subscribe((data: any) => {
            this.loader.hide();
                if (data.tarifas.length === 0 && data.error.codError === 0 ) {
                    swal('Atención', 'No se encontraron resultados', 'info');
                } else {
                    if (data.tarifas === null) {
                        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                    } else {
                        (this.tarifas = data.tarifas);
                    }
                }
                if (data.error.codError !== 0) {
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                }
        },
        error => {
            this.loader.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        }
    );
  }
  buscar($event: any) {
    if (!this.forma.invalid) {
        this.loader.show();
        this.request = this.forma.value;
        this.tarifasService.getTarifasTransportistas(this.request)
            .subscribe((data: any) => {
                this.loader.hide();
                    if (data.tarifas.length === 0 && data.error.codError === 0 ) {
                        swal('Atención', 'No se encontraron resultados', 'info');
                    } else {
                        if (data.tarifas === null) {
                            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                        } else {
                            (this.tarifas = data.tarifas);
                        }
                    }
                    if (data.error.codError !== 0) {
                        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                    }
            },
            error => {
                this.loader.hide();
                swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        );
    }
}
  enviarInfo(tarifa) {
    this.tarifasService.setDatos(tarifa);
    this.tarifasModal.hide();
  }

}
