  // 20190903 => Martes
  dayOfWeek(dateStr: string) {
    let anio = dateStr.substr(0, 4);
    let mes = dateStr.substr(4, 2);
    let dia = dateStr.substr(6, 2);

    let d = new Date(`${anio},${mes},${dia}`);
    let weekday = new Array(7);

    weekday[0] = "Domingo";
    weekday[1] = "Lunes";
    weekday[2] = "Martes";
    weekday[3] = "Miercoles";
    weekday[4] = "Jueves";
    weekday[5] = "Viernes";
    weekday[6] = "Sábado";

    return weekday[d.getDay()];
  }

  // "01" => 31
  lastDayOfMonth(month: string) {
    let d = new Date(2019, Number(month), 0);
    return d.getDate();
  }

  // dd/mm/yyyy    '13/11/2019','15/12/2019' => 32
  diffDays2Dates(date1:string, date2:string) {

  let anio1 = date1.substr(0, 4);
  let mes1 = date1.substr(4, 2);
  let dia1 = date1.substr(6, 2);

  let anio2 = date2.substr(0, 4);
  let mes2 = date2.substr(4, 2);
  let dia2 = date2.substr(6, 2);

  let ds1 = new Date(`${mes1},${dia1},${anio1}`);
  let ds2 = new Date(`${mes2},${dia2},${anio2}`);

  const d1:any = new Date(ds1);
  const d2:any = new Date(ds2);
  const diffTime = (d2 - d1);
  return Math.ceil(diffTime / (1000 * 60 * 60 * 24)); 
  }

  // 12345678 => 12.345.678
  formatDotNumber(num) {
    return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1.");
  }

  //33.333.333-3 => 333333333
  stringFormatSinPuntos(num) {
    let numero = num.replace(/[-\/\.]/g, "");
    return numero;
  }

  //12.345.678-9 => 12345678 || 12.345.678 => 12345678
  rutStringFormatToRequest(str: string) {
    if (/[0-9]{1,2}[.][0-9]{3}[.][0-9]{3}[-]/.test(str)) {
      return this.rutString2NumberFormatSinVerificador(str);
    } else if (/[0-9]{1,2}[.][0-9]{3}[.][0-9]{3}/.test(str)) {
      return this.stringFormatSinPuntos(str);
    } else {
      return str;
    }
  }

  //12.345.678-9 => 12345678
  rutString2NumberFormatSinVerificador(string) {
    let numero = string.replace(/[-\/\.]/g, "");
    return Number(numero.substring(0, numero.length - 1));
  }

  //Validación de RUT
  isValidRUT(rut) {
    if (!rut || typeof rut !== "string") return false;

    var regexp = /^\d{7,8}[k|K|\d]{1}$/;

    if (regexp.test(rut)) {
      var caracteres = new Array();
      var serie = new Array(2, 3, 4, 5, 6, 7);
      var dig = rut.substr(rut.length - 1, 1);
      rut = rut.substr(0, rut.length - 1);

      for (var i = 0; i < rut.length; i++) {
        caracteres[i] = parseInt(rut.charAt(rut.length - (i + 1)));
      }

      var sumatoria = 0;
      var k = 0;
      var resto = 0;

      for (var j = 0; j < caracteres.length; j++) {
        if (k == 6) {
          k = 0;
        }
        sumatoria += parseInt(caracteres[j]) * serie[k];
        k++;
      }

      resto = sumatoria % 11;
      let dv = (11 - resto).toString();

      if (dv === "10") {
        dv = "K";
      } else if (dv === "11") {
        dv = "0";
      }

      if (dv.trim().toUpperCase() === dig.trim().toUpperCase()) {
        return true;
      } else {
        return false;
      }
    } else {
      return false;
    }
  }

  isValidaPassword(password) {
    console.log(" isValidaPassword Util");
    if (!password || typeof password !== "string") return false;
    var regexp = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d){8,20}/;
    if (regexp.test(password)) {
      return true;
    } else {
      return false;
    }
  }

