import { Component, OnInit, OnDestroy } from '@angular/core';
import { Location } from '@angular/common';
import { FormGroup, FormBuilder, Validators, FormControl, NgForm } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ClientesService } from '../../../core/consultaService/clientes.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { UtilGeoPopUpComponent } from '../utilGeoPopUp/utilGeoPopUp.component';

const swal = require('sweetalert');
declare var jQuery: any;
declare var $: any;

@Component({
  selector: 'app-direccionesPopUp',
  templateUrl: './direccionesPopUp.component.html',
  styleUrls: ['./direccionesPopUp.component.scss']
})
export class DireccionesPopUpComponent implements OnInit, OnDestroy {
  forma: FormGroup;
  subData: any;
  origen: string;
  titulo: string;
  utilGeoModal: BsModalRef;
  creaDire: any = {};
  direccion: any;
  rutcliente = this.clientesService.selectedCliente.cli_nrutcliente;
  rutoperador = this.clientesService.selectedCliente.cli_nrutoperador;
  territorioSeleccionado: any;
  ciudadSeleccionado: any;
  comunaSeleccionado: any;

  estado: Array<any> = [
    {
      dic_scodestado: 'VIGEN',
      descEstado: 'Vigente'
    },
    {
      dic_scodestado: 'NOVIG',
      descEstado: 'No Vigente'
    }
  ];
  user: any = {};

  constructor(fb: FormBuilder, private clientesService: ClientesService,
    private setearDatos: ConsultasService, public direccionesModal: BsModalRef,
    private modalService: BsModalService, public loader: NgxSpinnerService,
    private authService: AuthService) {

      this.forma = new FormGroup ({
        codRegion: new FormControl('', Validators.required),
        region: new FormControl({value: '', disabled: true }),
        codCiudad: new FormControl('', Validators.required),
        ciudad: new FormControl({value: '', disabled: true }),
        codComuna: new FormControl('', Validators.required),
        comuna: new FormControl({value: '', disabled: true }),
        direccion: new FormControl('', Validators.required),
        codigoPostal: new FormControl(''),
        telefono: new FormControl(''),
        codEstadoDire: new FormControl('', Validators.required),
        referencia: new FormControl(''),
        nDireccion: new FormControl('')
      });
    }

  ngOnInit() {
    this.subData = this.setearDatos.datosGeo$.subscribe(resData =>{
      this.setData(resData);
    });
    this.user = this.authService.getDatosUser();

    if (this.direccion === undefined ) {
    } else {
      this.titulo = `${'Detalle Dirección N' + this.direccion.dic_niddireccion}`;
      this.forma.controls.codRegion.setValue(this.direccion.dic_scodregion);
      this.forma.controls.region.setValue(this.direccion.dic_sdesregion);
      this.forma.controls.codCiudad.setValue(this.direccion.dic_scodciudad);
      this.forma.controls.ciudad.setValue(this.direccion.dic_sdesciudad);
      this.forma.controls.codComuna.setValue(this.direccion.dic_scodcomuna);
      this.forma.controls.comuna.setValue(this.direccion.dic_sdescomuna);
      this.forma.controls.direccion.setValue(this.direccion.dic_sdireccion);
      this.forma.controls.codigoPostal.setValue(this.direccion.dic_scodpostal);
      this.forma.controls.telefono.setValue(this.direccion.dic_snumtelefono);
      this.forma.controls.codEstadoDire.setValue(this.direccion.dic_scodestado);
      this.forma.controls.referencia.setValue(this.direccion.dic_sptoreferencia);
      this.forma.controls.nDireccion.setValue(this.direccion.dic_niddireccion);
    }
  }
  ngOnDestroy() {
    if (this.subData !== undefined) {
      this.subData.unsubscribe();
    }
  }
  openModalRegiones() {
    this.origen = 'region';
    const initialState = {
      titulo: 'Region',
      origen: 'region'
    };
    this.utilGeoModal = this.modalService.show(UtilGeoPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  openModalCiudades() {
    this.origen = 'ciudad';
    const initialState = {
      titulo: 'Ciudad',
      origen: 'ciudad',
      regionSeleccionada: this.territorioSeleccionado
    };
    this.utilGeoModal = this.modalService.show(UtilGeoPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  openModalComunas() {
    this.origen = 'comuna';
    const initialState = {
      titulo: 'Comúna',
      origen: 'comuna',
      regionSeleccionada: this.territorioSeleccionado,
      ciudadSeleccionada:  this.ciudadSeleccionado
    };
    this.utilGeoModal = this.modalService.show(UtilGeoPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  setData(resData) {
    if (resData !== 0) {
      switch (this.origen) {
        case 'region': {
          this.forma.controls.codRegion.setValue(resData.par_codparametro01);
          this.forma.controls.region.setValue(resData.par_deslargo01);
          this.forma.controls.codCiudad.setValue('');
          this.forma.controls.ciudad.setValue('');
          this.forma.controls.codComuna.setValue('');
          this.forma.controls.comuna.setValue('');
          this.territorioSeleccionado = resData.par_codparametro01;
          break;
        }
        case 'ciudad': {
          this.forma.controls.codCiudad.setValue(resData.par_codparametro01);
          this.forma.controls.ciudad.setValue(resData.par_deslargo01);
          this.forma.controls.codComuna.setValue('');
          this.forma.controls.comuna.setValue('');
          this.ciudadSeleccionado = resData.par_codparametro01;
          break;
        }
        case 'comuna': {
          this.forma.controls.codComuna.setValue(resData.par_codparametro01);
          this.forma.controls.comuna.setValue(resData.par_deslargo01);
          break;
        }
      }
    }
    $('body').removeClass('modal-open');
  }
  guardarNuevaDireccion ($event: any, succesDir: string) {
    if (this.forma.valid) {

      this.loader.show();
      this.creaDire.codCiudad = this.forma.controls.codCiudad.value;
      this.creaDire.codComuna = this.forma.controls.codComuna.value;
      this.creaDire.codEstado = this.forma.controls.codEstadoDire.value;
      this.creaDire.codPostal = this.forma.controls.codigoPostal.value;
      this.creaDire.codRegion = this.forma.controls.codRegion.value;
      this.creaDire.desCiudad = this.forma.controls.ciudad.value;
      this.creaDire.desComuna = this.forma.controls.comuna.value;
      this.creaDire.desRegion = this.forma.controls.region.value;

      if ((this.forma.controls.nDireccion.value === undefined) || (this.forma.controls.nDireccion.value === '') ) {
        this.creaDire.nidDireccion = 0;
      } else {
        this.creaDire.nidDireccion = this.forma.controls.nDireccion.value;
      }

      this.creaDire.numTelefono = this.forma.controls.telefono.value;
      this.creaDire.ptoReferencia = this.forma.controls.referencia.value;
      this.creaDire.runUsuarioActualiza = this.user.nrunusuario;
      this.creaDire.rutCliente = this.rutcliente;
      this.creaDire.rutOperador = this.rutoperador;
      this.creaDire.sDireccion = this.forma.controls.direccion.value;
      this.clientesService.saveDirecciones(this.creaDire)
            .subscribe((data: any) => {
              if (data.error.codError === 0) {
                this.loader.hide();
                swal('Bien', 'Los datos han sido ingresados satisfactoriamente.', 'success' );
                this.setearDatos.setDatos(succesDir);
                this.direccionesModal.hide();
              } else {
                swal('Atención', data.error.msjError, 'error');
                this.loader.hide();
              }
            },
            error => {
              this.loader.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            });
    } else {
      swal('Atención', 'Existen campos requeridos no ingresados', 'error' );
      if (!this.forma.controls.codRegion.valid) {
        $('[name=region]').addClass('error-form');
      } else {
        $('[name=region]').removeClass('error-form');
      }

      if (!this.forma.controls.codCiudad.valid) {
        $('[name=ciudad]').addClass('error-form');
      } else {
        $('[name=ciudad]').removeClass('error-form');
      }

      if (!this.forma.controls.codComuna.valid) {
        $('[name=comuna]').addClass('error-form');
      } else {
        $('[name=comuna]').removeClass('error-form');
      }

      if (!this.forma.controls.direccion.valid) {
        $('[name=direccion]').addClass('error-form');
      } else {
        $('[name=direccion]').removeClass('error-form');
      }
      if (!this.forma.controls.codEstadoDire.valid) {
        $('[name=codEstadoDire]').addClass('error-form');
      } else {
        $('[name=codEstadoDire]').removeClass('error-form');
      }
      this.loader.hide();
    }
  }

}
