import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { CajaCobranzaService } from '../../../core/consultaService/cajaCobranza.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { UtilService } from '../../../services/util.service';
import { DecimalPipe } from '@angular/common';

@Component({
  selector: 'app-detalle-factura-pop-up',
  templateUrl: './detalleFacturaPopUp.component.html',
  styleUrls: ['./detalleFacturaPopUp.component.scss']
})
export class DetalleFacturaPopUpComponent implements OnInit {

  origen: string;
  param: string;
  titulo: string;
  forma: FormGroup;
  forma2: FormGroup;
  clientes: Array<any> = [];
  movimiento: any;
  cliente: any;
  user: any;
  cargando = false;
  facturas: Array<any> = [];
  hidden = true;

  constructor(public bsModalRef: BsModalRef,
    private cajaCobranzaService: CajaCobranzaService,
    private authService: AuthService,
    private utilService: UtilService,
    private decimalPipe: DecimalPipe,
  ) {

    this.forma = new FormGroup({
      rutCliente: new FormControl({ value: '', disabled: true }),
      nombreCliente: new FormControl({ value: '', disabled: true }),
      direccionEntrega: new FormControl({ value: '', disabled: true }),
      rutaCodigo: new FormControl({ value: '', disabled: true }),
      rutaDescripcion: new FormControl({ value: '', disabled: true }),
      condicionPago: new FormControl({ value: '', disabled: true }),
      fechaEmision: new FormControl({ value: '', disabled: true }),
      direccion: new FormControl({ value: '', disabled: true }),
      codigoTerritorio: new FormControl({ value: '', disabled: true }),
      descripcionTerritorio: new FormControl({ value: '', disabled: true }),
      fechaPedido: new FormControl({ value: '', disabled: true }),
      fechaEntrega: new FormControl({ value: '', disabled: true }),
      notaCredito: new FormControl({ value: '', disabled: true }),
    });

    this.forma2 = new FormGroup({
      totalParcial: new FormControl({ value: '', disabled: true }),
      descuentoBonificacion: new FormControl({ value: '', disabled: true }),
      baseImpuesto: new FormControl({ value: '', disabled: true }),
      iva: new FormControl({ value: '', disabled: true }),
      iaba: new FormControl({ value: '', disabled: true }),
      valorTotal: new FormControl({ value: '', disabled: true }),
      kilosBruto: new FormControl({ value: '', disabled: true }),
      volumen: new FormControl({ value: '', disabled: true }),
    });


  }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
    this.cargaFactura();
  }

  cargaFactura() {
    this.cargando = true;
    const request = {
      nIdFactura: this.movimiento.cte_niddocto ? this.movimiento.cte_niddocto : this.movimiento.smc_niddocto,
      nNumFactura: this.movimiento.nnumdocto,
      nRutOperador: this.cliente.cli_nrutoperador,
    };

    this.cajaCobranzaService.cargaFactura(request)
      .subscribe((data: any) => {
        this.cargando = false;
        if (data) {
          if (data.detalleFactura.length === 0 && data.error.codError === 0) {
            this.facturas = [];
            this.clientes = [];
          } else {
            this.clientes = data.detalleFactura;
            if (data.factura.length > 0) {
              this.setFactura(data.factura[0]);
              this.cliente = data.factura[0];
              this.facturas = data.factura;
            }
          }
        }
      },
        error => {
          this.cargando = false;
        });
  }

  setFactura(data) {
    this.forma.controls.rutCliente.setValue(data.nrutcliente + '' + data.sdigverificador);
    this.forma.controls.nombreCliente.setValue(data.snombre);
    this.forma.controls.direccionEntrega.setValue(data.sdireccionentrega);
    this.forma.controls.rutaCodigo.setValue(data.scodruta);
    this.forma.controls.rutaDescripcion.setValue(data.sdesruta);
    this.forma.controls.condicionPago.setValue(data.sdesformapago);
    this.forma.controls.fechaEmision.setValue(this.utilService.dateStringServiceToLocalStringFormat(data.dfhoemision));
    this.forma.controls.direccion.setValue(data.sdireccion);
    this.forma.controls.codigoTerritorio.setValue(data.scodterritorio);
    this.forma.controls.descripcionTerritorio.setValue(data.sdesterritorio);
    this.forma.controls.fechaPedido.setValue(this.utilService.dateStringServiceToLocalStringFormat(data.dfhopedido));
    this.forma.controls.fechaEntrega.setValue(this.utilService.dateStringServiceToLocalStringFormat(data.dfhovencimiento));
    this.forma.controls.notaCredito.setValue(data.nidnotacredito === 0 ? 'NINGUNA' : data.nidnotacredito);


    this.forma2.controls.totalParcial.setValue(this.decimalPipe.transform(data.nnetoafecto, '', 'es'));
    this.forma2.controls.descuentoBonificacion.setValue(data.ndescuento);
    this.forma2.controls.baseImpuesto.setValue(this.decimalPipe.transform(data.ndescuento, '', 'es'));
    this.forma2.controls.iva.setValue(this.decimalPipe.transform(data.niva, '', 'es'));
    this.forma2.controls.iaba.setValue(this.decimalPipe.transform(data.niaba, '', 'es'));
    this.forma2.controls.valorTotal.setValue(this.decimalPipe.transform(data.ntotal, '', 'es'));
    this.forma2.controls.kilosBruto.setValue(data.nkilos);
    this.forma2.controls.volumen.setValue(data.nvolumen);
  }

  convertirFecha(fecha) {
    return this.utilService.dateStringServiceToLocalStringFormat(fecha);
  }
}
