import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';

import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { UtilService } from '../../../services/util.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { CajaCobranzaService } from '../../../core/consultaService/cajaCobranza.service';

import { registerLocaleData, Time } from '@angular/common';
import es from '@angular/common/locales/es';

const swal = require('sweetalert');

@Component({
  selector: 'app-documentos-cliente-pop-up',
  templateUrl: './documentosClientePopUp.component.html',
  styleUrls: ['./documentosClientePopUp.component.scss']
})
export class DocumentosClientePopUpComponent implements OnInit {

  forma: FormGroup;
  fechaActual =  new Date();
  titulo: string;
  cli_nrutcliente:string;
  request: any = {};
  cargando:boolean;

  cuentasBancarias: Array <any> = [];

  documentos : Array <any> = [];

  constructor(public bsModalRef: BsModalRef, private consultasService:ConsultasService, private utilService:UtilService,
              private authService: AuthService, private cajaCobranzaService:CajaCobranzaService,  public cargador: NgxSpinnerService) {
    this.forma = new FormGroup ({
      cuenta: new FormControl(''),
      fecha: new FormControl('')
    });
  }

  ngOnInit() {
    registerLocaleData( es );
    
    console.log(this.cli_nrutcliente);
    this.request.nRutCliente = parseInt(this.cli_nrutcliente.substr(0, this.cli_nrutcliente.length - 2));
    this.request.nRutOperador = parseInt(this.authService.getDatosUser().nrutoperador);
    console.log('request cuentas bancarias', this.request);
    this.cajaCobranzaService.buscaCuentasBanco(this.request)
    .subscribe((data:any)=>{
      this.cargador.hide();
      console.log('cuentas',data);
      if(data){
        if(data.error.codError != 0){
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          }else{
            this.cuentasBancarias = data.banco;
          }
      }
    },
    error =>{
      this.cargador.hide();
      swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
    })
  }

  validarFechas(fecha){
      let dateValid = this.utilService.DateValidator(fecha.value);
      if(!dateValid){
        fecha.value = '';
      }
  }

  buscar(){
    this.cargando = true;
    this.request.nRutCliente = parseInt(this.cli_nrutcliente.substr(0, this.cli_nrutcliente.length - 2));
    this.request.nRutOperador = parseInt(this.authService.getDatosUser().nrutoperador);
    this.request.sCodBanco = null;
    this.request.sCodEstado = 'DEPOS';
    this.request.sFhoVencimientoDesde = this.forma.controls.fecha.value === '' ? null : (this.forma.controls.fecha.value).replace(/-/g, '') + '000000';
    this.request.sFhoVencimientoHasta = this.forma.controls.fecha.value === '' ? null : (this.forma.controls.fecha.value).replace(/-/g, '') + '000000';
    this.request.sNumCuenta = this.forma.controls.cuenta.value === '' ? null : this.forma.controls.cuenta.value;

    console.log('request buscar', this.request);

    this.cajaCobranzaService.getGarantias(this.request)
    .subscribe((data:any)=>{
      this.cargador.hide();
      console.log('docs',data);
      this.cargando = false;
      if(data){
        if(data.error.codError != 0){
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          }else{
            if(data.garantia.length == 0 && data.error.codError == 0){
              swal('Atención', 'No se encontraron resultados', 'info');
            }else{
              this.documentos = data.garantia;
            }
          }
      }
    },
    error =>{
      this.cargador.hide();
      swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
    })
    
  }

  enviarInfo(dato){
    this.consultasService.setDatos(dato);
    this.bsModalRef.hide();
  }

}
