import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, NgForm } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ClientesService } from '../../../core/consultaService/clientes.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { UtilGeoPopUpComponent } from '../utilGeoPopUp/utilGeoPopUp.component';
import { PedidosService } from '../../../core/consultaService/pedidos.service';
import { MoverGestionPopUpComponent } from '../moverGestionPopUp/moverGestionPopUp.component';

const swal = require('sweetalert');
declare var jQuery: any;
declare var $: any;
@Component({
  selector: 'app-gestionRecorridoPopUp',
  templateUrl: './gestionRecorridoPopUp.component.html',
  styleUrls: ['./gestionRecorridoPopUp.component.scss']
})
export class GestionRecorridoPopUpComponent implements OnInit, OnDestroy {
  forma: FormGroup;
  subData: any;
  origen: string;
  titulo: string;
  gestionesUnoModal: BsModalRef;
  creaDire: any = {};
  direccion: any;
  territorioSeleccionado: any;
  ciudadSeleccionado: any;
  comunaSeleccionado: any;
  diasRecorridos: Array<any> = [
    {
      dre_Sdia: 'LUNES',
      descDia: 'Lunes'
    },
    {
      dre_Sdia: 'MARTE',
      descDia: 'Martes'
    },
    {
      dre_Sdia: 'MIERC',
      descDia: 'Miercoles'
    },
    {
      dre_Sdia: 'JUEVE',
      descDia: 'Jueves'
    },
    {
      dre_Sdia: 'VIERN',
      descDia: 'Viernes'
    },
    {
      dre_Sdia: 'SABAD',
      descDia: 'Sabado'
    },
    {
      dre_Sdia: 'DOMIN',
      descDia: 'Domingo'
    }
  ];
  gestion: string;
  user: any = {};
  sucursal: any = {};
  request: any = {};
  datos: Array<any> = [];
  rutaFinal: Array<any> = [];
  pedidoFinal: Array<any> = [];
  combo: string;
  moverPedidos = 0;
  filtro: string;
  filtroDos: string;
  recorridoSeleccionado: any;
  diaFiltro: any;
  arregloRuta: any;
  arregloRutaDos: any;
  arregloPedido: any;
  arregloPedidoDos: any;
  finalPedido: any = {};
  finalRuta: any = {};

  constructor(fb: FormBuilder, private clientesService: ClientesService,
    private setearDatos: ConsultasService, public gestionRecorridoModal: BsModalRef,
    private modalService: BsModalService, public loader: NgxSpinnerService,
    private authService: AuthService, private pedidosService: PedidosService) {
    this.forma = new FormGroup ({
      sDia: new FormControl('', Validators.required),
      codRecorrido: new FormControl('', Validators.required),
      recorrido: new FormControl({value: '', disabled: true }),
      codRuta: new FormControl('', Validators.required),
      ruta: new FormControl({value: '', disabled: true })
    });
  }

  ngOnInit() {
    this.subData = this.pedidosService.datos$.subscribe(resData =>{
      this.setData(resData);
    });
    this.user = this.authService.getDatosUser();
    this.sucursal = JSON.parse(localStorage.getItem("SucursalUsuario"));
    if (this.gestion === 'moverPedido') {
      this.moverPedidos = 1;
    } else {
      this.moverPedidos = 0;
    }
  }
  ngOnDestroy() {
    if (this.subData !== undefined) {
      this.subData.unsubscribe();
    }
  }
  openModalRecorridos() {
    if (this.moverPedidos === 1) {
      this.filtro = 'moverPedidos';
      this.filtroDos = 'pedido';
      this.combo = 'recorrido';
    } else {
      this.filtro = 'recorridoRuta';
      this.filtroDos = '';
      this.combo = '';
    }
    this.diaFiltro = this.forma.controls.sDia.value;
    this.origen = 'recorridoRuta';
    const initialState = {
      titulo: 'Recorridos',
      origen: this.filtro,
      parametro: this.filtroDos,
      combo: this.combo,
      diaFiltro: this.diaFiltro
    };
    this.gestionesUnoModal = this.modalService.show(MoverGestionPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  openModalRutas() {
    this.origen = 'recorridoPedido';
    const initialState = {
      titulo: 'Recorridos',
      origen: 'moverPedidos',
      combo: 'ruta',
      parametro: 'pedido',
      moverPedidos: 1,
      recorridoSeleccionado: this.recorridoSeleccionado
    };
    this.gestionesUnoModal = this.modalService.show(MoverGestionPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }
  setData(resData) {
    console.log(resData);
    
    if (resData !== 0) {
      switch (this.origen) {
        case 'recorridoRuta': {
          this.forma.controls.codRecorrido.setValue(resData.dre_scodrecorrido);
          this.forma.controls.recorrido.setValue(resData.dre_sdesrecorrido);
          this.recorridoSeleccionado = resData.dre_scodrecorrido;
          break;
        }
        case 'recorridoPedido': {
          this.forma.controls.codRuta.setValue(resData.rut_scodruta);
          this.forma.controls.ruta.setValue(resData.rut_sdesruta);
          break;
        }
      }
    }
    $('body').removeClass('modal-open');
  }
  moverPedido () {
    let arregloIdRecorridos = [];
    arregloIdRecorridos = this.arregloPedido;
      console.log('ENTRE ', arregloIdRecorridos);
      arregloIdRecorridos.forEach(datosItem => {
        if (datosItem.codRuta !== this.forma.controls.codRuta.value) {
          let previo: any = {};
          previo.codRuta = this.forma.controls.codRuta. value;
          previo.idPedido = datosItem.idPedido;
          previo.indPedidoNuevo = 'N';
          previo.rutOperador = datosItem.rutOperador;
          this.pedidoFinal.push(previo);
        }
      });
    console.log('SALI ', this.pedidoFinal);
    this.finalPedido.aSRV_EntPedidos = this.pedidoFinal;
    this.finalPedido.runUsuario = Number(this.user.nrunusuario);
    console.log(this.finalPedido);
    this.pedidosService.updateMoverPedidos(this.finalPedido)
        .subscribe( (data: any) => {
          if (data) {
            if (data.error.codError === 0) {
              swal('Bien', 'Los datos han sido actualizados satisfactoriamente', 'success' );
              this.forma.reset();
              this.loader.hide();
              this.pedidosService.setDatos('refresh');
              this.gestionRecorridoModal.hide();
            } else {
              swal('Atención', data.error.msjError, 'error');
              this.loader.hide();
            }
          } else {
            swal('Atención', data.error.msjError, 'error');
            this.loader.hide();
          }
        },
        error => {
          this.loader.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }
  moverRuta () {
    console.log(this.arregloRuta);
    let arregloIdRecorridos = [];
    arregloIdRecorridos = this.arregloRuta;
      console.log('ENTRE ', arregloIdRecorridos);
      arregloIdRecorridos.forEach(datosItem => {
        if (datosItem.codRecorrido !== this.forma.controls.codRecorrido.value) {
          let previo: any = {};
          previo.codRecorrido = this.forma.controls.codRecorrido.value;
          previo.idRuta = datosItem.idRuta;
          previo.rutOperador = datosItem.rutOperador;
          this.rutaFinal.push(previo);
        }
      });
    console.log('SALI ', this.rutaFinal);
    this.finalRuta.aSRV_EntRutas = this.rutaFinal;
    this.finalRuta.runUsuario = Number(this.user.nrunusuario);
    console.log(this.finalRuta);
    this.pedidosService.updateMoverRutas(this.finalRuta)
        .subscribe( (data: any) => {
          if (data) {
            if (data.error.codError === 0) {
              swal('Bien', 'Los datos han sido actualizados satisfactoriamente', 'success' );
              this.forma.reset();
              this.loader.hide();
              this.pedidosService.setDatos('refresh');
              this.gestionRecorridoModal.hide();
            } else {
              swal('Atención', data.error.msjError, 'error');
              this.loader.hide();
            }
          } else {
            swal('Atención', data.error.msjError, 'error');
            this.loader.hide();
          }
        },
        error => {
          this.loader.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }

}
