import { Component, OnInit, OnDestroy } from '@angular/core';
import { Location } from '@angular/common';
import { FormGroup, FormBuilder, Validators, FormControl, NgForm } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ClientesService } from '../../../core/consultaService/clientes.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { UtilGeoPopUpComponent } from '../utilGeoPopUp/utilGeoPopUp.component';
import { BuscaBancoPopUpComponent } from '../buscaBancoPopUp/buscaBancoPopUp.component';

const swal = require('sweetalert');
declare var jQuery: any;
declare var $: any;

@Component({
  selector: 'app-relacionesPopUp',
  templateUrl: './relacionesPopUp.component.html',
  styleUrls: ['./relacionesPopUp.component.scss']
})
export class RelacionesPopUpComponent implements OnInit, OnDestroy {

  forma: FormGroup;
  subData: any;
  subData2: any;
  origen: string;
  utilGeoModal: BsModalRef;
  buscaBancosModal: BsModalRef;
  creaRela: any = {};
  relacion: any = {};
  titulo: string;
  rutcliente = this.clientesService.selectedCliente.cli_nrutcliente;
  rutoperador = this.clientesService.selectedCliente.cli_nrutoperador;
  user: any = {};

  constructor(fb: FormBuilder, private clientesService: ClientesService,
    private setearDatos: ConsultasService, public relacionesModal: BsModalRef,
    private modalService: BsModalService, public loader: NgxSpinnerService,
    private authService: AuthService) {

    this.forma = new FormGroup ({
      codLocal: new FormControl({value: '', disabled: true }, Validators.required),
      local: new FormControl({value: '', disabled: true }, Validators.required),
      codRelaciones: new FormControl({value: '', disabled: true }, Validators.required),
      relacionesComerciales: new FormControl({value: '', disabled: true }, Validators.required)
    });
  }

  ngOnInit() {
    this.subData = this.setearDatos.datos$.subscribe(resData => {
      this.setData(resData);
    });
    this.subData2 = this.setearDatos.datosGeo$.subscribe(resData => {
      this.setData(resData);
    });
    this.user = this.authService.getDatosUser();

    this.forma.controls.codLocal.setValue(this.relacion.dic_scodregion);
    this.forma.controls.local.setValue(this.relacion.dic_sdesregion);
    this.forma.controls.codRelaciones.setValue(this.relacion.dic_scodciudad);
    this.forma.controls.relacionesComerciales.setValue(this.relacion.dic_sdesciudad);
  }

  ngOnDestroy() {
    if (this.subData != undefined) {
      this.subData.unsubscribe();
    }
    if (this.subData2 != undefined) {
      this.subData2.unsubscribe();
    }
  }
  openModalLocalesRelaciones() {
    this.origen = 'direRelaciones';
    const initialState = {
      titulo: 'Locales',
      origen: 'direRelaciones'
    };
    this.utilGeoModal = this.modalService.show(UtilGeoPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  openModalRelaciones() {
    this.origen = 'relaciones';
    const initialState = {
      titulo: 'Relaciones',
      origen: 'relaciones'
    };
    this.buscaBancosModal = this.modalService.show(BuscaBancoPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  setData(resData) {
    if (resData !== 0) {
      switch (this.origen) {
        case 'direRelaciones': {
          this.forma.controls.codLocal.setValue((resData.loc_nnumlocal).toString());
          this.forma.controls.local.setValue(resData.loc_snombre);
          break;
        }
        case 'relaciones': {
          this.forma.controls.codRelaciones.setValue(resData.ore_scodrelacioncomercial);
          this.forma.controls.relacionesComerciales.setValue(resData.rel_sdescripcionrelacioncomerc);
          break;
        }
      }
    }
    $('body').removeClass('modal-open');
  }
  guardarNuevaRelacion ($event: any, succesRela: string) {
    this.forma.controls.codLocal.enable();
    this.forma.controls.codRelaciones.enable();
    if (this.forma.valid) {
      this.loader.show();
      this.creaRela.codRelacionComercial = this.forma.controls.codRelaciones.value;
      this.creaRela.numLocal = Number(this.forma.controls.codLocal.value);
      this.creaRela.runUsuario = Number(this.user.nrunusuario);
      this.creaRela.rutCliente = Number(this.rutcliente);
      this.creaRela.rutOperador = Number(this.rutoperador);
      this.clientesService.saveRelaciones(this.creaRela)
            .subscribe((data: any) => {
              if (data.error.codError === 0) {
                this.loader.hide();
                swal('Bien', 'Los datos han sido ingresados satisfactoriamente.', 'success' );
                this.setearDatos.setDatos(succesRela);
                this.relacionesModal.hide();
              } else {
                swal('Atención', data.error.msjError, 'error');
                this.loader.hide();
              }
              this.forma.controls.codLocal.disable();
              this.forma.controls.codRelaciones.disable();
          },
          error => {
            this.loader.hide();
            this.forma.controls.codLocal.disable();
            this.forma.controls.codRelaciones.disable();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          });
    } else {
      swal('Atención', 'Existen campos requeridos no ingresados', 'error' );
      this.forma.controls.codLocal.disable();
      this.forma.controls.codRelaciones.disable();
      if (!this.forma.controls.codLocal.valid) {
        $('[name=local]').addClass('error-form');
      } else {
        $('[name=local]').removeClass('error-form');
      }

      if (!this.forma.controls.codRelaciones.valid) {
        $('[name=relacionesComerciales]').addClass('error-form');
      } else {
        $('[name=relacionesComerciales]').removeClass('error-form');
      }
      this.loader.hide();
    }
  }

}
