import { Component, OnInit, OnDestroy } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';

import { UtilService } from '../../../services/util.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { CajaCobranzaService } from '../../../core/consultaService/cajaCobranza.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';

import { BuscarArticuloPopUpComponent } from '../buscarArticuloPopUp/buscarArticuloPopUp.component';

const swal = require('sweetalert');

declare var jQuery:any;
declare var $:any;

@Component({
  selector: 'app-modificacion-garantia-pop-up',
  templateUrl: './modificacionGarantiaPopUp.component.html',
  styleUrls: ['./modificacionGarantiaPopUp.component.scss']
})
export class ModificacionGarantiaPopUpComponent implements OnInit, OnDestroy {

  formaTipoPago: FormGroup;
  formaProrroga: FormGroup;
  formaEfectivo: FormGroup;
  formaChequeDia: FormGroup;
  formaChequeFecha: FormGroup;
  formaNotasCredito: FormGroup;
  formaAjusteDebito: FormGroup;
  titulo: string;
  fechaActual =  new Date();
  garantia: any = {};
  request: any = {};
  requestGrabar: any = {};
  subData: any;
  tipoPago = 0;
  tablaPagos: Array <any> = [];
  tablaPagosRequest: Array <any> = [];
  secondModal: BsModalRef;
  totalIngresado = 0;
  datos: Array <any> = [];
  verCambio = true;
  verProrroga = false;
  sumaFavor: number;

  cuentasBancarias: Array <any> = [];
  notasCredito: Array <any> = [];
  ajusteDebito: Array <any> = [];

  dataModal: boolean;

  constructor(public bsModalRef: BsModalRef,  private utilService:UtilService, private authService: AuthService, private cajaCobranzaService:CajaCobranzaService,
              public cargador: NgxSpinnerService, private consultasService:ConsultasService, private modalService: BsModalService) 
  {
    this.formaTipoPago = new FormGroup ({
      tipoPagoCod: new FormControl(''),
      tipoPagoDesc: new FormControl({value:'', disabled:true})
    });

    this.formaEfectivo = new FormGroup ({
      montoEfectivo: new FormControl(0)
    });

    this.formaChequeDia = new FormGroup ({
      cuenta: new FormControl('', Validators.required),
      numCheque: new FormControl('', Validators.required),
      fecha: new FormControl({value:this.utilService.dateToStringFormat(this.fechaActual), disabled:true}),
      montoChequeDia: new FormControl(0)
    });

    this.formaChequeFecha = new FormGroup ({
      cuenta: new FormControl('', Validators.required),
      numCheque: new FormControl('', Validators.required),
      fechaCh: new FormControl(this.utilService.dateToStringFormat(this.fechaActual)),
      montoChequeFecha: new FormControl(0)
    });

    this.formaNotasCredito = new FormGroup ({
      notaCredito: new FormControl('', Validators.required),
      montoNotaCredito: new FormControl({value:0, disabled:true})
    });

    this.formaAjusteDebito = new FormGroup ({
      aFavor: new FormControl({value:0, disabled:true}),
      montoAjuste: new FormControl(0)
    });

    this.formaProrroga = new FormGroup ({
      fecha: new FormControl('')
    });

  }

  ngOnInit() {

    this.dataModal = false;

    console.log('garantia', this.garantia);
    this.formaTipoPago.controls.tipoPagoCod.setValue(null);
    this.formaTipoPago.controls.tipoPagoDesc.setValue('');
  
    this.formaProrroga.controls.fecha.setValue(this.utilService.dateToStringFormat(this.fechaActual));
    // this.formaChequeDia.controls.fecha.setValue(this.utilService.dateToStringFormat(this.fechaActual));

    // carga tipo pago 
    this.request.nIdRecorrido = 0;
    this.request.nNumLocal = 0;
    this.request.nRutCliente = this.garantia.gar_nrutcliente;
    this.request.nRutOperador = this.garantia.gar_nrutoperador;
    this.request.opcion = 'PAGOS',
    this.request.sCodTipoPago = '03'
    console.log(this.request);
    this.cajaCobranzaService.cargaTipoPago(this.request)
      .subscribe((data:any)=>{
        this.cargador.hide();
        console.log('cuentas',data);
        if(data){
          if(data.error.codError != 0){
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }else{
              this.cuentasBancarias = data.cuentasCliente;
              this.notasCredito = data.notasCredito.filter( notas => notas.cte_scodtipodocto === 'NOCRE');
              this.ajusteDebito = data.notasCredito.filter( notas => notas.cte_scodtipodocto === 'GARAN');
              if(this.ajusteDebito.length > 0){
                this.sumaFavor = 0;
                for(let i in this.ajusteDebito){
                  this.sumaFavor =+ this.ajusteDebito[i].cte_nsaldodocto;
                }
                this.formaAjusteDebito.controls.aFavor.setValue(this.sumaFavor);
              }
            }
        }
      },
      error =>{
        this.cargador.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })

    this.subData = this.consultasService.datos$.subscribe(resData =>{
      this.setData(resData);
    })
  }

  ngOnDestroy() {
    if(this.subData != undefined) {
      this.subData.unsubscribe();
    }
  }


  validaFecha(fecha){
    let dateValid = this.utilService.DateValidator(fecha.value);
    if(!dateValid){
      fecha.value = this.utilService.dateToStringFormat(this.fechaActual);
    }
  }

  validaFechaCheque(fecha){
    let dateValid = this.utilService.DateValidator(fecha.value);
    if(!dateValid){
      fecha.value = this.utilService.dateToStringFormat(this.fechaActual);
    }else{
      if(fecha.value < this.utilService.dateToStringFormat(this.fechaActual)){
        fecha.value = this.utilService.dateToStringFormat(this.fechaActual);
      }
    }
  }

  validaTipoPago(){
    console.log(this.formaTipoPago.controls.tipoPagoCod.value);
    
    if(this.formaTipoPago.controls.tipoPagoCod.value === null){
      this.tipoPago = null;
      this.formaTipoPago.controls.tipoPagoDesc.setValue('');
    }else{
      this.request = {
        "nIdRecorrido": 0,
        "nNumLocal": 0,
        "nRutCliente": this.garantia.gar_nrutcliente,
        "nRutOperador": this.garantia.gar_nrutoperador,
        "opcion": "PAGOS"
      }
      this.cargador.show();
      this.cajaCobranzaService.buscaTiposPago(this.request)
      .subscribe((data:any)=>{
        this.cargador.hide();
        console.log(data);
        if(data){
          if(data.error.codError != 0){
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }else{
              let existeTipoPago = false;
              this.datos = data.tipoPago;
              if(this.datos.length > 0){
                for(let i in this.datos){
                  console.log('this.datos[i].par_codparametro01', this.datos[i].par_codparametro01);
                  console.log('this.formaTipoPago.controls.tipoPagoCod.value', this.formaTipoPago.controls.tipoPagoCod.value);
                  
                  
                  if(this.datos[i].par_codparametro01 == this.formaTipoPago.controls.tipoPagoCod.value){
                    this.formaTipoPago.controls.tipoPagoDesc.setValue(this.datos[i].par_deslargo01);
                    this.tipoPago = parseInt(this.datos[i].par_codparametro01);
                    existeTipoPago = true;
                    break;
                  }
                }
                if(!existeTipoPago){
                  this.formaTipoPago.controls.tipoPagoCod.setValue(null);
                  this.formaTipoPago.controls.tipoPagoDesc.setValue('');
                  this.tipoPago = null;
                  swal('Atención', 'Forma de pago no existe o no está habilitado para utilizarla', 'warning');
                }
              }
            }
        }
      },
      error =>{
        this.cargador.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
    }
  }

  validaAccion(accion){
    console.log(accion.value);
    if(accion.value === 'cambio'){
      this.verCambio = true;
      this.verProrroga = false;
    }else{
      this.verCambio = false;
      this.verProrroga = true;
    }
  }


  validaCero(monto){
    if(monto.value === ''){
      monto.value = 0;
    }
  }

  cambiaMonto(){
    console.log(this.formaNotasCredito.controls.notaCredito.value);
    for(let i in this.notasCredito){
      if(this.notasCredito[i].cte_nidoperacion === this.formaNotasCredito.controls.notaCredito.value){
        this.formaNotasCredito.controls.montoNotaCredito.setValue(this.notasCredito[i].cte_nsaldodocto);
        break;
      }
      else{
        this.formaNotasCredito.controls.montoNotaCredito.setValue(0);
      }
    }
  }

  openModal() {
    // this.origen = 'linea';
    this.dataModal = true;
    const initialState = {
      titulo: 'Tipos de Pago',
      origen: 'tipoPago',
      rutCliente : this.garantia.gar_nrutcliente
    };
    this.secondModal = this.modalService.show(BuscarArticuloPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  agregarEfectivo(){
    if(this.formaEfectivo.controls.montoEfectivo.value === 0 || this.formaEfectivo.controls.montoEfectivo.value === null){
      swal('Atención', 'El monto ingresado debe ser mayor a cero', 'warning');
    }
    else{
      console.log('agregar');
      let pago = {
        id: Math.floor(Math.random() * (10000 - 0)) + 1,
        monto: this.formaEfectivo.controls.montoEfectivo.value,
        nDocto: 0,
        nIdGarantia: this.garantia.gar_nidgarantia,
        nRutCliente: this.garantia.gar_nrutcliente,
        nRutOperador: this.garantia.gar_nrutoperador,
        sCodBanco: '',
        sCodSucursal: JSON.parse(localStorage.getItem('SucursalUsuario')).sus_scodsucursal,
        sCodTipoPago: '01',
        sFhoVencimiento: '',
        sNumCuenta: ''
      }
      this.validaExisteFormaPago('efectivo', pago, '01');
      this.formaTipoPago.controls.tipoPagoCod.setValue(null);
      this.formaTipoPago.controls.tipoPagoDesc.setValue('');
      this.formaEfectivo.controls.montoEfectivo.setValue(0);
      this.tipoPago = null;
      // this.calculaTotalIngresado();
    }
  }

  agregarChequeDia(){
    console.log(this.formaChequeDia);
    if(this.formaChequeDia.valid){
      $('*').removeClass('error-form');
      if(this.formaChequeDia.controls.montoChequeDia.value === 0 || this.formaChequeDia.controls.montoChequeDia.value === null){
        swal('Atención', 'El monto ingresado debe ser mayor a cero', 'warning');
      }else{
        console.log('agregar cheque al día');
        let bcoCta = this.formaChequeDia.controls.cuenta.value.split('&');     
        let pago = {
          id: Math.floor(Math.random() * (10000 - 0)) + 1,
          monto: this.formaChequeDia.controls.montoChequeDia.value,
          nDocto: this.formaChequeDia.controls.numCheque.value,
          nIdGarantia: this.garantia.gar_nidgarantia,
          nRutCliente: this.garantia.gar_nrutcliente,
          nRutOperador: this.garantia.gar_nrutoperador,
          sCodBanco: bcoCta[0],
          sCodSucursal: JSON.parse(localStorage.getItem('SucursalUsuario')).sus_scodsucursal,
          sCodTipoPago: '02',
          sFhoVencimiento: this.formaChequeDia.controls.fecha.value,
          sNumCuenta: bcoCta[1]
        }
        this.validaExisteFormaPago('cheque', pago, '02');
        this.formaTipoPago.controls.tipoPagoCod.setValue(null);
        this.formaTipoPago.controls.tipoPagoDesc.setValue('');
        this.formaChequeDia.controls.numCheque.setValue('');
        this.formaChequeDia.controls.montoChequeDia.setValue(0);
        this.tipoPago = null;
      }
    }else{
      swal('Atención', 'Debe ingresar todos los datos requeridos', 'warning' );
      if(!this.formaChequeDia.controls.cuenta.valid){
        $('[name=cuenta]').addClass('error-form');
      }
      else{
        $('[name=cuenta]').removeClass('error-form');
      }
      if(!this.formaChequeDia.controls.numCheque.valid){
        $('[name=numCheque]').addClass('error-form');
      }
      else{
        $('[name=numCheque]').removeClass('error-form');
      }
    }
  }

  agregarChequeFecha(){
    console.log(this.formaChequeFecha);
    if(this.formaChequeFecha.valid){
      $('*').removeClass('error-form');
      if(this.formaChequeFecha.controls.montoChequeFecha.value === 0 || this.formaChequeFecha.controls.montoChequeFecha.value === null){
        swal('Atención', 'El monto ingresado debe ser mayor a cero', 'warning');
      }else{
        console.log('agregar cheque a fecha');
        let bcoCta = this.formaChequeFecha.controls.cuenta.value.split('&');   
        let pago = {
          id: Math.floor(Math.random() * (10000 - 0)) + 1,
          monto: this.formaChequeFecha.controls.montoChequeFecha.value,
          nDocto: this.formaChequeFecha.controls.numCheque.value,
          nIdGarantia: this.garantia.gar_nidgarantia,
          nRutCliente: this.garantia.gar_nrutcliente,
          nRutOperador: this.garantia.gar_nrutoperador,
          sCodBanco: bcoCta[0],
          sCodSucursal: JSON.parse(localStorage.getItem('SucursalUsuario')).sus_scodsucursal,
          sCodTipoPago: '03',
          sFhoVencimiento: this.formaChequeFecha.controls.fechaCh.value,
          sNumCuenta: bcoCta[1]
        }
        this.validaExisteFormaPago('cheque', pago, '03');
        this.formaTipoPago.controls.tipoPagoCod.setValue(null);
        this.formaTipoPago.controls.tipoPagoDesc.setValue('');
        this.formaChequeFecha.controls.numCheque.setValue('');
        this.formaChequeFecha.controls.montoChequeFecha.setValue(0);
        this.tipoPago = null;
      }
    }else{
      swal('Atención', 'Debe ingresar todos los datos requeridos', 'warning' );
      if(!this.formaChequeFecha.controls.cuenta.valid){
        $('[name=cuenta]').addClass('error-form');
      }
      else{
        $('[name=cuenta]').removeClass('error-form');
      }
      if(!this.formaChequeFecha.controls.numCheque.valid){
        $('[name=numCheque]').addClass('error-form');
      }
      else{
        $('[name=numCheque]').removeClass('error-form');
      }
    }
  }

  agregarNotaCredito(){
    console.log(this.formaNotasCredito);
    if(this.formaNotasCredito.valid){
      $('*').removeClass('error-form');
      console.log('agregar Nota Crédito');
      let pago = {
        id: Math.floor(Math.random() * (10000 - 0)) + 1,
        monto: this.formaNotasCredito.controls.montoNotaCredito.value,
        nDocto: this.formaNotasCredito.controls.notaCredito.value,
        nIdGarantia: this.garantia.gar_nidgarantia,
        nRutCliente: this.garantia.gar_nrutcliente,
        nRutOperador: this.garantia.gar_nrutoperador,
        sCodBanco: '',
        sCodSucursal: JSON.parse(localStorage.getItem('SucursalUsuario')).sus_scodsucursal,
        sCodTipoPago: '04',
        sFhoVencimiento: '',
        sNumCuenta: ''

      }
      this.validaExisteFormaPago('cheque', pago, '04');
      this.formaTipoPago.controls.tipoPagoCod.setValue(null);
      this.formaTipoPago.controls.tipoPagoDesc.setValue('');
      this.formaNotasCredito.controls.notaCredito.setValue('');
      this.formaNotasCredito.controls.montoNotaCredito.setValue(0);
      this.tipoPago = null;
    }else{
      swal('Atención', 'Debe ingresar todos los datos requeridos', 'warning' );
      if(!this.formaNotasCredito.controls.notaCredito.valid){
        $('[name=notaCredito]').addClass('error-form');
      }
      else{
        $('[name=notaCredito]').removeClass('error-form');
      }
    }
  }

  agregarAjusteDebito(){
    if(this.formaAjusteDebito.controls.montoAjuste.value === 0 || this.formaAjusteDebito.controls.montoAjuste.value === null){
      swal('Atención', 'El monto ingresado debe ser mayor a cero', 'warning');
    }
    else if(this.formaAjusteDebito.controls.montoAjuste.value > this.formaAjusteDebito.controls.aFavor.value){
      swal('Atención', 'El monto ingresado sobrepasa el monto a favor', 'warning');
    }else{
      console.log('agregar saldo a favor');
      let pago = {
        id: Math.floor(Math.random() * (10000 - 0)) + 1,
        monto: this.formaAjusteDebito.controls.montoAjuste.value,
        nDocto: 0,
        nIdGarantia: this.garantia.gar_nidgarantia,
        nRutCliente: this.garantia.gar_nrutcliente,
        nRutOperador: this.garantia.gar_nrutoperador,
        sCodBanco: '',
        sCodSucursal: JSON.parse(localStorage.getItem('SucursalUsuario')).sus_scodsucursal,
        sCodTipoPago: '07',
        sFhoVencimiento: '',
        sNumCuenta: ''
      }
      this.validaExisteFormaPago('efectivo', pago, '07');
      this.formaTipoPago.controls.tipoPagoCod.setValue(null);
      this.formaTipoPago.controls.tipoPagoDesc.setValue('');
      this.formaAjusteDebito.controls.montoAjuste.setValue(0);
      this.tipoPago = null;
    }

  }

  validaExisteFormaPago(desde, pago, tipoPago){
    if(this.tablaPagos.length === 0){
      this.tablaPagos.push(pago);
      this.calculaTotalIngresado();
    }else{
      switch(desde){
        case 'efectivo':{
          let codigoDuplicado = false;
          for(let i in this.tablaPagos){
            if(this.tablaPagos[i].sCodTipoPago === tipoPago){
              codigoDuplicado = true;
              swal({
                title: 'Atención',
                text: 'Forma de pago ya se encuentra registrada, ¿Desea reemplazada?',
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#85b3cd',
                confirmButtonText: 'Aceptar',
                cancelButtonText: 'Cancelar',
                closeOnConfirm: true
              }, () => {
                console.log('resta monto');
                this.totalIngresado = this.totalIngresado - this.tablaPagos[i].monto;
                this.tablaPagos.splice(parseInt(i),1,pago);
                this.calculaTotalIngresado();
              });
              break;
            }
          }
          if(!codigoDuplicado){
            this.tablaPagos.push(pago);
            this.calculaTotalIngresado();
          }
          break;
        }

        case 'cheque':{
          let codigoDuplicado = false;
          for(let i in this.tablaPagos){
            if(this.tablaPagos[i].sCodTipoPago === tipoPago && this.tablaPagos[i].nDocto === pago.nDocto){
              codigoDuplicado = true;
              swal({
                title: 'Atención',
                text: 'Forma de pago ya se encuentra registrada, ¿Desea reemplazada?',
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#85b3cd',
                confirmButtonText: 'Aceptar',
                cancelButtonText: 'Cancelar',
                closeOnConfirm: true
              }, () => {
                console.log('resta monto');
                this.totalIngresado = this.totalIngresado - this.tablaPagos[i].monto;
                this.tablaPagos.splice(parseInt(i),1,pago);
                this.calculaTotalIngresado();
              });
              break;
            }
          }
          if(!codigoDuplicado){
            this.tablaPagos.push(pago);
            this.calculaTotalIngresado();
          }
          break;
        }
      }
    }
  }

  calculaTotalIngresado(){
    console.log('calculaTotalIngresado');
    this.totalIngresado = 0;
    
    for(let i in this.tablaPagos){
      this.totalIngresado = this.totalIngresado + this.tablaPagos[i].monto;
    }
  }


  eliminarPago(pago){
    for(let i in this.tablaPagos){
      if(this.tablaPagos[i].id === pago.id){
        this.tablaPagos.splice(this.tablaPagos.indexOf(this.tablaPagos[i]), 1);
        this.calculaTotalIngresado()
        break;
      }
    }
  }

  aceptar(){
    this.tablaPagosRequest = [];
    if(this.tablaPagos.length === 0){
      swal('Atención', 'Debe ingresar detalle de pagos', 'warning');
    }else if(this.totalIngresado < this.garantia.gar_nmonto){
      swal('Atención', 'Monto ingresado no puede ser menor al monto a pagar', 'warning');
    }else{
      console.log('acepta pago');
      //prepara estructura del request
      for(let i in this.tablaPagos){
        let pago = {
          monto: this.tablaPagos[i].monto,
          nDocto: parseInt(this.tablaPagos[i].nDocto),
          nIdGarantia: this.tablaPagos[i].nIdGarantia,
          nRutCliente: this.tablaPagos[i].nRutCliente,
          nRutOperador: this.tablaPagos[i].nRutOperador,
          sCodBanco: this.tablaPagos[i].sCodBanco === '' ? null : this.tablaPagos[i].sCodBanco,
          sCodSucursal: this.tablaPagos[i].sCodSucursal,
          sCodTipoPago: this.tablaPagos[i].sCodTipoPago,
          sFhoVencimiento: this.tablaPagos[i].sFhoVencimiento === '' ? `${this.utilService.dateToStringDateFullFormat(this.fechaActual)}00`: `${(this.tablaPagos[i].sFhoVencimiento).replace(/-/g, '')}00000000`,
          sNumCuenta: this.tablaPagos[i].sNumCuenta === '' ? null : this.tablaPagos[i].sNumCuenta
        }
        this.tablaPagosRequest.push(pago);
      }

      this.requestGrabar.aEntDetalleCambio = this.tablaPagosRequest;
      this.requestGrabar.nUsuarioCrea = parseInt(this.authService.getDatosUser().nrunusuario);

      console.log('request graba', this.requestGrabar);
      this.cargador.show();

      this.cajaCobranzaService.cambiaGarantia(this.requestGrabar)
      .subscribe((data:any)=>{
        this.cargador.hide();
        console.log('res cambia garantia',data);
        if(data){
          if(data.error.codError != 0){
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }else{
              this.consultasService.setDatos('OK');
              this.bsModalRef.hide();
              swal('Atención', 'Cambio de Cheque realizado Exitosamente', 'success');
            }
        }
      },
      error =>{
        this.cargador.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
      
    }
  }

  aceptaProrroga(fecha){
    console.log(fecha.value);
    if(fecha.value <= this.utilService.dateToStringFormat(this.fechaActual)){
      swal('Atención', 'Fecha de la Prórroga debe ser mayor que fecha actual', 'warning');
    }
    else{
      this.request.dFhoVencimiento = `${(this.formaProrroga.controls.fecha.value).replace(/-/g, '')}000000`;
      this.request.nIdGarantia = this.garantia.gar_nidgarantia;
      this.request.nRunUsuarioActualiza = parseInt(this.authService.getDatosUser().nrunusuario);
      this.request.nRutCliente = this.garantia.gar_nrutcliente;
      this.request.nRutOperador = this.garantia.gar_nrutoperador;

      console.log('request prorroga', this.request);
      this.cargador.show();  
      this.cajaCobranzaService.actualizaProrrogaGarantia(this.request)
      .subscribe((data:any)=>{
          this.cargador.hide();
          console.log('data prorroga',data);
          
          if(data.error.codError != 0){
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          }else{
            this.consultasService.setDatos('OK');
            this.bsModalRef.hide();
            swal('Atención', 'Prorroga de Documento realizado Exitosamente', 'success');
          }
      },
      error =>{
          this.cargador.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
    }

  }

  setData(resData){
    if(this.dataModal){
      if(resData != 0){
        console.log(resData);
        this.formaTipoPago.controls.tipoPagoCod.setValue(resData.par_codparametro01);
        this.formaTipoPago.controls.tipoPagoDesc.setValue(resData.par_deslargo01);
        this.tipoPago = parseInt(resData.par_codparametro01);
  
        // limpia valores
        this.formaEfectivo.controls.montoEfectivo.setValue(0);
        this.formaChequeDia.controls.cuenta.setValue('');
        this.formaChequeDia.controls.numCheque.setValue('');
        this.formaChequeDia.controls.montoChequeDia.setValue(0);
        this.formaChequeFecha.controls.cuenta.setValue('');
        this.formaChequeFecha.controls.numCheque.setValue('');
        this.formaChequeFecha.controls.fechaCh.setValue(this.utilService.dateToStringFormat(this.fechaActual));
        this.formaChequeFecha.controls.montoChequeFecha.setValue(0);
        this.formaNotasCredito.controls.notaCredito.setValue('');
        this.formaNotasCredito.controls.montoNotaCredito.setValue(0);
        this.formaAjusteDebito.controls.montoAjuste.setValue(0);
      }
    }
  }
  
}
