import { Component, OnInit } from '@angular/core';
import * as _ from 'lodash';
import { HttpClient } from '@angular/common/http';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';

import { NgxSpinnerService } from 'ngx-spinner';
import { AuthService } from '../../../core/consultaService/auth.service';
import { Router } from '@angular/router';
import { AdministracionOperativaService } from '../../../core/consultaService/administracionOperativa.service';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';

declare var jQuery: any;
declare var $: any;

const swal = require('sweetalert');
@Component({
  selector: 'app-recorridosGeneralPopUp',
  templateUrl: './recorridosGeneralPopUp.component.html',
  styleUrls: ['./recorridosGeneralPopUp.component.scss']
})
export class RecorridosGeneralPopUpComponent implements OnInit {
  forma: FormGroup;
  diasRecorridos: Array<any> = [
    {
      codDia: null,
      descEstado: '(Todos)'
    },
    {
      codDia: 'LUNES',
      descEstado: 'Lunes'
    },
    {
      codDia: 'MARTE',
      descEstado: 'Martes'
    },
    {
      codDia: 'MIERC',
      descEstado: 'Miercoles'
    },
    {
      codDia: 'JUEVE',
      descEstado: 'Jueves'
    },
    {
      codDia: 'VIERN',
      descEstado: 'Viernes'
    },
    {
      codDia: 'SABAD',
      descEstado: 'Sabado'
    },
    {
      codDia: 'DOMIN',
      descEstado: 'Domingo'
    }
  ];
  user: any = {};
  request: any = {};
  recorridos: Array <any> = [];
  titulo: string;
  origen: string;

  constructor(public http: HttpClient, fb: FormBuilder,
    private recorridosService: AdministracionOperativaService,
    public loader: NgxSpinnerService, private authService: AuthService,
    public recorridoGeneralModal: BsModalRef, private modalService: BsModalService) {
      this.forma = new FormGroup ({
        sDia: new FormControl(''),
        nRutOperador: new FormControl(''),
      });
     }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
    switch (this.origen) {
      case 'recorridosRutas': {
        this.titulo =  'Recorridos';
        console.log(this.user);
        console.log(this.forma.controls.sDia);
        this.listarRecorridos ();
      break;
      }
    }
  }

  listarRecorridos () {
    console.log(this.user);
    this.forma.controls.sDia.setValue(null);
    this.forma.controls.nRutOperador.setValue(this.user.nrutoperador);
    this.request = this.forma.value;
    console.log(this.request);
    this.recorridosService.getRecorridos(this.request)
        .subscribe((data: any) => {
            console.log(data);
            this.loader.hide();
                if (data.recorridosAdministracion.length === 0 && data.error.codError === 0 ) {
                    swal('Atención', 'No se encontraron resultados', 'info');
                } else {
                    if (data.recorridosAdministracion === null) {
                        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                    } else {
                        (this.recorridos = data.recorridosAdministracion);
                    }
                }
                if (data.error.codError !== 0) {
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                }
        },
        error => {
            this.loader.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        }
    );
  }
  buscar($event: any) {

    if (!this.forma.invalid) {

        this.loader.show();
        this.request = this.forma.value;
        console.log(this.request);
        this.recorridosService.getRecorridos(this.request)
        .subscribe((data: any) => {
            console.log(data);
            this.loader.hide();
                if (data.recorridosAdministracion.length === 0 && data.error.codError === 0 ) {
                    swal('Atención', 'No se encontraron resultados', 'info');
                } else {
                    if (data.recorridosAdministracion === null) {
                        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                    } else {
                        (this.recorridos = data.recorridosAdministracion);
                    }
                }
                if (data.error.codError !== 0) {
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                }
        },
        error => {
            this.loader.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        }
    );
    }
}
  enviarInfo(recorrido) {
    this.recorridosService.setDatos(recorrido);
    this.recorridoGeneralModal.hide();
  }

}
