import { Component, OnInit, OnDestroy } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

import * as _ from 'lodash';
import { NgxSpinnerService } from 'ngx-spinner';

import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { AuthService } from '../../../core/consultaService/auth.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { ArticuloService } from '../../../core/consultaService/articulo.service';
import { BuscarArticuloPopUpComponent } from '../buscarArticuloPopUp/buscarArticuloPopUp.component';

const swal = require('sweetalert');

@Component({
  selector: 'app-producto-base-pop-up',
  templateUrl: './productoBasePopUp.component.html',
  styleUrls: ['./productoBasePopUp.component.scss']
})
export class ProductoBasePopUpComponent implements OnInit, OnDestroy {

  forma: FormGroup;

  prodBase: any = {};
  titulo = '';
  origen = '';

  secondModal: BsModalRef;

  estado: Array<any> = [
    {
      codEstado: 'VIGEN',
      descEstado: 'Vigente' 
    },
    {
      codEstado: 'NOVIG',
      descEstado: 'No Vigente' 
    }
  ];

  request: any = {};

  subData: any;
  datos: Array <any> = [];
  agrupacionValida: boolean;

  constructor(public bsModalRef: BsModalRef, private authService:AuthService, private consultasService:ConsultasService,
    private articuloService:ArticuloService, public cargador: NgxSpinnerService, private modalService: BsModalService)
    {
      this.forma = new FormGroup ({
        codigoAgrupacion: new FormControl('', Validators.required),
        descripcionAgrupacion: new FormControl({value:'', disabled:true }, Validators.required),
        codigoProdBase: new FormControl('', Validators.required),
        descripcionProdBase: new FormControl('', Validators.required),
        estadoProdBase: new FormControl('', Validators.required),
      });
    }

  ngOnInit() {

    this.subData = this.consultasService.datos$.subscribe(resData =>{
      this.setData(resData);
    })

    if(this.titulo === 'Editar'){
      this.forma.controls.codigoProdBase.disable();
    }

    this.forma.controls.codigoAgrupacion.setValue(this.prodBase.jag_scodagrupacion);
    this.forma.controls.descripcionAgrupacion.setValue(this.prodBase.jag_sdesagrupacion);
    this.forma.controls.codigoProdBase.setValue(this.prodBase.jpb_scodproductobase);
    this.forma.controls.descripcionProdBase.setValue(this.prodBase.jpb_sdesproductobase);
    this.forma.controls.estadoProdBase.setValue(this.prodBase.jpb_scodestado);
  }

  ngOnDestroy() {
    if(this.subData != undefined) {
      this.subData.unsubscribe();
    }
  }

  openModalAgrupaciones(){
    const initialState = {
      titulo: 'Agrupaciones',
      origen: 'agrupacion'
    };
    this.secondModal = this.modalService.show(BuscarArticuloPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  validaAgrupacion(){
    this.agrupacionValida = false;
    if(this.forma.controls.codigoAgrupacion.value === ''){
      this.forma.controls.codigoAgrupacion.setValue('');
      this.forma.controls.descripcionAgrupacion.setValue('');
    }else{
      this.request.scodestado = 'VIGEN';
      this.request.scodagrupacion = this.forma.controls.codigoAgrupacion.value;
      this.cargador.show();
      this.articuloService.getAgrupacion(this.request)
      .subscribe((data:any)=>{
        this.cargador.hide();
        if(data){
          if(data.error.codError != 0){
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }else{
              this.datos = data.articulo;
              if(this.datos.length > 0){
                this.forma.controls.descripcionAgrupacion.setValue(this.datos[0].jag_sdesagrupacion);
                this.agrupacionValida = true;
              }else{
                this.forma.controls.codigoAgrupacion.setValue('');
                this.forma.controls.descripcionAgrupacion.setValue('');
                swal('Atención', 'No Existe Agrupación', 'warning');
              }
            }
        }
      },
      error =>{
        this.cargador.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
    }
  }

  setData(resData){
    this.forma.controls.codigoAgrupacion.setValue(resData.jag_scodagrupacion);
    this.forma.controls.descripcionAgrupacion.setValue(resData.jag_sdesagrupacion);
    this.agrupacionValida = true;
  }

  guardarProdBase(){
    if(this.forma.valid && this.agrupacionValida){
      this.request.nrunusuario = this.authService.getDatosUser().nrunusuario;
      this.request.scodagrupacion = this.forma.controls.codigoAgrupacion.value.toUpperCase();
      this.request.scodestado = this.forma.controls.estadoProdBase.value;
      this.request.scodproductobase = this.forma.controls.codigoProdBase.value.toUpperCase();
      this.request.sdesproductobase =  this.forma.controls.descripcionProdBase.value;
      if(this.titulo === 'Agregar'){
        this.request.sindnuevo = 'S';
      }
      if(this.titulo === 'Editar'){
        this.request.sindnuevo = 'N';
      }

      this.cargador.show();
      this.articuloService.creaProdBase(this.request)
        .subscribe((data:any)=>{
          this.cargador.hide();

          if(data.error.codError != 0){
            swal('Atención', data.error.msjError, 'error');
          } else{
            this.consultasService.setDatos('guardar');
            swal('Atención','Producto Base guardado correctamente', 'success');
            this.bsModalRef.hide();
          }
          
        },
        error =>{
          this.cargador.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        })
    } 
  }

}
