import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { CajaCobranzaService } from '../../../core/consultaService/cajaCobranza.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { DecimalPipe } from '@angular/common';

@Component({
  selector: 'app-documentos-asociados-pop-up',
  templateUrl: './documentosAsociadosPopUp.component.html',
  styleUrls: ['./documentosAsociadosPopUp.component.scss']
})
export class DocumentosAsociadosPopUpComponent implements OnInit {

  origen: string;
  param: string;
  titulo: string;
  forma: FormGroup;
  clientes: Array<any> = [];
  movimiento: any;
  cliente: any;
  user: any;
  cargando = false;
  hidden = true;

  constructor(public bsModalRef: BsModalRef,
    private cajaCobranzaService: CajaCobranzaService,
    private authService: AuthService,
    private decimalPipe: DecimalPipe,
  ) {

    this.forma = new FormGroup({
      tipoDoc: new FormControl({ value: '', disabled: true }),
      numDoc: new FormControl({ value: '', disabled: true }),
      montoTotal: new FormControl({ value: '', disabled: true }),
    });
  }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
    this.forma.controls.tipoDoc.setValue(this.movimiento.cte_sdestipodocto);
    this.forma.controls.numDoc.setValue(this.movimiento.nnumdocto);
    this.forma.controls.montoTotal.setValue(this.decimalPipe.transform(this.movimiento.cte_ncargo, '', 'es'));
    this.buscaDocumentosAsociados();
  }

  buscaDocumentosAsociados() {
    this.cargando = true;
    const request = {
      nIdoperacion: Number(this.movimiento.cte_nidoperacion),
      nRutOperador: Number(this.user.nrutoperador),
    };

    this.cajaCobranzaService.buscaDocumentosAsociados(request)
      .subscribe((data: any) => {
        this.cargando = false;
        if (data) {
          if (data.movimientos.length === 0 && data.error.codError === 0) {
            //swal('Atención', 'No se encontraron resultados', 'info');
          } else {
            this.clientes = data.movimientos;
          }
        }
      },
        error => {
          this.cargando = false;
        });
  }

}
