import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';

import { AuthService } from '../../../core/consultaService/auth.service';
import { AdministracionComercialService } from '../../../core/consultaService/administracionComercial.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';

const swal = require('sweetalert');

@Component({
  selector: 'app-zonas-pop-up',
  templateUrl: './zonasPopUp.component.html',
  styleUrls: ['./zonasPopUp.component.scss']
})
export class ZonasPopUpComponent implements OnInit {

  forma: FormGroup;

  zona: any = {};
  codFuerzaVenta = "";
  titulo = "";

  estado: Array<any> = [
    {
      codEstado: 'VIGEN',
      descEstado: 'Vigente' 
    },
    {
      codEstado: 'NOVIG',
      descEstado: 'No Vigente' 
    }
  ];

  request: any = {};

  constructor(public bsModalRef: BsModalRef, private authService:AuthService, public cargador: NgxSpinnerService,
    private administracionComercialService:AdministracionComercialService, private consultasService:ConsultasService) {
    this.forma = new FormGroup ({
      codigoZona: new FormControl('', Validators.required),
      descripcionZona: new FormControl('', Validators.required),
      estadoZona: new FormControl('', Validators.required),
    });
  }

  ngOnInit() { 

    console.log(this.zona);
    console.log("codfuerzaVenta", this.codFuerzaVenta);
    if(this.titulo === 'Editar'){
      this.forma.controls.codigoZona.disable();
    }

    this.forma.controls.codigoZona.setValue(this.zona.zon_scodzona);
    this.forma.controls.descripcionZona.setValue(this.zona.zon_sdeszona);
    this.forma.controls.estadoZona.setValue(this.zona.zon_scodestado);
  }

  guardarZona(){
    if(this.forma.valid){
      this.request.nRunUsuario = this.authService.getDatosUser().nrunusuario;
      this.request.nRutOperador = this.authService.getDatosUser().nrutoperador;
      this.request.sCodEstado = this.forma.controls.estadoZona.value;
      this.request.sCodFuerzaVta = this.codFuerzaVenta;
      this.request.sCodZona = this.forma.controls.codigoZona.value.toUpperCase();
      this.request.sDesZona =  this.forma.controls.descripcionZona.value;
      if(this.titulo === 'Agregar'){
        this.request.sIndNuevo = 'S';
      }
      if(this.titulo === 'Editar'){
        this.request.sIndNuevo = 'N';
      }
      
      this.cargador.show();
      console.log("request guardar zona", this.request);

      this.administracionComercialService.guardarZona(this.request)
        .subscribe((data:any)=>{
          this.cargador.hide();

          if(data.error.codError != 0){
            swal('Atención', data.error.msjError, 'error');
          } else{
            this.consultasService.setDatos('guardar');
            swal('Atención','Zona guardada correctamente', 'success');
            this.bsModalRef.hide();
          }
        },
        error =>{
          this.cargador.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        })
    } 
  }
}