  private baseUrl = environment.baseCustomUrl;

  constructor(private http: HttpClient) { }

  getGarantias(request: any) {
    // add headers
    let headers = new HttpHeaders({
      'Content-Type': 'application/json',
      'Accept': '*/*'
    });
    // busca garantías from api
    return this.http.post(this.baseUrl + "cajaCobranza/busca/Garantias", request, { headers });
  }

