import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';

import { AdministracionComercialService } from './../../../core/consultaService/administracionComercial.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { ArticuloService } from '../../../core/consultaService/articulo.service';
import { AuthService } from '../../../core/consultaService/auth.service';

const swal = require('sweetalert');

@Component({
  selector: 'app-categoria-negocio-pop-up',
  templateUrl: './categoriaNegocioPopUp.component.html',
  styleUrls: ['./categoriaNegocioPopUp.component.scss']
})
export class CategoriaNegocioPopUpComponent implements OnInit {

  cargando = false;

  origen: string;

  codFuerzaVenta:string;

  datos: Array <any> = [];
  titulo: string;

  request: any = {};

  constructor(public bsModalRef: BsModalRef,
              private administracionComercialService: AdministracionComercialService, private authService:AuthService,
              private consultasService: ConsultasService, private articuloService: ArticuloService ) {}

  ngOnInit() {
    console.log('origen', this.origen);
    this.cargando = true;
      switch (this.origen) {
        case 'catNegocio': {
          this.administracionComercialService.getCategoriaNegocio()
          .subscribe((data: any) => {
              this.datos = data.categoriaNegocio;
              this.cargando = false;
              if (data.error.codError != 0) {
                this.bsModalRef.hide();
                swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
              }
          },
        error => {
          this.bsModalRef.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
        }
        case 'paises': {
          this.request.scodestado = "VIGEN";

          this.articuloService.getPais(this.request)
              .subscribe((data: any) => {
                  this.datos = data.articulo;
                  this.cargando = false;

                  if (data.error.codError != 0) {
                    this.bsModalRef.hide();
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                  }
              },
              error => {
                this.bsModalRef.hide();
                swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
              });
        break;
        }
        case 'zona': {
          this.request.nRutOperador = this.authService.getDatosUser().nrutoperador;
          this.request.sCodEstado = "VIGEN";
          this.request.sCodFuerzaVta = this.codFuerzaVenta;

          console.log("request zona", this.request);

          this.administracionComercialService.getZonasOperador(this.request)
              .subscribe((data: any) => {
                console.log(data);
                  this.datos = data.zonasOperador;
                  this.cargando = false;

                  if (data.error.codError != 0) {
                    this.bsModalRef.hide();
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                  }
              },
              error => {
                this.bsModalRef.hide();
                swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
              });
        break;
        }
      }

  }
  enviarInfo(dato) {
    this.consultasService.setDatos(dato);
    this.bsModalRef.hide();
  }

}
