import { Component, OnInit, OnDestroy } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

import * as _ from 'lodash';
import { NgxSpinnerService } from 'ngx-spinner';

import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { AuthService } from '../../../core/consultaService/auth.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { ArticuloService } from '../../../core/consultaService/articulo.service';
import { BuscarArticuloPopUpComponent } from '../buscarArticuloPopUp/buscarArticuloPopUp.component';

const swal = require('sweetalert');

@Component({
  selector: 'app-categoria-pop-up',
  templateUrl: './categoriaPopUp.component.html',
  styleUrls: ['./categoriaPopUp.component.scss']
})
export class CategoriaPopUpComponent implements OnInit, OnDestroy {

  forma: FormGroup;

  categoria: any = {};
  titulo = '';
  origen = '';

  secondModal: BsModalRef;

  estado: Array<any> = [
    {
      codEstado: 'VIGEN',
      descEstado: 'Vigente' 
    },
    {
      codEstado: 'NOVIG',
      descEstado: 'No Vigente' 
    }
  ];

  request: any = {};

  subData: any;
  datos: Array <any> = [];
  lineaValida: boolean;

  constructor(public bsModalRef: BsModalRef, private authService:AuthService, private consultasService:ConsultasService,
    private articuloService:ArticuloService, public cargador: NgxSpinnerService, private modalService: BsModalService) {
      this.forma = new FormGroup ({
        codigoLinea: new FormControl('', Validators.required),
        descLinea: new FormControl({value:'', disabled:true }, Validators.required),
        codigoCategoria: new FormControl('', Validators.required),
        descripcionCategoria: new FormControl('', Validators.required),
        estadoCategoria: new FormControl('', Validators.required),
      });
    }

  ngOnInit() {
    this.subData = this.consultasService.datos$.subscribe(resData =>{
      this.setData(resData);
    })

    if(this.titulo === 'Editar'){
      this.forma.controls.codigoCategoria.disable();
    }

    this.forma.controls.codigoLinea.setValue(this.categoria.jli_scodlinea);
    this.forma.controls.descLinea.setValue(this.categoria.jli_sdeslinea);
    this.forma.controls.codigoCategoria.setValue(this.categoria.jca_scodcategoria);
    this.forma.controls.descripcionCategoria.setValue(this.categoria.jca_sdescategoria);
    this.forma.controls.estadoCategoria.setValue(this.categoria.jca_scodestado);
  }

  ngOnDestroy() {
    if(this.subData != undefined) {
      this.subData.unsubscribe();
    }
  }

  openModalLineas(){
    const initialState = {
      titulo: 'Líneas',
      origen: 'linea'
    };
    this.secondModal = this.modalService.show(BuscarArticuloPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  validaLinea(){
    this.lineaValida = false;
    if(this.forma.controls.codigoLinea.value === ''){
      this.forma.controls.codigoLinea.setValue('');
      this.forma.controls.descLinea.setValue('');
    }else{
      this.request.scodestado = 'VIGEN';
      this.request.scodlinea = this.forma.controls.codigoLinea.value;
      this.cargador.show();
      this.articuloService.getLineas(this.request)
      .subscribe((data:any)=>{
        this.cargador.hide();
        if(data){
          if(data.error.codError != 0){
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }else{
              this.datos = data.articulo;
              if(this.datos.length > 0){
                this.forma.controls.descLinea.setValue(this.datos[0].jli_sdeslinea);
                this.lineaValida = true
              }else{
                this.forma.controls.codigoLinea.setValue('');
                this.forma.controls.descLinea.setValue('');
                swal('Atención', 'No Existe Línea', 'warning');
              }
            }
        }
      },
      error =>{
        this.cargador.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
    }
  }

  setData(resData){
    this.forma.controls.codigoLinea.setValue(resData.jli_scodlinea);
    this.forma.controls.descLinea.setValue(resData.jli_sdeslinea);
    this.lineaValida = true
  }

  guardarCategoria(){
    if(this.forma.valid && this.lineaValida){

      this.request.nrunusuario = this.authService.getDatosUser().nrunusuario;
      this.request.scodcategoria = this.forma.controls.codigoCategoria.value.toUpperCase();
      this.request.scodestado = this.forma.controls.estadoCategoria.value;
      this.request.scodlinea = this.forma.controls.codigoLinea.value.toUpperCase();
      this.request.sdescategoria =  this.forma.controls.descripcionCategoria.value;
      if(this.titulo === 'Agregar'){
        this.request.sindnuevo = 'S';
      }
      if(this.titulo === 'Editar'){
        this.request.sindnuevo = 'N';
      }

      this.cargador.show();
      this.articuloService.creaCategoria(this.request)
        .subscribe((data:any)=>{
          this.cargador.hide();

          if(data.error.codError != 0){
            swal('Atención', data.error.msjError, 'error');
          } else{
            this.consultasService.setDatos('guardar');
            swal('Atención','Categoría guardada correctamente', 'success');
            this.bsModalRef.hide();
          }
          
        },
        error =>{
          this.cargador.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        })
    }
    
    
  }

}
