import { Component, OnInit } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ClientesService } from '../../../core/consultaService/clientes.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { PedidosService } from '../../../core/consultaService/pedidos.service';
const swal = require('sweetalert');

@Component({
  selector: 'app-relaComerPedidoPopUp',
  templateUrl: './relaComerPedidoPopUp.component.html',
  styleUrls: ['./relaComerPedidoPopUp.component.scss']
})
export class RelaComerPedidoPopUpComponent implements OnInit {
  origen: string;
  param: string;
  parametro: any;
  parametrodos: any;
  parametrotres: any;
  titulo: string;
  cargando = false;
  datos: Array <any> = [];
  rutcliente: any;
  rutoperador: any;
  request: any = {};

  constructor(private setearDatos: ConsultasService, public relaComerPedModal: BsModalRef,
    private modalService: BsModalService, private consultaDirecciones: ClientesService, private pedidosService: PedidosService) { }

  ngOnInit() {
    this.cargando = true;

    switch (this.origen) {
      case 'relacionesPedidos': {
        this.titulo = 'Relaciones Comerciales';
        this.request.nNumLocal = this.parametrotres;
        this.request.nRutCliente = this.parametrodos;
        this.request.nRutOperador = this.parametro;
        console.log(this.request);
        this.pedidosService.getRelacionesPedidos(this.request)
            .subscribe((data: any) => {
              console.log(data);
                (this.datos = data.relacionComercial);
                this.cargando = false;

            if (data.error.codError !== 0) {
              this.relaComerPedModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
          },
        error => {
          this.relaComerPedModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
    }
  }
  enviarInfo(dato) {
    this.setearDatos.setDatos(dato);
    this.relaComerPedModal.hide();
  }

}
