import { Component, OnInit } from '@angular/core';

import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { ClientesService } from '../../../core/consultaService/clientes.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { AdministracionOperativaService } from '../../../core/consultaService/administracionOperativa.service';

import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { FormGroup, FormControl, Validators } from '@angular/forms';



const swal = require('sweetalert');
declare var jQuery: any;
declare var $: any;
@Component({
  selector: 'app-bodegaPopUp',
  templateUrl: './bodegaPopUp.component.html',
  styleUrls: ['./bodegaPopUp.component.scss']
})
export class BodegaPopUpComponent implements OnInit {

  bodega: any;
  forma: FormGroup;
  subData: any;
  user: any = {};
  request: any = {};
  datos: Array<any> = [];

  sCodBodega: "";
  sCodEstado: "";
  sCodSucursal: "";
  sDescBodega: "";
  titulo = "";

  constructor(private clientesService: ClientesService, private authService: AuthService,
    private administracionOperativaServiceDatos: AdministracionOperativaService, public bodegaModal: BsModalRef,
     private consultasService:ConsultasService) {
    this.user = this.authService.getDatosUser();
       this.forma = new FormGroup({
    });
  }

  ngOnInit() {
    console.log( "init " + this.user);
    this.request.runOperador= this.user.nrutoperador;
    this.administracionOperativaServiceDatos.getBodegas(this.request)
      .subscribe((data: any) => {
        console.log(data);
        (this.datos = data.bodegas);
        console.log(" Data Bodega "  +this.datos)
      },
        error => {
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }

  enviarInfo(dato) {
    console.log(dato);
    this.consultasService.setDatos(dato);
    this.bodegaModal.hide();
  }

}
