import { Component, OnInit } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { AdministracionOperativaService } from '../../../core/consultaService/administracionOperativa.service';

const swal = require('sweetalert');


@Component({
  selector: 'app-conductororesPopUp',
  templateUrl: './conductororesPopUp.component.html',
  styleUrls: ['./conductororesPopUp.component.scss']
})
export class ConductororesPopUpComponent implements OnInit {
  titulo: string;
  origen: string;
  cargando = false;
  datos: Array <any> = [];
  ruttransporte = this.transportistaService.selectedTransportista.tra_nruttransportista;
  rutoperador = this.transportistaService.selectedTransportista.tra_nrutoperador;
  request: any = {};

  constructor(public conductoresModal: BsModalRef, private modalService: BsModalService,
    private transportistaService: AdministracionOperativaService) { }

  ngOnInit() {
    this.cargando = true;
    switch (this.origen) {
      case 'conductores': {
        this.request.codEstadoChofer = 'VIGEN';
        this.request.rutOperador = this.rutoperador;
        this.request.rutTransportista = this.ruttransporte;
        this.transportistaService.getConductores(this.request)
            .subscribe((data: any) => {
                (this.datos = data.conductores);
                this.cargando = false;
                if (data.error.codError !== 0) {
                  this.conductoresModal.hide();
                  swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                }
            },
            error => {
              this.conductoresModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            });
            break;
          }
    }
  }
  enviarInfo(dato) {
    this.transportistaService.setDatos(dato);
    this.conductoresModal.hide();
  }

}
