import { Component, OnInit, Input} from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BsModalRef } from 'ngx-bootstrap/modal';

import * as _ from 'lodash';
import { NgxSpinnerService } from 'ngx-spinner';

import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { AuthService } from '../../../core/consultaService/auth.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { ArticuloService } from '../../../core/consultaService/articulo.service';

const swal = require('sweetalert');

@Component({
  selector: 'app-pais-pop-up',
  templateUrl: './paisPopUp.component.html',
  styleUrls: ['./paisPopUp.component.scss']
})
export class PaisPopUpComponent implements OnInit{

  forma: FormGroup;

  pais: any = {};
  titulo = "";

  estado: Array<any> = [
    {
      codEstado: 'VIGEN',
      descEstado: 'Vigente' 
    },
    {
      codEstado: 'NOVIG',
      descEstado: 'No Vigente' 
    }
  ];

  request: any = {};

  constructor(public bsModalRef: BsModalRef, private authService:AuthService, private consultasService:ConsultasService,
              private articuloService:ArticuloService, public cargador: NgxSpinnerService) {
    this.forma = new FormGroup ({
      codigoPais: new FormControl('', Validators.required),
      descripcionPais: new FormControl('', Validators.required),
      estadoPais: new FormControl('', Validators.required),
    });
  }

  ngOnInit() {

    if(this.titulo === 'Editar'){
      this.forma.controls.codigoPais.disable();
    }

    this.forma.controls.codigoPais.setValue(this.pais.jpa_scodpais);
    this.forma.controls.descripcionPais.setValue(this.pais.jpa_sdespais);
    this.forma.controls.estadoPais.setValue(this.pais.jpa_scodestado);
  }

  guardarPais(){
    if(this.forma.valid){
      this.request.nrunusuario = this.authService.getDatosUser().nrunusuario;
      this.request.scodestado = this.forma.controls.estadoPais.value;
      this.request.scodpais = this.forma.controls.codigoPais.value.toUpperCase();
      this.request.sdespais =  this.forma.controls.descripcionPais.value;
      if(this.titulo === 'Agregar'){
        this.request.sindnuevo = 'S';
      }
      if(this.titulo === 'Editar'){
        this.request.sindnuevo = 'N';
      }
      
      this.cargador.show();
      this.articuloService.creaPais(this.request)
        .subscribe((data:any)=>{
          this.cargador.hide();

          if(data.error.codError != 0){
            swal('Atención', data.error.msjError, 'error');
          } else{
            this.consultasService.setDatos('guardar');
            swal('Atención','País guardado correctamente', 'success');
            this.bsModalRef.hide();
          }
          
        },
        error =>{
          this.cargador.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        })
     
      // this.bsModalRef.hide();

    }
    
  }

}
