import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, NgForm } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { NgxSpinnerService } from 'ngx-spinner';
import { UtilService } from './../../../services/util.service';
import { AdministracionOperativaService } from '../../../core/consultaService/administracionOperativa.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { ConductororesPopUpComponent } from '../conductororesPopUp/conductororesPopUp.component';
import { ConsultasService } from '../../../core/consultaService/consultas.service';

const swal = require('sweetalert');
declare var jQuery: any;
declare var $: any;
@Component({
  selector: 'app-choferPopUp',
  templateUrl: './choferPopUp.component.html',
  styleUrls: ['./choferPopUp.component.scss']
})
export class ChoferPopUpComponent implements OnInit, OnDestroy {
  forma: FormGroup;
  conductor: any;
  subData: any;
  creaChofer: any = {};
  origen: string;
  titulo: string;
  nuevo: string;
  choferesModal: BsModalRef;
  prueba = 0;
  ruttransporte = this.transportistaService.selectedTransportista.tra_nruttransportista;
  rutoperador = this.transportistaService.selectedTransportista.tra_nrutoperador;
  estado: Array<any> = [
    {
      cho_scodestado: 'VIGEN',
      descEstado: 'Vigente'
    },
    {
      cho_scodestado: 'NOVIG',
      descEstado: 'No Vigente'
    }
  ];
  tipoVehiculos: Array<any> = [];
  request: any = {};
  consultaVehiculo: any = {};
  longitud_rutChofer: string;
  longitud_rutChofer2 = 0;
  rutValido: boolean;
  user: any = {};

  constructor(fb: FormBuilder, private transportistaService: AdministracionOperativaService,
    public choferVehiculoModal: BsModalRef, private utilService: UtilService,
    private modalService: BsModalService, public loader: NgxSpinnerService,
    private setearDatos: ConsultasService, private authService: AuthService) {
      this.forma = new FormGroup ({
        rutConductor: new FormControl('', Validators.required),
        digVerificador: new FormControl(''),
        nombresConductor: new FormControl('', Validators.required),
        apePaterno: new FormControl('', Validators.required),
        apeMaterno: new FormControl('', Validators.required),
        estado: new FormControl('', Validators.required)
      });
     }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
    if (this.conductor === undefined ) {
      console.log('es nuevo');
      console.log(this.conductor);
      this.nuevo = 'S';
    } else {
      this.nuevo = 'N';
      console.log(this.conductor);
      this.forma.controls.rutConductor.setValue(this.conductor.cho_nrunchofer + this.conductor.cho_sdigitoverificador);
      this.forma.controls.rutConductor.disable();
      this.forma.controls.digVerificador.setValue(this.conductor.cho_sdigitoverificador);
      this.forma.controls.nombresConductor.setValue(this.conductor.cho_snombre);
      this.forma.controls.apePaterno.setValue(this.conductor.cho_sapepaterno);
      this.forma.controls.apeMaterno.setValue(this.conductor.cho_sapematerno);
      this.forma.controls.estado.setValue(this.conductor.cho_scodestado);
    }
  }
  ngOnDestroy() {
    if (this.subData !== undefined) {
      this.subData.unsubscribe();
    }
  }
  validaRut() {
    this.rutValido = false;
    if (!this.utilService.isValidRUT(this.forma.controls.rutConductor.value)) {
      this.forma.controls.rutConductor.setValue('');
      swal('Atención', 'El RUT es incorrecto', 'error');
    } else {
      this.rutValido =  true;
    }
  }
  guardarChofer ($event: any, succesChofer: string) {
    if (this.forma.valid) {
      $('*').removeClass('error-form');
      this.loader.show();
      this.longitud_rutChofer = this.forma.controls.rutConductor.value.toString();
      this.longitud_rutChofer2 = this.longitud_rutChofer.length;

      this.creaChofer.apeMatchofer = this.forma.controls.apeMaterno.value;
      this.creaChofer.apePatchofer = this.forma.controls.apePaterno.value;
      this.creaChofer.codEstado = this.forma.controls.estado.value;
      this.creaChofer.digitoVerificador = this.longitud_rutChofer.substr((this.longitud_rutChofer2 - 1), 1);
      this.creaChofer.indNuevo = this.nuevo;
      this.creaChofer.nombreChofer = this.forma.controls.nombresConductor.value;
      this.creaChofer.runChofer = Number(this.longitud_rutChofer.substr(0, this.longitud_rutChofer2 - 1));
      this.creaChofer.runCreador = Number(this.user.nrunusuario);
      this.creaChofer.rutOperador = Number(this.rutoperador);
      this.creaChofer.rutTransportista = Number(this.ruttransporte);
      console.log(this.creaChofer);
      this.transportistaService.saveChofer(this.creaChofer)
          .subscribe((data: any) => {
            if (data.error.codError === 0) {
              this.loader.hide();
              swal('Bien', 'Los datos han sido ingresados satisfactoriamente.', 'success' );
              this.transportistaService.setDatos(succesChofer);
              this.choferVehiculoModal.hide();
            } else {
              swal('Atención', data.error.msjError, 'error');
              this.loader.hide();
            }
          },
          error => {
            this.loader.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          });

    } else {
      swal('Atención', 'Existen campos requeridos no ingresados', 'error' );
      if (!this.forma.controls.rutConductor.valid) {
        $('[name=rutConductor]').addClass('error-form');
      } else {
        $('[name=rutConductor]').removeClass('error-form');
      }
      if (!this.forma.controls.nombresConductor.valid) {
        $('[name=nombresConductor]').addClass('error-form');
      } else {
        $('[name=nombresConductor]').removeClass('error-form');
      }
      if (!this.forma.controls.apeMaterno.valid) {
        $('[name=apeMaterno]').addClass('error-form');
      } else {
        $('[name=apeMaterno]').removeClass('error-form');
      }
      if (!this.forma.controls.apePaterno.valid) {
        $('[name=apePaterno]').addClass('error-form');
      } else {
        $('[name=apePaterno]').removeClass('error-form');
      }
      if (!this.forma.controls.estado.valid) {
        $('[name=estado]').addClass('error-form');
      } else {
        $('[name=estado]').removeClass('error-form');
      }
      this.loader.hide();
    }
  }

}
