import { UtilService } from './../../../services/util.service';
import { CajaCobranzaService } from "./../../../core/consultaService/cajaCobranza.service";
import { Component, OnInit } from "@angular/core";
import { BsModalRef } from "ngx-bootstrap/modal";
import { FormGroup, FormControl } from "@angular/forms";
import { AuthService } from "../../../core/consultaService/auth.service";

@Component({
  selector: "app-notadecreditopopup",
  templateUrl: "./notaDeCreditoPopUp.component.html",
  styleUrls: ["./notaDeCreditoPopUp.component.scss"]
})
export class NotaDeCreditoPopUpComponent implements OnInit {
  origen: string;
  titulo: string;
  forma: FormGroup;
  user: any;
  descripcionArticulos: any;
  fechaEmision: any;
  fechaVencimiento: any;

  articulosArray = [];
  request: any = {};

  fechaPedido: any;
  numeroDeNota: any;
  pActualBuscaCliente =1 ;

  constructor(
    public notaDeCreditoModal: BsModalRef,
    private cajaCobranzaService: CajaCobranzaService,
    private authService: AuthService,
    public utilService: UtilService,
  ) {
    this.forma = new FormGroup({
      rutCliente: new FormControl({value: '', disabled: true}),
      nombreCliente: new FormControl({value: '', disabled: true}),
      nomDireccion: new FormControl({value: '', disabled: true}),
      nomDireccionEntrega: new FormControl({value: '', disabled: true}),
      codTerritorio: new FormControl({value: '', disabled: true}),
      nomTerritorio: new FormControl({value: '', disabled: true}),
      codRuta: new FormControl({value: '', disabled: true}),
      nomRuta: new FormControl({value: '', disabled: true}),
      condicionPago: new FormControl({value: '', disabled: true}),
      fechaPedido: new FormControl({value: '', disabled: true}),
      fechaEmision: new FormControl({value: '', disabled: true}),
      fechaEntrega: new FormControl({value: '', disabled: true}),
      factura: new FormControl({value: '', disabled: true}),
      ntotal: new FormControl({value: '', disabled: true}),
      ndescuento: new FormControl({value: '', disabled: true}),
      nnetobase: new FormControl({value: '', disabled: true}),
      niva: new FormControl({value: '', disabled: true}),
      niaba: new FormControl({value: '', disabled: true}),
      totalTotal: new FormControl({value: '', disabled: true}),
      nkilos: new FormControl({value: '', disabled: true}),
      nvolumen: new FormControl({value: '', disabled: true})
    });
  }

  ngOnInit() {

    this.user = this.authService.getDatosUser();
    this.request.nRutOperador = this.user.nrutoperador;

    this.cajaCobranzaService
      .cargaNotaCredito(this.request)
      .subscribe((data: any) => {
        if (data != null) {
          this.detalle(data);
        }
      });


    }

    detalle(data: any) {


    this.descripcionArticulos = data.detalleNotaCredito;

    this.fechaPedido = this.utilService.dateStringServiceToReverseStringFormat(data.notaCredito["0"].dfhopedido);
    this.fechaEmision = this.utilService.dateStringServiceToReverseStringFormat(data.notaCredito["0"].dfhoemision);
    this.fechaVencimiento = this.utilService.dateStringServiceToReverseStringFormat(data.notaCredito["0"].dfhovencimiento);


    for (var key in this.descripcionArticulos) {
      if (this.descripcionArticulos.hasOwnProperty(key)) {
        this.articulosArray.push(this.descripcionArticulos[key]);
      }
    }

    this.numeroDeNota = data.notaCredito["0"].nnumnotacredito;

    this.forma.controls.rutCliente.setValue( this.utilService.formatDotNumber(data.notaCredito["0"].nrutcliente) + "-" + data.notaCredito["0"].sdigverificador );
    this.forma.controls.nombreCliente.setValue(data.notaCredito["0"].snombre);
    this.forma.controls.nomDireccion.setValue(data.notaCredito["0"].sdireccion);
    this.forma.controls.nomDireccionEntrega.setValue(data.notaCredito["0"].sdireccionentrega);
    this.forma.controls.codTerritorio.setValue(data.notaCredito["0"].scodterritorio);
    this.forma.controls.nomTerritorio.setValue(data.notaCredito["0"].sdesterritorio);
    this.forma.controls.codRuta.setValue(data.notaCredito["0"].scodruta);
    this.forma.controls.nomRuta.setValue(data.notaCredito["0"].sdesruta);
    this.forma.controls.condicionPago.setValue( "(" + data.notaCredito["0"].scodformapago + ") " + data.notaCredito["0"].sdesformapago );

    if (data.notaCredito["0"].dfhopedido === " " || data.notaCredito["0"].dfhopedido === "") {
      this.forma.controls.fechaPedido.setValue("//");
    } else {
      this.forma.controls.fechaPedido.setValue( this.fechaPedido );
    }

    this.forma.controls.fechaEmision.setValue( this.fechaEmision );
    this.forma.controls.fechaEntrega.setValue( this.fechaVencimiento );
    this.forma.controls.factura.setValue( data.notaCredito["0"].nnumfactura ? data.notaCredito["0"].nnumfactura : "NINGUNA"  );

    this.forma.controls.ntotal.setValue(this.utilService.formatDotNumber(data.notaCredito["0"].ntotal));
    this.forma.controls.ndescuento.setValue(data.notaCredito["0"].ndescuento);
    this.forma.controls.nnetobase.setValue(this.utilService.formatDotNumber(data.notaCredito["0"].nnetobase));
    this.forma.controls.niva.setValue(this.utilService.formatDotNumber(data.notaCredito["0"].niva));
    this.forma.controls.niaba.setValue(this.utilService.formatDotNumber(data.notaCredito["0"].niaba));
    this.forma.controls.nkilos.setValue(data.notaCredito["0"].nkilos);
    this.forma.controls.nvolumen.setValue(data.notaCredito["0"].nvolumen);
  }
}
