import { Component, OnInit } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { ClientesService } from '../../../core/consultaService/clientes.service';

const swal = require('sweetalert');

@Component({
  selector: 'app-buscaBancoPopUp',
  templateUrl: './buscaBancoPopUp.component.html',
  styleUrls: ['./buscaBancoPopUp.component.scss']
})
export class BuscaBancoPopUpComponent implements OnInit {

  titulo: string;
  origen: string;
  cargando = false;
  request: any = {
    "parnumparametro": 20
  };
  datos: Array <any> = [];
  relacComer: any = {};
  rutoperador = this.clientesService.selectedCliente.cli_nrutoperador;

  constructor(private setearDatos: ConsultasService, public buscaBancoModal: BsModalRef,
    private modalService: BsModalService, private clientesService: ClientesService) { }

  ngOnInit() {
    this.cargando = true;
    switch (this.origen) {

    case 'buscaBanco': {
    this.setearDatos.getTipoCliente(this.request)
        .subscribe((data: any) => {
            (this.datos = data.general);
            this.cargando = false;

            if (data.error.codError != 0) {
              this.buscaBancoModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        },
        error => {
          this.buscaBancoModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
      case 'relaciones': {
        this.relacComer.nRutOperador = this.rutoperador;
        this.clientesService.getRelacionesOperador(this.relacComer)
            .subscribe((data: any) => {
                (this.datos = data.relacionComercial);
                this.cargando = false;
                if (data.error.codError != 0) {
                  this.buscaBancoModal.hide();
                  swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                }
            },
            error => {
              this.buscaBancoModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            });
            break;
          }


    }
  }
  enviarInfo(dato) {
    this.setearDatos.setDatos(dato);
    this.buscaBancoModal.hide();
  }

}
