import { DocumentosService } from "./../../../core/consultaService/documentos.service";
import { ConsultasService } from "./../../../core/consultaService/consultas.service";
import { Component, OnInit } from "@angular/core";
import { BsModalRef } from "ngx-bootstrap/modal";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-detalle-impresion-facturas-error-pop-up",
  templateUrl: "./detalleImpresionFacturasErrorPopUp.component.html",
  styleUrls: ["./detalleImpresionFacturasErrorPopUp.component.scss"]
})
export class DetalleImpresionFacturasErrorPopUpComponent implements OnInit {
  lote: any;
  idRecorrido: any;
  lfel_id_recorrido: any;
  tipoImpresion: any;
  estadoloteimpresion: any;
  cantDocs: any;
  cantDocsError: any;
  rutOperador: any;
  request2anterior: any;

  request: any = {};
  resultados: any;

  //   lote
  // idRecorrido
  // tipoImpresion
  // fechaEntrega: lo
  // estadoloteimpres
  // cantDocs: lott.l
  // cantDocsError: l
  // request2anterior

  constructor(
    public bsModalRef: BsModalRef,
    private consultasService: ConsultasService,
    private documentosService: DocumentosService,
    public loader: NgxSpinnerService
  ) {}

  ngOnInit() {
    this.resultados = false;

    this.request2anterior = this.request2anterior;

    this.rutOperador = parseInt(
      JSON.parse(localStorage.getItem("DatosUsuario"))["nrutoperador"],
      10
    );

    console.log("request2anterior", this.request2anterior);

    this.request = {
      ncodlote: this.request2anterior.ncodlote,
      ncodrecorrido: this.request2anterior.ncodrecorrido,
      nrutoperador: this.rutOperador,
      scodtipoimpresion: this.request2anterior.scodtipoimpresion
    };

    this.loader.show();
    this.documentosService.buscaDocumentoError(this.request).subscribe(res => {
      this.loader.hide();
      this.cantDocsError = res["documentos"];
    });
  }

  // goBack() {
  //   window.history.back();
  // }

  imprimir() {
    let printContents, popupWin;
    printContents = document.getElementById("print-section").innerHTML;
    popupWin = window.open("", "_blank", "top=0,left=0,height=100%,width=auto");
    popupWin.document.open();
    popupWin.document.write(`
      <html>
        <head>
          <title>Print tab</title>
          <style>
          </style>
        </head>
    <body onload="window.print();window.close()">${printContents}</body>
      </html>`);
    // popupWin.document.close();
  }
}
