import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { CajaCobranzaService } from '../../../core/consultaService/cajaCobranza.service';
import { UtilService } from './../../../services/util.service';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { NgxSpinnerService } from 'ngx-spinner';
import { AuthService } from '../../../core/consultaService/auth.service';
import { DatePipe } from '@angular/common';

const swal = require('sweetalert');

@Component({
  selector: 'app-detalle-reversa-pago-info-pop-up',
  templateUrl: './detalleReversaPagoInfoPopUp.component.html',
  styleUrls: ['./detalleReversaPagoInfoPopUp.component.scss']
})
export class DetalleReversaPagoInfoPopUpComponent implements OnInit {

  titulo: string;
  parametros: any;

  forma: FormGroup;
  pageActual = 1;
  datos: Array<any> = [];
  origen: string;
  clienteModal: any;
  params: any;
  sub: any;
  jsonParams: any;
  user: any = {};
  checkTituloSeleccionado = false;

  constructor(
    private cajaCobranzaService: CajaCobranzaService,
    private utilService: UtilService,
    public cargador: NgxSpinnerService,
    private authService: AuthService,
    public bsModalRef: BsModalRef,
  ) {
    this.forma = new FormGroup({
      rutCliente: new FormControl({ value: '', disabled: true }),
      nombreCliente: new FormControl({ value: '', disabled: true }),
      fecha: new FormControl({ value: '', disabled: true }),
      tipoPagoDesc: new FormControl({ value: '', disabled: true }),
      montoPagar: new FormControl({ value: '', disabled: true }),
      montoReversa: new FormControl({ value: '', disabled: true }),
      indicador: new FormControl({ value: '', disabled: true }),
    });
  }

  ngOnInit() {

    this.user = this.authService.getDatosUser();

    this.forma.setValue({
      rutCliente: this.parametros.formulario.rutCliente,
      nombreCliente: this.parametros.formulario.nombreCliente,
      fecha: this.convertirFecha(this.parametros.formulario.fecha),
      tipoPagoDesc: this.parametros.formulario.tipoPagoDesc,
      montoPagar: this.parametros.formulario.montoPagar,
      montoReversa: this.parametros.formulario.montoReversa,
      indicador:  this.parametros.formulario.indicador,
    });
    this.buscar(this.parametros.formulario.ere_nidreversa);

  }

  ngOnDestroy() { }

  buscar(nIdOperacion) {
    this.cargador.show();
    const request = {
      nIdoperacion: Number(nIdOperacion),
      nRutOperador: Number(this.user.nrutoperador),
    };

    this.cajaCobranzaService.buscaDocumentosAsociados(request)
      .subscribe((data: any) => {
        this.cargador.hide();
        if (data) {
          if (data.error.codError !== 0) {
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            return;
          }
          if (data.movimientos.length === 0 && data.error.codError === 0) {
            swal('Atención', 'No se encontraron resultados', 'info');
          } else {
            const datos = [];
            data.movimientos.forEach(element => {
              const item = Object.assign(element, { bCheck: false });
              datos.push(item);
            });
            this.datos = datos;
          }
        } else {
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        }
      },
        error => {
          this.cargador.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }



  convertirFecha(fecha) {
    if (fecha) {
      const regexp = /(\d{4})(\d{2})(\d{2})$/;
      const tag = fecha.match(regexp);
      return tag[1] + '-' + tag[2] + '-' + tag[3];
    }
    return '';
  }

  convertirFechaEspecial(fecha) {
    if (fecha) {
      const regexp = /(\d{4})(\d{2})(\d{2})(\d{6})$/;
      const tag = fecha.match(regexp);
      return tag[3] + '/' + tag[2] + '/' + tag[1];
    }
    return '';
  }

  cancelar() {
    this.bsModalRef.hide();
  }

  validaRut() {
    throw new Error('Method not implemented.');
  }


}
