import { Component, OnInit, OnDestroy, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, AbstractControl } from '@angular/forms';

import { BsModalRef } from 'ngx-bootstrap/modal';
import * as _ from 'lodash';
import { NgxSpinnerService } from 'ngx-spinner';
import * as moment from 'moment';

import { AdministracionComercialService } from '../../../core/consultaService/administracionComercial.service';
import { AuthService } from './../../../core/consultaService/auth.service';
import { ArticuloService } from '../../../core/consultaService/articulo.service';
import { GestionAccionComercialService } from '../../../core/consultaService/gestionAccionComercial.service';
import { UtilService } from '../../../services/util.service';
import { OperadoresPopUpComponent } from '../operadoresPopUp/operadoresPopUp.component';

const swal = require('sweetalert');

@Component({
  selector: 'app-inversionComercialPopup',
  templateUrl: './inversionComercialPopup.component.html'
})
export class InversionComercialPopupComponent implements OnInit, OnDestroy {

  forma: FormGroup;
  titulo = '';
  montoInversionFormateado: string;
  inversion: any;
  operadores: Array<any>;
  operadoresReceptores: Array<any>;
  tiposInversion: Array<any> = [];
  deshabilitaControles = false;
  request: any = {};
  categorias: Array<any> = [];
  user: any = {};
  listadoEstadosInversion: Array<any> = [];
  tipoOperacion = 'guardar';
  fechaDesde = new Date();
  fechaHasta = new Date();
  fechaCreacion = new Date();
  NombreOperadorReceptor: any;

  constructor(
    public bsModalRef: BsModalRef,
    private authService: AuthService,
    private articuloService: ArticuloService,
    public spinner: NgxSpinnerService,
    private administracionComercialService: AdministracionComercialService,
    private gestionAccionComercialService: GestionAccionComercialService,
    private utilService: UtilService) {
    this.forma = new FormGroup({
      descripcionInversion: new FormControl('', Validators.required),
      operador: new FormControl('', Validators.required),
      tipoInversion: new FormControl('', Validators.required),
      categoria: new FormControl('', Validators.required),
      montoInversion: new FormControl('', Validators.required),
      fechaDesde: new FormControl('', Validators.required),
      fechaHasta: new FormControl('', Validators.required),
      fechaCreacion: new FormControl('', Validators.required),
      operadorReceptor: new FormControl('', Validators.required),
      saldoInversion: new FormControl('', Validators.required),
      par_codparametro01: new FormControl(''),
      guardar: new FormControl('')
    });
  }

  ngOnInit() {
    this.listarOperadores();
    if (this.titulo !== 'Nueva') {
      this.listarEstadosInversion();
      this.deshabilitaControles = true;
      this.configurarAccesibilidadControles(true);
      this.tipoOperacion = 'Transferir';
      console.log('this.inversion => : ', this.inversion);
    } else {
      this.deshabilitaControles = false;
      this.configurarAccesibilidadControles(false);
    }
    this.listarTiposInversion();
    this.listarCategorias();
    this.user = this.authService.getDatosUser();
    this.inicializarFechas();
  }

  listarTiposInversion() {
    this.spinner.show();
    this.gestionAccionComercialService.listarTiposInversion()
      .subscribe((tiposInversion: any) => {
        this.tiposInversion = tiposInversion.tipoInversion;
        if (tiposInversion.error.codError !== 0) {
          this.spinner.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', tiposInversion.error);
        }
        this.spinner.hide();
      },
        error => {
          this.spinner.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', error);
        });
  }

  listarCategorias() {
    this.spinner.show();
    this.request.scodestado = 'VIGEN';
    this.articuloService.getCategorias(this.request)
      .subscribe((categorias: any) => {
        this.categorias = categorias.articulo;
        if (categorias.error.codError !== 0) {
          this.spinner.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        }
        this.spinner.hide();
      },
        error => {
          this.spinner.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }

  configurarAccesibilidadControles(deshabilitarControles: boolean) {
    Object.keys(this.forma.controls).forEach((control: string) => {
      const typedControl: AbstractControl = this.forma.controls[control];
      deshabilitarControles === true ? typedControl.disable() : typedControl.enable();
    });
    this.forma.controls.operadorReceptor.enable();
  }

  cargarControles() {
    this.forma.controls.descripcionInversion.setValue(this.inversion.tipoInversion);
    this.forma.controls.operador.setValue(this.inversion.operador);
    this.forma.controls.tipoInversion.setValue(this.inversion.tipoInversion);
    this.forma.controls.categoria.setValue(this.inversion.categoria);

    this.forma.controls.par_codparametro01.setValue(this.listadoEstadosInversion.filter(
      estadoInversion => estadoInversion.par_codparametro01 === this.inversion.inv_sEstado.toString())[0].par_deslargo01);

    this.forma.controls.montoInversion.setValue(this.inversion.inv_nmontoini);
    this.forma.controls.saldoInversion.setValue(this.inversion.inv_saldo_categoria);
  }

  listarOperadores() {
    this.spinner.show();
    this.request = {
      'nNumLocal': 0,
      'nRutCliente': 0,
      'nRutOperador': 0,
      'sCodBusqueda': 'OPERA',
      'sCodCatNegocio': '',
      'sCodRelacionComer': '',
      'sCodTerritorio': '',
      'sCodZona': '',
      'sDesRelacionComer': '',
      'sNomCliente': '',
      'scodFuerzavta': ''
    };

    this.administracionComercialService.getBuscaEstructuraComercial(this.request)
      .subscribe((operadores: any) => {
        this.operadores = operadores.operadores;
        this.listarOperadoresReceptores();

        if (operadores.error.codError !== 0) {
          this.spinner.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', operadores.error);
        }
        this.spinner.hide();
      },
        error => {
          this.spinner.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', error);
        });
  }

  listarEstadosInversion() {
    this.spinner.show();
    this.gestionAccionComercialService.listarEstadosInversion()
      .subscribe((estadosInversion: any) => {
        this.listadoEstadosInversion = estadosInversion;
        if (estadosInversion.error.codError !== 0) {
          this.spinner.hide();
          swal('Atención', `Ha ocurrido el siguiente error en listarEstadosInversion() : ${estadosInversion.error}`, 'error');
        } else {
          this.listadoEstadosInversion = estadosInversion.motivoRechazo;
          this.cargarControles();
        }
        this.spinner.hide();
      },
        error => {
          this.spinner.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }

  public async procesarOperacion() {
    if (this.validarControlesEntrada()) {
      if (this.titulo !== 'Nueva') {
        await swal({
          allowEscapeKey: false,
          allowOutsideClick: false,
          showCancelButton: true,
          confirmButtonText: `Confirmar`,
          cancelButtonText: `Cancelar`,
          type: 'warning',
          title: `Tranferencia de inversión`,
          text: `¿Está seguro que desea transferir la inversión
          desde el operador ${_.startCase(_.toLower(this.forma.controls.operador.value))}
          hacia el operador ${_.startCase(_.toLower(this.NombreOperadorReceptor.ope_snombre))}?`
        }, () => {
          setTimeout(() => {
            this.persistirInversion();
            swal('Tranferencia de inversión', `Se ha realizado la operación correctamente`, 'success');
          }, 250);
        });
      } else {
        this.persistirInversion();
      }
    }
  }

  listarOperadoresReceptores() {
    this.operadoresReceptores = this.operadores.filter(operador => operador.ope_snombre !== this.forma.controls.operador.value);
  }

  validarControlesEntrada(): boolean {
    let _validarControlesEntrada = true;
    if (this.titulo !== 'Nueva') {
      if (this.forma.controls.operadorReceptor.value === '' ||
        this.forma.controls.operadorReceptor.value === undefined) {
        _validarControlesEntrada = false;
        swal('Atención', 'Debe seleccionar un operador receptor de la transferencia de inversión', 'error');
      }
    } else {
      if (this.forma.controls.fechaHasta.value === '' ||
        this.forma.controls.fechaHasta.value === undefined) {
        _validarControlesEntrada = false;
        swal('Atención', 'Debe ingresar el termino de la vigencia', 'error');
      }

      if (this.forma.controls.fechaDesde.value === '' ||
        this.forma.controls.fechaDesde.value === undefined) {
        _validarControlesEntrada = false;
        swal('Atención', 'Debe ingresar el inicio de la vigencia', 'error');
      }

      if (!this.forma.controls.montoInversion.valid) {
        swal('Atención', 'Debe ingresar el monto de la inversion', 'warning');
        _validarControlesEntrada = false;
      }

      if (!this.forma.controls.categoria.valid) {
        swal('Atención', 'Debe seleccionar la categoría', 'warning');
        _validarControlesEntrada = false;
      }

      if (!this.forma.controls.tipoInversion.valid) {
        swal('Atención', 'Debe seleccionar el tipo de inversión', 'warning');
        _validarControlesEntrada = false;
      }

      if (!this.forma.controls.operador.valid) {
        swal('Atención', 'Debe seleccionar el operador', 'warning');
        _validarControlesEntrada = false;
      }

      if (!this.forma.controls.descripcionInversion.valid) {
        swal('Atención', 'Debe ingresar la descripción de inversión', 'warning');
        _validarControlesEntrada = false;
      }

    }

    return _validarControlesEntrada;
  }

  private inicializarFechas() {
    this.fechaDesde = moment().toDate();
    this.fechaHasta = moment().endOf('month').toDate();
    this.forma.controls.fechaDesde.setValue(this.utilService.dateToStringFormat(this.fechaDesde));
    this.forma.controls.fechaHasta.setValue(this.utilService.dateToStringFormat(this.fechaHasta));
    this.forma.controls.fechaCreacion.setValue(this.utilService.dateToStringFormat(this.fechaDesde));
  }

  persistirInversion() {
    if (this.titulo === 'Nueva') {
      this.spinner.show();
      this.request = new Object();
      this.request = {
        nMontoIni: Number(this.forma.controls.montoInversion.value),
        nRunUsuarioCrea: Number(this.user.nrunusuario),
        nRutOperador: Number(this.forma.controls.operador.value),
        nTipoInversion: Number(this.forma.controls.tipoInversion.value),
        sCodCategoria: this.forma.controls.categoria.value,
        sCodEstado: 'VIGEN',
        sFhoFechaDesde: moment(this.forma.controls.fechaDesde.value).format('YYYYMMDDHHMMSS'),
        sFhoFechaHasta: moment(this.forma.controls.fechaHasta.value).format('YYYYMMDDHHMMSS')
      };

      console.log('request creacion inversion => : ', JSON.stringify(this.request));
      this.gestionAccionComercialService.crearInversion(this.request)
        .subscribe((inversion: any) => {
          this.spinner.hide();
          if (inversion) {
            if (inversion.error.codError !== 0) {
              swal('Atención', `Ha ocurrido un error inesperado : ${inversion.error.msjError}`, 'error');
            } else {
              swal('Atención', `Se ha creado la inversión N° ${inversion.idInversion} correctamente`, 'success');
            }
          }
        },
          error => {
            this.spinner.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', error);
          });
    } else {
      let _rutOperadorEmisor = 0;
      _rutOperadorEmisor = this.operadores.find(operador => operador.ope_snombre === this.forma.controls.operador.value).ofv_nrutoperador;
      this.spinner.show();
      this.request = new Object();
      this.request = {
        nMontoTransf: Number(this.forma.controls.montoInversion.value),
        nRunUsuarioCrea: Number(this.user.nrunusuario),
        nTipoInversion: Number(this.inversion.idtipoinversion),
        retiroTranferenciaOrig: {
          nIdInversion: Number(this.inversion.idInversion),
          nRutOperador: Number(_rutOperadorEmisor)
        },
        transferenciaDestino: {
          nIdCategoria: this.inversion.jca_scodcategoria,
          nIdInversion: Number(this.inversion.idInversion),
          nRutOperadors: Number(this.NombreOperadorReceptor.ofv_nrutoperador)
        }
      };
      console.log('request creacion crear Transferencia Inversion => : ', JSON.stringify(this.request));
      this.gestionAccionComercialService.crearTransferenciaInversion(this.request)
        .subscribe((transferencia: any) => {
          this.spinner.hide();
          if (transferencia) {
            if (transferencia.error.codError !== 0) {
              swal('Atención', `Ha ocurrido el siguiente error en crearTransferenciaInversion(): ${transferencia.error.msjError}`, 'error');
            } else {
              swal('Atención', `Se ha creado la transferencia N° ${transferencia.idtransferencia} correctamente`, 'success');
            }
          }
        },
          error => {
            this.spinner.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', error);
          });
    }
  }

  ngOnDestroy() {

  }
}
