import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, NgForm } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ClientesService } from '../../../core/consultaService/clientes.service';

const swal = require('sweetalert');

@Component({
  selector: 'app-girosPopUp',
  templateUrl: './girosPopUp.component.html',
  styleUrls: ['./girosPopUp.component.scss']
})
export class GirosPopUpComponent implements OnInit {
  forma: FormGroup;
  request: any = {};
  codGiros: string;
  titulo: string;
  origen: string;
  desGiros: string;
  giros: Array <any> = [];
  datos: Array <any> = [];

  constructor(fb: FormBuilder, private clientesService: ClientesService,
    private setearDatos: ConsultasService, public girosModal: BsModalRef,
    private modalService: BsModalService, public loader: NgxSpinnerService) {

    this.forma = new FormGroup ({
      codGiro: new FormControl(''),
      descripcionGiro: new FormControl(''),
    });
   }

  ngOnInit() {
  }

  buscar($event: any) {

    if (!this.forma.invalid) {

        this.loader.show();
        if ((this.forma.controls.codGiro.value !== 0) && (this.forma.controls.codGiro.value !== '') ) {
          this.request.codGiros = this.forma.controls.codGiro.value;
        } else {
          this.request.codGiros = null;
        }
        if ((this.forma.controls.descripcionGiro.value !== 0) && (this.forma.controls.descripcionGiro.value !== '') ) {
          this.request.desGiros = this.forma.controls.descripcionGiro.value;
        } else {
          this.request.desGiros = null;
        }

        this.setearDatos.getGiros(this.request)
            .subscribe((data: any) => {
                this.loader.hide();
                if (data) {
                  if (data.giros === null || data.error.codError !== 0) {
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                  } else {
                    if (data.giros.length === 0 && data.error.codError === 0 ) {
                      swal('Atención', 'No se encontraron resultados', 'info');
                    } else {
                        (this.giros = data.giros);
                    }
                  }
                  /* if (data.error.codError !== 0) {
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                  } */
                } else {
                  swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                }
            },
            error => {
                this.loader.hide();
                swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        );
    }
}
enviarInfo(giro) {
  this.setearDatos.setDatos(giro);
  this.girosModal.hide();
}


}
