import { Component, OnInit } from "@angular/core";
import { BsModalRef } from "ngx-bootstrap/modal";
import { NgxSpinnerService } from "ngx-spinner";
import { ReportesService } from "../../../core/consultaService/reportes.service";


const swal = require("sweetalert");


@Component({
  selector: 'app-relaciones-comerciales-pop-up',
  templateUrl: './relacionesComercialesPopUp.component.html'
})
export class RelacionesComercialesPopUpComponent implements OnInit {
  origen: string;
  titulo: string;
  relacionesComerciales: Array<any> = [
    {
      "rel_scodrelacioncomercial": "",
      "rel_sdescripcionrelacioncomerc": "(Todas)"
    }
  ];

  constructor(
    private setearDatos: ReportesService,
    public relacionesComercialesModal: BsModalRef,
    public loader: NgxSpinnerService,
  ) {}

  ngOnInit() {

    this.setearDatos.getTodasRcomerciales().subscribe(
      (data: any) => {
        this.loader.hide();
        if (data.relacionComercial.length === 0 && data.error.codError === 0) {
          swal("Atención", "No se encontraron resultados", "info");
        } else {
          if (data.relacionComercial === null) {
            swal("Atención", "Ha ocurrido un error inesperado!", "error");
          } else {
            data.relacionComercial.forEach(e => {
              this.relacionesComerciales.push(e);
            });
            this.relacionesComerciales.push(data.relacionComercial);
          }
        }
        if (data.error.codError !== 0) {
          swal("Atención", "Ha ocurrido un error inesperado!", "error");
        }
      },
      error => {
        this.loader.hide();
        swal("Atención", "Ha ocurrido un error inesperado!", "error");
      }
    );

  }


  enviarInfo(relacionComercial) {
    this.setearDatos.setDatos(relacionComercial);
    this.relacionesComercialesModal.hide();
  }
}
