import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';

import { ConsultasService } from '../../../core/consultaService/consultas.service';


@Component({
  selector: 'app-facturas-pop-up',
  templateUrl: './facturasPopUp.component.html',
  styleUrls: ['./facturasPopUp.component.scss']
})
export class FacturasPopUpComponent implements OnInit {

  constructor(public rutasPedidosModal: BsModalRef,
    private setearDatos:ConsultasService) { }

  ngOnInit() {

  }

  doHide() {
    this.rutasPedidosModal.hide();
  }

  enviarInfo(dato){
    this.setearDatos.setDatos(dato);
    this.rutasPedidosModal.hide();
  }
}
