import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { CajaCobranzaService } from '../../../core/consultaService/cajaCobranza.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { DecimalPipe } from '@angular/common';

@Component({
  selector: 'app-detalle-protesto-pop-up',
  templateUrl: './detalleProtestoPopUp.component.html',
  styleUrls: ['./detalleProtestoPopUp.component.scss']
})
export class DetalleProtestoPopUpComponent implements OnInit {

  origen: string;
  param: string;
  titulo: string;
  forma: FormGroup;
  movimiento: any;
  user: any;
  cargando = false;
  hidden = true;
  protesto: any;
  cliente: any;

  constructor(public bsModalRef: BsModalRef,
    private authService: AuthService,
    private decimalPipe: DecimalPipe,
    private cajaCobranzaService: CajaCobranzaService,
  ) {

    this.forma = new FormGroup({
      numDocumento: new FormControl({ value: '', disabled: true }),
      numCtaCte: new FormControl({ value: '', disabled: true }),
      montoTotal: new FormControl({ value: '', disabled: true }),
      banco: new FormControl({ value: '', disabled: true }),
      estadoDoc: new FormControl({ value: '', disabled: true }),
      monto: new FormControl({ value: '', disabled: true }),
    });
  }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
    this.buscaProtestos();
  }

  buscaProtestos() {
    this.cargando = true;

    const request = {
      nIdProtesto: Number(this.movimiento.smc_niddocto),
      nNumProtesto: Number(this.movimiento.nnumdocto),
      nRutOperador: Number(this.user.nrutoperador),
    };

    this.cajaCobranzaService.buscaDatosProtestos(request)
      .subscribe((data: any) => {
        this.cargando = false;
        if (data) {

          if (data.garantiaProtestada.length > 0 && data.error.codError === 0) {
            const protesto = data.garantiaProtestada.find(e => e.gar_niddocto === this.movimiento.nnumdocto);
            if (protesto) {
              this.protesto = protesto;
              this.forma.controls.numDocumento.setValue(this.protesto.gar_niddocto);
              this.forma.controls.numCtaCte.setValue(this.protesto.gar_snumcuenta);
              this.forma.controls.banco.setValue(this.protesto.sdesbanco.trim() === '' ?
                'Sin información' : `Banco ${this.protesto.sdesbanco}`
              );
              this.forma.controls.estadoDoc.setValue(this.protesto.sdesestado);
              this.forma.controls.monto.setValue('$ ' + this.decimalPipe.transform(this.protesto.gar_nmonto, '', 'es'));
            }
          }

        }
      },
        error => {
          this.cargando = false;
        });
  }

}
