import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { ConsultasService } from './../../../core/consultaService/consultas.service';

@Component({
  selector: 'app-motivos-pop-up',
  templateUrl: './motivosPopUp.component.html',
  styleUrls: ['./motivosPopUp.component.scss']
})
export class MotivosPopUpComponent implements OnInit {

  constructor( public bsModalRef: BsModalRef,
    private consultasService:ConsultasService
    ) { }

  ngOnInit() {
  
  }

  enviarInfo(dato){
    this.consultasService.setDatos(dato);
    this.bsModalRef.hide();
  }
}
