import { Component, OnInit, OnDestroy } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

import * as _ from 'lodash';
import { NgxSpinnerService } from 'ngx-spinner';

import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { AuthService } from '../../../core/consultaService/auth.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { ArticuloService } from '../../../core/consultaService/articulo.service';
import { BuscarArticuloPopUpComponent } from '../buscarArticuloPopUp/buscarArticuloPopUp.component';

const swal = require('sweetalert');

@Component({
  selector: 'app-agrupacion-pop-up',
  templateUrl: './agrupacionPopUp.component.html',
  styleUrls: ['./agrupacionPopUp.component.scss']
})
export class AgrupacionPopUpComponent implements OnInit, OnDestroy {

  forma: FormGroup;

  agrupacion: any = {};
  titulo = '';
  origen = '';

  secondModal: BsModalRef;

  estado: Array<any> = [
    {
      codEstado: 'VIGEN',
      descEstado: 'Vigente' 
    },
    {
      codEstado: 'NOVIG',
      descEstado: 'No Vigente' 
    }
  ];

  request: any = {};

  subData: any;
  datos: Array <any> = [];
  categoriaValida: boolean;

  constructor(public bsModalRef: BsModalRef, private authService:AuthService, private consultasService:ConsultasService,
    private articuloService:ArticuloService, public cargador: NgxSpinnerService, private modalService: BsModalService)
    {
      this.forma = new FormGroup ({
        codigoCategoria: new FormControl('', Validators.required),
        descCategoria: new FormControl({value:'', disabled:true }, Validators.required),
        codigoAgrupacion: new FormControl('', Validators.required),
        descripcionAgrupacion: new FormControl('', Validators.required),
        estadoAgrupacion: new FormControl('', Validators.required),
      });

    }

  ngOnInit() {
    this.subData = this.consultasService.datos$.subscribe(resData =>{
      this.setData(resData);
    })

    if(this.titulo === 'Editar'){
      this.forma.controls.codigoAgrupacion.disable();
    }

    this.forma.controls.codigoCategoria.setValue(this.agrupacion.jca_scodcategoria);
    this.forma.controls.descCategoria.setValue(this.agrupacion.jca_sdescategoria);
    this.forma.controls.codigoAgrupacion.setValue(this.agrupacion.jag_scodagrupacion);
    this.forma.controls.descripcionAgrupacion.setValue(this.agrupacion.jag_sdesagrupacion);
    this.forma.controls.estadoAgrupacion.setValue(this.agrupacion.jag_scodestado);
  }

  ngOnDestroy() {
    if(this.subData != undefined) {
      this.subData.unsubscribe();
    }
  }

  openModalCategorias(){
    const initialState = {
      titulo: 'Categorías',
      origen: 'categoria'
    };
    this.secondModal = this.modalService.show(BuscarArticuloPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  validaCategoria(){
    this.categoriaValida = false;
    if(this.forma.controls.codigoCategoria.value === ''){
      this.forma.controls.codigoCategoria.setValue('');
      this.forma.controls.descCategoria.setValue('');
    }else{
      this.request.scodestado = 'VIGEN';
      this.request.scodcategoria = this.forma.controls.codigoCategoria.value;
      this.cargador.show();
      this.articuloService.getCategorias(this.request)
      .subscribe((data:any)=>{
        this.cargador.hide();
        if(data){
          if(data.error.codError != 0){
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }else{
              this.datos = data.articulo;
              if(this.datos.length > 0){
                this.forma.controls.descCategoria.setValue(this.datos[0].jca_sdescategoria);
                this.categoriaValida = true;
              }else{
                this.forma.controls.codigoCategoria.setValue('');
                this.forma.controls.descCategoria.setValue('');
                swal('Atención', 'No Existe Categoría', 'warning');
              }
            }
        }
      },
      error =>{
        this.cargador.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
    }
  }

  setData(resData){
    this.forma.controls.codigoCategoria.setValue(resData.jca_scodcategoria);
    this.forma.controls.descCategoria.setValue(resData.jca_sdescategoria);
    this.categoriaValida = true;
  }

  guardarAgrupacion(){
    if(this.forma.valid && this.categoriaValida){

      this.request.nrunusuario = this.authService.getDatosUser().nrunusuario;
      this.request.scodagrupacion = this.forma.controls.codigoAgrupacion.value.toUpperCase();
      this.request.scodcategoria = this.forma.controls.codigoCategoria.value.toUpperCase();
      this.request.scodestado = this.forma.controls.estadoAgrupacion.value;
      this.request.sdesagrupacion =  this.forma.controls.descripcionAgrupacion.value;
      if(this.titulo === 'Agregar'){
        this.request.sindnuevo = 'S';
      }
      if(this.titulo === 'Editar'){
        this.request.sindnuevo = 'N';
      }

      this.cargador.show();
      this.articuloService.creaAgrupacion(this.request)
        .subscribe((data:any)=>{
          this.cargador.hide();

          if(data.error.codError != 0){
            swal('Atención', data.error.msjError, 'error');
          } else{
            this.consultasService.setDatos('guardar');
            swal('Atención','Agrupación guardada correctamente', 'success');
            this.bsModalRef.hide();
          }
          
        },
        error =>{
          this.cargador.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        })
    } 
  }

}
