import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, AbstractControl } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { NgxSpinnerService } from 'ngx-spinner';
import { UtilService } from './../../../services/util.service';
import { ClientesService } from "./../../../core/consultaService/clientes.service";
import { AdministracionOperativaService } from "./../../../core/consultaService/administracionOperativa.service";
import { DocumentosService } from "./../../../core/consultaService/documentos.service";
import { AuthService } from '../../../core/consultaService/auth.service';
import { SucursalesPopUpComponent } from "./../../../routes/commonPopUp/sucursalesPopUp/sucursalesPopUp.component";
import { DatePipe } from '@angular/common';
import { ConsultasService } from "./../../../core/consultaService/consultas.service";
const swal = require('sweetalert');

@Component({
  selector: 'app-crea-folios-autorizados',
  templateUrl: './creaFoliosAutorizados.component.html',
  styleUrls: ['./creaFoliosAutorizados.component.scss']
})
export class CreaFoliosAutorizadosComponent implements OnInit {

  fechaActual = new Date();
  forma: FormGroup;
  tipoDocumentos: Array<any> = [];
  request: any = {};
  requestDos: any = {};
  tiposfacturas: Array<any> = [];
  user: any = {};
  documentosTimbrados: Array<any> = [];
  sucursales: Array<any> = [];
  actionBtns: boolean = true;
  origen: any;
  subData: any;
  self: this;

  constructor(
    private modalService: BsModalService,
    public loader: NgxSpinnerService,
    private utilService: UtilService,
    private clientesService: ClientesService,
    private administracionOperativaService: AdministracionOperativaService,
    private authService: AuthService,
    private datePipe: DatePipe,
    private modal: BsModalRef,
    private setearDatos: AdministracionOperativaService,
    private consultasService: ConsultasService,
    private documentosService: DocumentosService,


  ) {
    this.forma = new FormGroup({
      folioDesde: new FormControl(''),
      folioHasta: new FormControl(''),
      codigoSucursal: new FormControl(''),
      nombreSucursal: new FormControl(''),
      tipoFactura: new FormControl(''),
      radioIpunt: new FormControl(),
    });
  }

  ngOnInit() {
    this.subData = this.consultasService.datos$.subscribe(resData => {
      this.setData(resData);
    })
    // CreaSolicitudFolioPopUpComponent
    this.user = this.authService.getDatosUser();
    this.request.parcodparametro01 = "";
    this.request.parcodparametro02 = "";
    this.request.parcodparametro03 = "";
    this.request.parcodparametro04 = "";
    this.request.parcodparametro05 = "";
    this.request.parnumparametro = 21;
    console.log("TCL: ConsultaFolioComponent -> ngOnInit -> this.request", this.request)

    this.documentosService.buscaParametros(this.request).subscribe((res) => {
      this.tiposfacturas = res['general']
    })
  }

  ngOnDestroy() {
    if (this.subData != undefined) {
      this.subData.unsubscribe();
    }
  }

  buscaSucursales() {
    this.requestDos.nRutOperador = Number(this.user.nrutoperador);
    this.requestDos.sDescSucursal = this.forma.controls.nombreSucursal.value;
    this.requestDos.sCodSucursal = this.forma.controls.codigoSucursal.value;
    this.requestDos.sCodEstado = "VIGEN";
    this.administracionOperativaService.getSucursales(this.requestDos).subscribe((res) => {


      let initialState = {
        sucursalesArray: res['sucursalExistencia'],
        origen: 'sucursales'
      }
      this.modal = this.modalService.show(SucursalesPopUpComponent, {initialState, backdrop: 'static', keyboard: false});

    })
  }

  guardaDocumentoTimbrado() {

    if (!this.forma.controls.folioDesde.value || !this.forma.controls.folioHasta.value) {
      swal("Por favor, Seleccione N° Folios", "", "warning")
    }

    else if(!this.forma.controls.tipoFactura.value){
      swal("Por favor, Seleccione un tipo de documento", "", "warning")
    }

    else if(!this.forma.controls.codigoSucursal.value){
      swal("Por favor, Seleccione una sucursal", "", "warning")
    }

    else if(!this.forma.controls.radioIpunt.value){
      swal("¿Electronico o no?", "", "warning")
    }

    else{
      this.request.niddoctotimbrado = 0
      this.request.nnumfoliodesde = this.forma.controls.folioDesde.value;
      this.request.nnumfoliohasta = this.forma.controls.folioHasta.value;
      this.request.nrutoperador = this.user.nrutoperador;
      this.request.nrutusuariocreador = 10380815;
      this.request.scodsucursal = this.forma.controls.codigoSucursal.value;
      this.request.scodtipodocumento = this.forma.controls.tipoFactura.value;
      this.request.sindelectronico = "E" // Qué servicio me trae este valor? o en de que input obtengo el valor y esta pantalla no tiene para seleccionar Eletronico o no... si es el de la pantalla anterior pero aquella lista registros existentes y este metodo crea nuevos registros. El valor del radio input  checado en la pantalla padre de esta no deberia ser el valor que se obtenga aqui.
      console.log("TCL: CreaFoliosAutorizadosComponent -> guardaDocumentoTimbrado -> this.request", this.request)

      this.documentosService.guardaDocumentoTimbrado(this.request).subscribe((res) => {
        console.log("TCL: CreaFoliosAutorizadosComponent -> guardaDocumentoTimbrado -> res", res)
        if (res) {
          swal('Perfecto!', 'Se ha generado un nuevo folio!', 'success');
        }
        this.modal.hide()
      })
    }

  }

  setData(resData) {
    console.log(resData)
    if (resData != 0) {
      this.forma.controls.codigoSucursal.setValue(resData.suc_scodsucursal);
      this.forma.controls.nombreSucursal.setValue(resData.suc_sdescsucursal);
    }
  }

  closeModal() {
    this.modal.hide();
  }

}

