import { Component, OnInit, OnDestroy } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

import * as _ from 'lodash';
import { NgxSpinnerService } from 'ngx-spinner';

import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { AuthService } from '../../../core/consultaService/auth.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { ArticuloService } from '../../../core/consultaService/articulo.service';
import { CategoriaNegocioPopUpComponent } from '../categoriaNegocioPopUp/categoriaNegocioPopUp.component';

const swal = require('sweetalert');

@Component({
  selector: 'app-linea-pop-up',
  templateUrl: './lineaPopUp.component.html',
  styleUrls: ['./lineaPopUp.component.scss']
})
export class LineaPopUpComponent implements OnInit, OnDestroy {

  forma: FormGroup;

  linea: any = {};
  titulo = '';
  origen = '';

  secondModal: BsModalRef;

  estado: Array<any> = [
    {
      codEstado: 'VIGEN',
      descEstado: 'Vigente' 
    },
    {
      codEstado: 'NOVIG',
      descEstado: 'No Vigente' 
    }
  ];

  request: any = {};

  subData: any;
  datos: Array <any> = [];
  paisValido: boolean;

  constructor(public bsModalRef: BsModalRef, private authService:AuthService, private consultasService:ConsultasService,
    private articuloService:ArticuloService, public cargador: NgxSpinnerService, private modalService: BsModalService) {
    
    this.forma = new FormGroup ({
      codigoPais: new FormControl('', Validators.required),
      descPais: new FormControl({value:'', disabled:true }, Validators.required),
      codigoLinea: new FormControl('', Validators.required),
      descripcionLinea: new FormControl('', Validators.required),
      estadoLinea: new FormControl('', Validators.required),
    });
  }

  ngOnInit() {

    this.subData = this.consultasService.datos$.subscribe(resData =>{
      this.setData(resData);
    })

    if(this.titulo === 'Editar'){
      this.forma.controls.codigoLinea.disable();
    }

    this.forma.controls.codigoPais.setValue(this.linea.jpa_scodpais);
    this.forma.controls.descPais.setValue(this.linea.jpa_sdespais);
    this.forma.controls.codigoLinea.setValue(this.linea.jli_scodlinea);
    this.forma.controls.descripcionLinea.setValue(this.linea.jli_sdeslinea);
    this.forma.controls.estadoLinea.setValue(this.linea.jli_scodestado);
  }

  guardarLinea(){
    if(this.forma.valid && this.paisValido){

      this.request.nrunusuario = this.authService.getDatosUser().nrunusuario;
      this.request.scodestado = this.forma.controls.estadoLinea.value;
      this.request.scodlinea = this.forma.controls.codigoLinea.value.toUpperCase();
      this.request.scodpais = this.forma.controls.codigoPais.value.toUpperCase();
      this.request.sdeslinea =  this.forma.controls.descripcionLinea.value;
      if(this.titulo === 'Agregar'){
        this.request.sindnuevo = 'S';
      }
      if(this.titulo === 'Editar'){
        this.request.sindnuevo = 'N';
      }

      this.cargador.show();
      this.articuloService.creaLinea(this.request)
        .subscribe((data:any)=>{
          this.cargador.hide();

          if(data.error.codError != 0){
            swal('Atención', data.error.msjError, 'error');
          } else{
            this.consultasService.setDatos('guardar');
            swal('Atención','Línea guardada correctamente', 'success');
            this.bsModalRef.hide();
          }
          
        },
        error =>{
          this.cargador.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        })
    }
    
    
  }

  ngOnDestroy() {
    if(this.subData != undefined) {
      this.subData.unsubscribe();
    }
  }

  openModalPaises(){
    const initialState = {
      titulo: 'Países',
      origen: 'paises'
    };
    this.secondModal = this.modalService.show(CategoriaNegocioPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  validaPais(){
    this.paisValido = false;
    if(this.forma.controls.codigoPais.value === ''){
      this.forma.controls.codigoPais.setValue('');
      this.forma.controls.descPais.setValue('');
    }else{
      this.request.scodestado = "VIGEN";
      this.request.scodpais = this.forma.controls.codigoPais.value;
      this.cargador.show();
      this.articuloService.getPais(this.request)
        .subscribe((data: any) => {
          this.cargador.hide();
          if(data){
            if (data.error.codError != 0) {
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }else{
              this.datos = data.articulo;
              if(this.datos.length > 0){
                this.forma.controls.descPais.setValue(this.datos[0].jpa_sdespais);
                this.paisValido = true;
              }else{
                this.forma.controls.codigoPais.setValue('');
                this.forma.controls.descPais.setValue('');
                swal('Atención', 'No Existe País', 'warning');
              }
            }
          }
        },
        error => {
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
    }
  }

  setData(resData){
    this.forma.controls.codigoPais.setValue(resData.jpa_scodpais);
    this.forma.controls.descPais.setValue(resData.jpa_sdespais);
    this.paisValido = true;
  }

}
