import { Component, OnInit } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ClientesService } from '../../../core/consultaService/clientes.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
const swal = require('sweetalert');

@Component({
  selector: 'app-listaLocalesPopUp',
  templateUrl: './listaLocalesPopUp.component.html',
  styleUrls: ['./listaLocalesPopUp.component.scss']
})
export class ListaLocalesPopUpComponent implements OnInit {
  origen: string;
  parametro: any;
  parametrodos: any;
  titulo: string;
  cargando = false;
  datos: Array <any> = [];
  localesClient: any = {};
  rutcliente: any;
  rutoperador: any;
  locales: Array <any> = [];
  constructor(private setearDatos: ConsultasService, public listarLocalModal: BsModalRef,
    private modalService: BsModalService, private consultasCliente: ClientesService) {

     }

  ngOnInit() {
    this.cargando = true;

    switch (this.origen) {
      case 'localesPedido': {
        this.titulo =  'Locales';
        this.localesClient.rutCliente = Number(this.parametrodos);
        this.localesClient.rutOperador = Number(this.parametro);
        console.log(this.localesClient);
        this.consultasCliente.getLocales(this.localesClient)
            .subscribe((data: any) => {
              console.log(data);
              this.cargando = false;

                (this.datos = data.locales);
            },
            error => {
              this.listarLocalModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            });
      break;
      }
   }
  }
  enviarInfo(cliente) {
    console.log('envio');
    this.setearDatos.setDatos(cliente);
    this.listarLocalModal.hide();
  }

}
