import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, AbstractControl } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { NgxSpinnerService } from 'ngx-spinner';
import { UtilService } from './../../../services/util.service';
import { ClientesService } from "./../../../core/consultaService/clientes.service";
import { AdministracionOperativaService } from "./../../../core/consultaService/administracionOperativa.service";
import { DocumentosService } from "./../../../core/consultaService/documentos.service";
import { AuthService } from '../../../core/consultaService/auth.service';
import { SucursalesPopUpComponent } from "./../../../routes/commonPopUp/sucursalesPopUp/sucursalesPopUp.component";
import { DatePipe } from '@angular/common';
import { ConsultasService } from "./../../../core/consultaService/consultas.service";
const swal = require('sweetalert');

@Component({
  selector: 'app-crea-solicitud-folio-pop-up',
  templateUrl: './creaSolicitudFolioPopUp.component.html',
  styleUrls: ['./creaSolicitudFolioPopUp.component.scss']
})
export class CreaSolicitudFolioPopUpComponent implements OnInit {

  @ViewChild('canti') canti: ElementRef;

  fechaActual = new Date();
  forma: FormGroup;
  tipoDocumentos: Array<any> = [];
  request: any = {};
  requestDos: any = {};
  tiposfacturas: Array<any> = [];
  user: any = {};
  documentosTimbrados: Array<any> = [];
  sucursales: Array<any> = [];
  actionBtns: boolean = true;
  origen: any;
  subData: any;
  requestTres: any = {};

  constructor(
    private modalService: BsModalService,
    private bsModalRef: BsModalRef,
    public loader: NgxSpinnerService,
    private utilService: UtilService,
    private clientesService: ClientesService,
    private administracionOperativaService: AdministracionOperativaService,
    private authService: AuthService,
    private datePipe: DatePipe,
    private setearDatos: AdministracionOperativaService,
    private consultasService: ConsultasService,
    private documentosService: DocumentosService

  ) {
    this.forma = new FormGroup({
      folioDesde: new FormControl({ value: '', disabled: true }),
      folioHasta: new FormControl({ value: '', disabled: true }),
      tipoFactura: new FormControl(''),
      cantidad: new FormControl(),
    });
  }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
    this.request.parcodparametro01 = "";
    this.request.parcodparametro02 = "";
    this.request.parcodparametro03 = "";
    this.request.parcodparametro04 = "";
    this.request.parcodparametro05 = "";
    this.request.parnumparametro = 21;
    console.log("TCL: ConsultaFolioComponent -> ngOnInit -> this.request", this.request)

    this.documentosService.buscaParametros(this.request).subscribe((res) => {
      this.tiposfacturas = res['general']
      console.log("TCL: ConsultaFolioComponent -> ngOnInit ->  this.tiposfacturas ", this.tiposfacturas)
    })
  }
  closeModal() {
    this.bsModalRef.hide()
  }

  buscaFoliosNodisvet() {
    this.requestDos.nnumcantidad = this.forma.controls.cantidad.value;
    this.requestDos.scodtipodocumento = this.forma.controls.tipoFactura.value;
    this.requestDos.nrutoperador = Number(this.user.nrutoperador);

    if (!this.forma.controls.cantidad.value) {
      this.canti.nativeElement.focus()
      swal("Por favor, Seleccione una cantidad", "", "warning")
      this.canti.nativeElement.focus()
    }
    else {
      this.documentosService.buscaFoliosNodisvet(this.requestDos).subscribe((res) => {
        console.log("TCL: CreaSolicitudFolioPopUpComponent -> buscaFoliosNodisvet -> res", res);

        this.forma.controls.folioDesde.setValue((res['nnumfoliodesde']));
        this.forma.controls.folioHasta.setValue(res['nnumfoliohasta']);
      })
    } 
  }

  guardaFoliosNoDisvet() {

    if (!this.forma.controls.cantidad.value) {
      this.canti.nativeElement.focus()
      swal("Por favor, Seleccione una cantidad", "", "warning")
      this.canti.nativeElement.focus()
    }

    else if(!this.forma.controls.tipoFactura.value){
      swal("Por favor, Seleccione un tipo de documento", "", "warning")
    }
    else{
      this.requestTres.nnumcantidad = this.forma.controls.cantidad.value;
      this.requestTres.scodtipodocumento = this.forma.controls.tipoFactura.value;
      this.requestTres.nrutoperador = Number(this.user.nrutoperador);
      this.requestTres.nrunusuariocrea = this.authService.getDatosUser().nrunusuario;
  
      this.documentosService.guardaFoliosNoDisvet(this.requestTres).subscribe(res => {
        console.log("TCL: CreaSolicitudFolioPopUpComponent -> guardaFoliosNoDisvet -> res", res)
        swal("Se ha guardado correctamente el documento", " ", "success")
  
      });
    }



  }

}
