import { Component, OnInit, OnDestroy } from '@angular/core';
import { Location } from '@angular/common';
import { FormGroup, FormBuilder, Validators, FormControl, NgForm } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ClientesService } from '../../../core/consultaService/clientes.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { BuscaBancoPopUpComponent } from '../buscaBancoPopUp/buscaBancoPopUp.component';

const swal = require('sweetalert');
declare var jQuery: any;
declare var $: any;

@Component({
  selector: 'app-bancoPopUp',
  templateUrl: './bancoPopUp.component.html',
  styleUrls: ['./bancoPopUp.component.scss']
})
export class BancoPopUpComponent implements OnInit, OnDestroy {
  forma: FormGroup;
  creaBanco: any = {};
  localidad: string;
  buscaBancosModal: BsModalRef;
  ctaCliente: string;
  subData: any;
  origen: string;
  titulo: string;
  edit: string;
  banco: any;
  rutcliente = this.clientesService.selectedCliente.cli_nrutcliente;
  rutoperador = this.clientesService.selectedCliente.cli_nrutoperador;
  estado: Array<any> = [
    {
      cbc_scodestado: 'VIGEN',
      descEstado: 'Vigente'
    },
    {
      cbc_scodestado: 'NOVIG',
      descEstado: 'No Vigente'
    }
  ];
  user: any = {};

  constructor(fb: FormBuilder, private clientesService: ClientesService,
    private setearDatos: ConsultasService, public bancosModal: BsModalRef,
    private modalService: BsModalService, public loader: NgxSpinnerService,
    private location: Location, private authService: AuthService) {

      this.forma = new FormGroup ({
        ctaCliente: new FormControl('', Validators.required),
        nombreBanco: new FormControl({value: '', disabled: true }),
        codBanco: new FormControl('', Validators.required),
        estado: new FormControl('', Validators.required),
        localidad: new FormControl('')

      });
    }

  ngOnInit() {
    this.subData = this.setearDatos.datos$.subscribe(resData => {
      this.setTipo(resData);
    });
    this.user = this.authService.getDatosUser();
    if (this.banco === undefined ) {
      console.log('es nuevo');
      this.edit = 'N';
    } else {
    this.edit = 'S';
    this.titulo = ('Detalle Cuenta ' +  this.banco.cbc_snumcuentacte + '-' + this.banco.par_deslargo01);
    this.forma.controls.ctaCliente.setValue(this.banco.cbc_snumcuentacte);
    this.forma.controls.ctaCliente.disable();
    this.forma.controls.nombreBanco.setValue(this.banco.par_deslargo01);
    this.forma.controls.codBanco.setValue(this.banco.cbc_scodbanco);
    this.forma.controls.estado.setValue(this.banco.cbc_scodestado);
    this.forma.controls.localidad.setValue(this.banco.cbc_slocalidad);
  }
  }
  ngOnDestroy() {
    if (this.subData !== undefined) {
      this.subData.unsubscribe();
    }
  }

  openModalBuscaBancos() {
    this.origen = 'buscaBanco';
    const initialState = {
      titulo: 'Banco',
      origen: 'buscaBanco'
    };
    this.buscaBancosModal = this.modalService.show(BuscaBancoPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  guardarNuevoBanco ($event: any, succesBanco: string) {
    if (this.forma.valid) {

      this.loader.show();

      this.creaBanco.codBanco = this.forma.controls.codBanco.value;
      this.creaBanco.codEstado = this.forma.controls.estado.value;
      this.creaBanco.localidad = this.forma.controls.localidad.value;
      this.creaBanco.numCuentacte = this.forma.controls.ctaCliente.value;
      this.creaBanco.runUsuario = this.user.nrunusuario;
      this.creaBanco.rutCliente = this.rutcliente;
      this.creaBanco.rutOperador = this.rutoperador;

      this.setearDatos.saveBancos(this.creaBanco)
          .subscribe((data: any) => {
              if (data.error.codError === 0) {
                this.loader.hide();
                swal('Bien', 'Los datos han sido ingresados satisfactoriamente.', 'success' );
                this.setearDatos.setDatos(succesBanco);
                this.bancosModal.hide();
              } else {
                swal('Atención', data.error.msjError, 'error');
                this.loader.hide();
              }
          },
          error => {
            this.loader.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          });

    } else {
      swal('Atención', 'Existen campos requeridos no ingresados', 'error' );
      if (!this.forma.controls.ctaCliente.valid) {
        $('[name=ctaCliente]').addClass('error-form');
      } else {
        $('[name=ctaCliente]').removeClass('error-form');
      }

      if (!this.forma.controls.codBanco.valid) {
        $('[name=nombreBanco]').addClass('error-form');
      } else {
        $('[name=nombreBanco]').removeClass('error-form');
      }
      if (!this.forma.controls.estado.valid) {
        $('[name=estado]').addClass('error-form');
      } else {
        $('[name=estado]').removeClass('error-form');
      }
      this.loader.hide();
    }
  }
  setTipo(resData) {
    if (resData !== 0) {
      switch (this.origen) {
        case 'buscaBanco': {
          this.forma.controls.codBanco.setValue(resData.par_codparametro01);
          this.forma.controls.nombreBanco.setValue(resData.par_deslargo01);
          break;
        }
      }
    }
    $('body').removeClass('modal-open');
  }

}
