import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';

import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { UtilService } from '../../../services/util.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { AdministracionOperativaService } from '../../../core/consultaService/administracionOperativa.service';

const swal = require('sweetalert');

@Component({
  selector: 'app-transportista-pago-pop-up',
  templateUrl: './transportistaPagoPopUp.component.html',
  styleUrls: ['./transportistaPagoPopUp.component.scss']
})
export class TransportistaPagoPopUpComponent implements OnInit {

  forma: FormGroup;
  datos: Array <any> = [];
  rutValido:boolean;
  request: any = {};
  cargando = false;
  titulo: string;

  constructor(public bsModalRef: BsModalRef, private consultasService:ConsultasService, private utilService:UtilService, 
              private authService:AuthService, private administracionOperativaService:AdministracionOperativaService) {
    this.forma = new FormGroup ({
      rutTransportista: new FormControl('')
    });
  }

  ngOnInit() {
  }

  validaRut() {
    this.rutValido = false;

    if(!this.utilService.isValidRUT(this.forma.controls.rutTransportista.value)){
      this.forma.controls.rutTransportista.setValue('');
      swal('Atención', 'El RUT es incorrecto', 'error');
      // this.datosCliente = [];
    }else{
      this.rutValido =  true;
      console.log('rut valido');
    }
  }

  buscar(){
    if(this.rutValido || this.forma.controls.rutTransportista.value === ''){
      console.log('consumir servicio');
      this.request.rutTransportista = this.forma.controls.rutTransportista.value === '' ? 0 : parseInt(this.forma.controls.rutTransportista.value.substr(0, this.forma.controls.rutTransportista.value.length - 1));
      this.request.rutOperador = parseInt(this.authService.getDatosUser().nrutoperador);
      console.log('request', this.request);
      this.cargando = true;
      this.administracionOperativaService.getTransportista(this.request)
      .subscribe((data:any)=>{
        console.log(data);
          (this.datos = data.buscaTransportistaDTO);
          this.cargando = false;

          if(data.error.codError != 0){
            this.bsModalRef.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          } 
      },
      error =>{
        this.bsModalRef.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
    }
  }

  enviarInfo(dato){
    this.consultasService.setDatos(dato);
    this.bsModalRef.hide();
  }

}
