import { Component, OnInit, OnDestroy } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { ConsultasService } from './../../../core/consultaService/consultas.service';

@Component({
  selector: 'app-nota-credito-direcciones-pop-up',
  templateUrl: './notaCreditoDireccionesPopUp.component.html',
  styleUrls: ['./notaCreditoDireccionesPopUp.component.scss']
})
export class NotaCreditoDireccionesPopUpComponent implements OnInit {

  constructor(public rutasPedidosModal: BsModalRef,
    private setearDatos:ConsultasService) { }

  ngOnInit() { }
  
  doHide() {
    this.rutasPedidosModal.hide();
  }

  enviarInfo(dato){
    this.setearDatos.setDatos(dato);
    this.rutasPedidosModal.hide();
  }
}
