import { Component, OnInit } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ClientesService } from '../../../core/consultaService/clientes.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { AdministracionOperativaService } from '../../../core/consultaService/administracionOperativa.service';
import { AuthService } from '../../../core/consultaService/auth.service';
const swal = require('sweetalert');

@Component({
  selector: 'app-sucursalesPopUp',
  templateUrl: './sucursalesPopUp.component.html',
  styleUrls: ['./sucursalesPopUp.component.scss']
})
export class SucursalesPopUpComponent implements OnInit {
  origen: string;
  param: string;
  titulo: string;
  cargando = false;
  datos: Array <any> = [];
  direccionesClient: any = {};
  localesClient: any = {};
  direcciones: Array <any> = [];
  rutcliente: any;
  rutoperador: any;
  request: any = {};
  user: any = {};

  constructor(private setearDatos: AdministracionOperativaService, public sucursalesModal: BsModalRef,
    private modalService: BsModalService, private consultaDirecciones: ClientesService,
    private authService: AuthService,private consultasService:ConsultasService) {

      this.user = this.authService.getDatosUser();
      
      if (this.consultaDirecciones.selectedCliente !== undefined) {
        console.log(this.setearDatos.selectedRecorrido);
        this.rutoperador = this.setearDatos.selectedRecorrido.dre_nrutoperador;
      } else {
        this.rutoperador = this.user.nrutoperador;
      }

     }

  ngOnInit() {
    this.cargando = true;
    switch (this.origen) {
      case 'sucursales': {
        this.titulo =  'Sucursales';
        this.request.nRutOperador = this.rutoperador;
        this.request.sCodEstado = 'VIGEN';
        this.setearDatos.getSucursales(this.request)
        .subscribe((data: any) => {
          console.log("sucursalComponent", data);
            (this.datos = data.sucursalExistencia);
            this.cargando = false;
            if (data.error.codError !== 0) {
              this.sucursalesModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        },
        error => {
          this.sucursalesModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
    }
  }
  enviarInfo(dato) {
    console.log("envia datos", dato);
    this.setearDatos.setDatos(dato);
    this.consultasService.setDatos(dato);
    this.sucursalesModal.hide();
  }

}
