import { DocumentosService } from "./../../../core/consultaService/documentos.service";
import { Component, OnInit } from "@angular/core";
import { DatePipe } from "@angular/common";
import { BsModalService, BsModalRef } from "ngx-bootstrap/modal";
import { NgxSpinnerService } from "ngx-spinner";

import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl,
  NgForm
} from "@angular/forms";
import { ConsultasService } from "../../../core/consultaService/consultas.service";
import { CajaCobranzaService } from "./../../../core/consultaService/cajaCobranza.service";
import { UtilService } from "./../../../services/util.service";


@Component({
  selector: "app-detalle-factura-print-pop-up",
  templateUrl: "./detalleFacturaPrintPopUp.component.html",
  styleUrls: ["./detalleFacturaPrintPopUp.component.scss"]
})
export class DetalleFacturaPrintPopUpComponent implements OnInit {
  forma: FormGroup;
  facturaDetails: any;
  request: any = {};

  data2: any;
  data1: any;

  fechaEmision: any;
  fechaVencimiento: any;
  fechaPedido: any;

  fechaEmisionSubstringed: any;
  fechaEntregaSubstringed: any;
  fechaPedidoSubstringed: any;

  emisionAnio: any;
  emisionMes: any;
  emisionDia: any;

  entregaAnio: any;
  entregaMes: any;
  entregaDia: any;

  pedidoAnio: any
  pedidoMes: any
  pedidoDia: any

  constructor(
    private consultasService: ConsultasService,
    private cajaCobranzaService: CajaCobranzaService,
    private documentosService: DocumentosService,
    private datePipe: DatePipe,
    private modalService: BsModalService,
    private bsModalRef: BsModalRef,
    public loader: NgxSpinnerService,
    private utilService: UtilService

  ) {
    this.forma = new FormGroup({
      rutCliente: new FormControl(),
      nombreCliente: new FormControl(),
      nomDireccion: new FormControl(),
      nomDireccionEntrega: new FormControl(),
      codTerritorio: new FormControl(),
      nomTerritorio: new FormControl(),
      codRuta: new FormControl(),
      nomRuta: new FormControl(),
      condicionPago: new FormControl(),
      fechaPedido: new FormControl(),
      fechaEmision: new FormControl(),
      fechaEntrega: new FormControl(),
      factura: new FormControl()
    });
  }

  enviarInfo(dato) {
    this.consultasService.setDatos(dato);
  }

  ngOnInit() {
    this.request = this.documentosService.selectedFactura;

    if (this.request.nIdFactura) {
      this.cajaCobranzaService.cargaFactura(this.request).subscribe(data => {
        console.log("tranzeando data", data);


        this.data1 = data["detalleFactura"][0];
        this.data2 = data["factura"][0];
        console.log("this.data2", this.data2);

        //breaking date

        //fraccionando en año, mes, dia: FECHA EMISION:

        this.fechaEmision = this.data2.dfhoemision;

        this.emisionAnio = Number(this.fechaEmision.substring(0, 4));
        this.emisionMes = Number(this.fechaEmision.substring(4, 6)) - 1;
        this.emisionDia = Number(this.fechaEmision.substring(6, 8)) + 1;

        //fraccionando en año, mes, dia FECHA ENTREGA:

        this.fechaVencimiento = this.data2.dfhovencimiento;

        this.entregaAnio = Number(this.fechaVencimiento.substring(0, 4));
        this.entregaMes = Number(this.fechaVencimiento.substring(4, 6)) - 1;
        this.entregaDia = Number(this.fechaVencimiento.substring(6, 8)) + 1;

        //fraccionando en año, mes, dia FECHA PEDIDO:

        this.fechaPedido = this.data2.dfhopedido;

        this.pedidoAnio = Number(this.fechaPedido.substring(0, 4));
        this.pedidoMes = Number(this.fechaPedido.substring(4, 6)) - 1;
        this.pedidoDia = Number(this.fechaPedido.substring(6, 8)) + 1;

        //Substringing dates:

        this.fechaEmisionSubstringed = new Date(
          this.emisionAnio,
          this.emisionMes,
          this.emisionDia
        );
        console.log("this.fechaEmisionSubstringed", this.fechaEmisionSubstringed);

        this.fechaEntregaSubstringed = new Date(
          this.entregaAnio,
          this.entregaMes,
          this.entregaDia
        );

        this.fechaEmisionSubstringed = new Date(
          this.emisionAnio,
          this.emisionMes,
          this.emisionDia
        );

        console.log("this.fechaEntregaSubstringed", this.fechaEntregaSubstringed);

        this.fechaPedidoSubstringed = new Date(
          this.pedidoAnio,
          this.pedidoMes,
          this.pedidoDia
        );

        // fin de las operaciones de date string trimming

        //asginando valores al modal "Detalle Factura"

        this.forma.controls.rutCliente.setValue(
          `${this.data2.nrutcliente}${this.data2.sdigverificador}`
        );
        this.forma.controls.nombreCliente.setValue(this.data2.snombre);
        this.forma.controls.nomDireccion.setValue(this.data2.sdireccion);
        this.forma.controls.nomDireccionEntrega.setValue(this.data2.sdireccionentrega);
        this.forma.controls.codTerritorio.setValue(this.data2.scodterritorio);
        this.forma.controls.nomTerritorio.setValue(this.data2.sdesterritorio);
        this.forma.controls.codRuta.setValue(this.data2.scodruta);
        this.forma.controls.nomRuta.setValue(this.data2.sdesruta);
        this.forma.controls.condicionPago.setValue(this.data2.sdesformapago);

        this.forma.controls.fechaPedido.setValue(
          this.datePipe.transform(this.fechaPedidoSubstringed, "dd/MM/yyyy") // no substringed
        );

        this.forma.controls.fechaEmision.setValue(
          this.datePipe.transform(this.fechaEmisionSubstringed, "dd/MM/yyyy")
        );
        this.forma.controls.fechaEntrega.setValue(
          this.datePipe.transform(this.fechaEntregaSubstringed, "dd/MM/yyyy")
        );

        this.forma.controls.factura.setValue(this.data2.nnumfactura);

        this.forma.disable()
      });
    }
    else if (this.request.nIdNotaCredito) {
      this.cajaCobranzaService.cargaNotaCredito(this.request).subscribe(res => {

        

        this.data1 = res["detalleNotaCredito"][0];
        this.data2 = res["notaCredito"][0];

        this.forma.controls.rutCliente.setValue(
          `${this.data2.nrutcliente}${this.data2.sdigverificador}`
        );

        this.fechaEmision = this.utilService.dateStringServiceToStringFormat(this.data2.dfhoemision)
        this.fechaVencimiento = this.utilService.dateStringServiceToStringFormat(this.data2.dfhovencimiento)
        this.fechaPedido = this.utilService.dateStringServiceToStringFormat(this.data2.dfhopedido)

        this.forma.controls.nombreCliente.setValue(this.data2.snombre);
        this.forma.controls.nomDireccion.setValue(this.data2.sdireccion);
        this.forma.controls.nomDireccionEntrega.setValue(this.data2.sdireccionentrega);
        this.forma.controls.codTerritorio.setValue(this.data2.scodterritorio);
        this.forma.controls.nomTerritorio.setValue(this.data2.sdesterritorio);
        this.forma.controls.codRuta.setValue(this.data2.scodruta);
        this.forma.controls.nomRuta.setValue(this.data2.sdesruta);
        this.forma.controls.condicionPago.setValue(this.data2.sdesformapago);
        this.forma.controls.fechaEmision.setValue(this.fechaEmision);
        this.forma.controls.fechaEntrega.setValue(this.fechaVencimiento);
        this.forma.controls.fechaPedido.setValue(this.fechaPedido);

        this.forma.disable()

      })

    }


  }
  imprimir() {
    let printContents, popupWin;
    printContents = document.getElementById("print-section").innerHTML;
    popupWin = window.open("", "_blank", "top=0,left=0,height=100%,width=auto");
    popupWin.document.open();
    popupWin.document.write(`
      <html>
        <head>
          <title>Print tab</title>
          <style>
          </style>
        </head>
    <body onload="window.print();window.close()">${printContents}</body>
      </html>`);
    // popupWin.document.close();
  }
}
