import { Component, OnInit } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ClientesService } from '../../../core/consultaService/clientes.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
const swal = require('sweetalert');

@Component({
  selector: 'app-utilGeoPopUp',
  templateUrl: './utilGeoPopUp.component.html',
  styleUrls: ['./utilGeoPopUp.component.scss']
})
export class UtilGeoPopUpComponent implements OnInit {
  origen: string;
  param: string;
  titulo: string;
  cargando = false;
  datos: Array <any> = [];
  direccionesClient: any = {};
  localesClient: any = {};
  direcciones: Array <any> = [];
  rutcliente: any;
  rutoperador: any;
  regionSeleccionada: any;
  ciudadSeleccionada: any;

  request: any = {
      parcodparametro01: null,
      parcodparametro02: null,
      parcodparametro03: null,
      parcodparametro04: null,
      parcodparametro05: null,
      parnumparametro: 27
  };
  constructor(private setearDatos: ConsultasService, public utilGeoModal: BsModalRef,
    private modalService: BsModalService, private consultaDirecciones: ClientesService) {
      if (this.consultaDirecciones.selectedCliente !== undefined) {
        this.rutcliente = this.consultaDirecciones.selectedCliente.cli_nrutcliente;
        this.rutoperador = this.consultaDirecciones.selectedCliente.cli_nrutoperador;
      }
     }

  ngOnInit() {
    this.cargando = true;

    switch (this.origen) {
      case 'region': {
        this.titulo =  'Región';
        this.setearDatos.getRegiones(this.request)
        .subscribe((data: any) => {
            (this.datos = data.general);
            this.cargando = false;

            if (data.error.codError !== 0) {
              this.utilGeoModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        },
        error => {
          this.utilGeoModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
      case 'ciudad': {
        this.titulo =  'Ciudad';
        this.request.parnumparametro = 26;
        this.request.parcodparametro02 = this.regionSeleccionada;
        this.setearDatos.getRegiones(this.request)
        .subscribe((data: any) => {
            (this.datos = data.general);
            this.cargando = false;

            if (data.error.codError !== 0) {
              this.utilGeoModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        },
        error => {
          this.utilGeoModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
      case 'comuna': {
        this.titulo =  'Comúna';
        this.request.parnumparametro = 25;
        this.request.parcodparametro02 = this.ciudadSeleccionada;
        this.request.parcodparametro03 = this.regionSeleccionada;
        this.setearDatos.getRegiones(this.request)
        .subscribe((data: any) => {
            (this.datos = data.general);
            this.cargando = false;

            if (data.error.codError !== 0) {
              this.utilGeoModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        },
        error => {
          this.utilGeoModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
      case 'direccionBanco': {
        this.direccionesClient.nRutCliente = this.rutcliente;
        this.direccionesClient.nRutOperador = this.rutoperador;
        this.consultaDirecciones.getDireccionesCliente(this.direccionesClient)
            .subscribe((data: any) => {
                (this.datos = data.direccionCliente);
                this.cargando = false;

            if (data.error.codError !== 0) {
              this.utilGeoModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
          },
        error => {
          this.utilGeoModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
      case 'direRelaciones': {
        this.localesClient.rutCliente = this.rutcliente;
        this.localesClient.rutOperador = this.rutoperador;
        this.consultaDirecciones.getLocales(this.localesClient)
            .subscribe((data: any) => {
                (this.datos = data.locales);
                this.cargando = false;

            if (data.error.codError != 0) {
              this.utilGeoModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
          },
        error => {
          this.utilGeoModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
      case 'direVisitas': {
        this.direccionesClient.nRutCliente = this.rutcliente;
        this.direccionesClient.nRutOperador = this.rutoperador;
        this.consultaDirecciones.getDireccionesCliente(this.direccionesClient)
            .subscribe((data: any) => {
                (this.datos = data.direccionCliente);
                this.cargando = false;

            if (data.error.codError !== 0) {
              this.utilGeoModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
          },
        error => {
          this.utilGeoModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
      case 'direPagos': {
        this.direccionesClient.nRutCliente = this.rutcliente;
        this.direccionesClient.nRutOperador = this.rutoperador;
        this.consultaDirecciones.getDireccionesCliente(this.direccionesClient)
            .subscribe((data: any) => {
                (this.datos = data.direccionCliente);
                this.cargando = false;

            if (data.error.codError != 0) {
              this.utilGeoModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
          },
        error => {
          this.utilGeoModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
      case 'direEntregas': {
        this.direccionesClient.nRutCliente = this.rutcliente;
        this.direccionesClient.nRutOperador = this.rutoperador;
        this.consultaDirecciones.getDireccionesCliente(this.direccionesClient)
            .subscribe((data: any) => {
                (this.datos = data.direccionCliente);
                this.cargando = false;

            if (data.error.codError !== 0) {
              this.utilGeoModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
          },
        error => {
          this.utilGeoModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
   }
  }
  enviarInfo(dato) {
    this.setearDatos.setDatosGeo(dato);
    this.utilGeoModal.hide();
  }

}
