import { Component, OnInit } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { CajaCobranzaService } from '../../../core/consultaService/cajaCobranza.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { AuthService } from '../../../core/consultaService/auth.service';
const swal = require('sweetalert');

@Component({
  selector: 'app-tiposPagoReversaPopUp',
  templateUrl: './tiposPagoReversaPopUp.component.html',
  styleUrls: ['./tiposPagoReversaPopUp.component.scss']
})
export class TiposPagoReversaPopUpComponent implements OnInit {

  titulo: string;
  cargando = false;
  datos: Array<any> = [];

  request: any = {};
  user: any = {};
  origen: string;
  param: string;

  constructor(private setearDatos: ConsultasService,
    public modal: BsModalRef,
    private authService: AuthService,
    private cajaCobranzaService: CajaCobranzaService) { }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
    this.cargando = true;

    this.titulo = 'Tipos Pago';
    this.request = {};

    this.cajaCobranzaService.buscaTiposPagoReversa(this.request)
      .subscribe((data: any) => {
        (this.datos = data.tipoPago);
        this.cargando = false;

        if (data.error.codError !== 0) {
          this.modal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        }
      },
        error => {
          this.modal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }

  enviarInfo(dato) {
    this.setearDatos.setDatos(dato);
    this.modal.hide();
  }

}
