import { UtilService } from './../../../services/util.service';
import { Component, OnInit } from "@angular/core";
import { BsModalRef } from "ngx-bootstrap/modal";
import { AdministracionOperativaService } from "../../../core/consultaService/administracionOperativa.service";
import { AuthService } from "../../../core/consultaService/auth.service";
import { FormGroup, FormControl } from "@angular/forms";
import { NgxSpinnerService } from "ngx-spinner";
import { PedidosService } from '../../../core/consultaService/pedidos.service';

const swal = require("sweetalert");

@Component({
  selector: "app-transportistasPopUp",
  templateUrl: "./transportistasPopUp.component.html",
  styleUrls: ["./transportistasPopUp.component.scss"]
})
export class TransportistasPopUpComponent implements OnInit {
  origen: string;
  param: string;
  titulo: string;
  dia: string;
  cargando = false;
  datos: Array<any> = [];
  direccionesClient: any = {};
  localesClient: any = {};
  direcciones: Array<any> = [];
  rutcliente: any;
  rutoperador: any;
  request: any = {};
  user: any = {};
  busquedaCompleta: boolean = false;
  forma: FormGroup;
  transportistas: Array<any> = [];
  rutTransportista: any = 0;
  vehiculos: Array<any> = [];
  recorrido: any;
  choferPedidos= false;

  constructor(
    private setearDatos: AdministracionOperativaService,
    public transportistasModal: BsModalRef,
    public loader: NgxSpinnerService,
    private authService: AuthService,
		private utilService: UtilService,
    private pedidosService: PedidosService
  ) {
    this.forma = new FormGroup({
      codEstadoTrans: new FormControl(""),
      razonSocialTrans: new FormControl(""),
      rutOperador: new FormControl(""),
      rutTransportista: new FormControl("")
    });
  }

  ngOnInit() {
    this.cargando = true;
    this.user = this.authService.getDatosUser();
    this.choferPedidos = false;

    switch (this.origen) {
      case 'chofer': {
        this.titulo =  'Transportistas';
        this.request.nRutOperador = Number(this.user.nrutoperador);
        this.request.sDia = this.dia;
        this.setearDatos.getChoferesDisponiblesRecorrido(this.request)
          .subscribe((data: any) => {
              if (data.error.codError !== 0) {
                this.transportistasModal.hide();
                swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
              } else {
                (this.datos = data.camiones);
                this.cargando = false;
              }
          },
          error => {
            this.transportistasModal.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          });
        break;
      }

      case "transportistas": {
        this.titulo = "Transportistas";
        this.request = {};
        break;
      }

      case "vehiculo": {
        this.titulo = "Vehiculo";
        this.loader.show();
        this.request = {};
        this.request.rutOperador = this.user.nrutoperador;
        this.request.rutTransportista = this.rutTransportista;
        this.setearDatos.getVehiculos(this.request).subscribe(
          (data: any) => {
            this.loader.hide();
            if (data.error.codError !== 0) {
              this.transportistasModal.hide();
              swal("Atención", "Ha ocurrido un error inesperado!", "error");
            } else {
              this.vehiculos = data.vehiculos;
            }
          },
          error => {
            this.transportistasModal.hide();
            swal("Atención", "Ha ocurrido un error inesperado!", "error");
          }
        );
        break;
      }
      case "choferPedido": {
        this.titulo = "Transportistas";
        this.choferPedidos = true;
        this.request.idRecorrido = this.recorrido;
        this.request.rutOperador = Number(this.user.nrutoperador);
        this.pedidosService.getChoferRecorrido(this.request)
            .subscribe((data: any) => {
                if (data.error.codError !== 0) {
                  this.transportistasModal.hide();
                  swal('Atención', data.error.msjError, 'error');
                } else {
                  (this.datos = data.buscaCamionesLibres);
                  this.cargando = false;
                }
            },
            error => {
              this.transportistasModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            });
        break;
      }
    }
  }

  buscar($event: any) {
    this.loader.show();

    this.forma.controls.rutOperador.setValue(Number(this.user.nrutoperador));
    this.request = this.forma.value;
    if (this.forma.controls.rutTransportista.value) {
      this.request.rutTransportista = this.utilService.rutStringFormatToRequest(this.forma.controls.rutTransportista.value);
    } else {
      this.request.rutTransportista = 0;
    }

    this.setearDatos.getTransportista(this.request).subscribe(
      (data: any) => {
        this.loader.hide();
        if (data.error.codError !== 0) {
          this.loader.hide();

          this.transportistasModal.hide();
          swal("Atención", "Ha ocurrido un error inesperado!", "error");
        } else {
          this.transportistas = data.buscaTransportistaDTO;
        }
      },
      error => {
        this.transportistasModal.hide();
        this.loader.hide();

        swal("Atención", "Ha ocurrido un error inesperado!", "error");
      }
    );
  }

  enviarInfo(dato) {
    this.setearDatos.setDatos(dato);
    this.transportistasModal.hide();
  }
  cambiarChofer(dato) {
    this.loader.show();
    this.request.idRecorrido = this.recorrido;
    this.request.patente = dato.cam_spatente;
    this.request.runChofer = dato.cho_nrunchofer;
    this.request.rutOperador = Number(this.user.nrutoperador);
    this.request.rutTransportista = dato.tra_nruttransportista;
    this.pedidosService.updateChoferRecorrido(this.request)
        .subscribe( (data: any) => {
          if (data) {
            if (data.error.codError === 0) {
              this.loader.hide();
              swal('Bien', 'Los datos han sido actualizados satisfactoriamente', 'success' );
              this.pedidosService.setDatos('refresh');
              this.transportistasModal.hide();
            } else {
              this.loader.hide();
              swal('Atención', data.error.msjError, 'error');
              this.transportistasModal.hide();
            }
          } else {
            this.loader.hide();
            swal('Atención', data.error.msjError, 'error');
            this.transportistasModal.hide();
          }
        },
        error => {
          this.loader.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          this.transportistasModal.hide();
        });

  }
}
