import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';

import { CajaCobranzaService } from '../../../core/consultaService/cajaCobranza.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';

import { NgxSpinnerService } from 'ngx-spinner';

const swal = require('sweetalert');

@Component({
  selector: 'app-confirmacionCierreCajaPopUp',
  templateUrl: './confirmacionCierreCajaPopUp.component.html',
  styleUrls: ['./confirmacionCierreCajaPopUp.component.scss']
})
export class ConfirmacionCierreCajaPopUpComponent implements OnInit {

  titulo: string;
  subtitulo: string;
  request: any = {};
  cargando = false;
  password: string;
  usuario: string;
  submitted = false;
  user: any = {};

  constructor(
    public bsModalRef: BsModalRef,
    private cajaCobranzaService: CajaCobranzaService,
    private authService: AuthService,
    public cargador: NgxSpinnerService,
    private setearDatos: ConsultasService,
  ) {

  }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
  }

  cancelar() {
    this.bsModalRef.hide();
  }

  submit() {
    this.submitted = true;

    this.cargador.show();

    const request = {
      nRunOperador: Number(this.user.nrutoperador),
      nRunUsuario: Number(this.user.nrunusuario),
      sContrasena: this.password,
      sNomUsuario: this.usuario,
    };

    this.cajaCobranzaService.cierreCaja(request)
      .subscribe((data: any) => {
        this.cargador.hide();
        this.bsModalRef.hide();
        if (data) {
          if (data.error.codError !== 0) {
            swal('Atención', data.error.msjError, 'error');
          } else {
            this.setearDatos.setDatos(data);
            swal('Bien', 'El cierre se ha realizado satisfactoriamente.', 'success');
          }
        } else {
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        }
      },
        error => {
          this.cargador.hide();
          this.bsModalRef.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });

  }

}
