import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { CajaCobranzaService } from '../../../core/consultaService/cajaCobranza.service';
import { UtilService } from './../../../services/util.service';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { RutAsociadoPopUpComponent } from '../../commonPopUp/rutAsociadoPopUp/rutAsociadoPopUp.component';
import { NgxSpinnerService } from 'ngx-spinner';
import { AuthService } from '../../../core/consultaService/auth.service';
import { ConfirmacionReversarPagoPopUpComponent } from '../../commonPopUp/confirmacionReversarPagoPopUp/confirmacionReversarPagoPopUp.component';
import { DatePipe } from '@angular/common';

const swal = require('sweetalert');

@Component({
  selector: 'app-detalle-reversa-pago-pop-up',
  templateUrl: './detalleReversaPagoPopUp.component.html',
  styleUrls: ['./detalleReversaPagoPopUp.component.scss']
})
export class DetalleReversaPagoPopUpComponent implements OnInit {

  titulo: string;
  parametros: any;

  forma: FormGroup;
  pageActual = 1;
  datos: Array<any> = [];
  origen: string;
  clienteModal: any;
  params: any;
  sub: any;
  jsonParams: any;
  user: any = {};
  checkTituloSeleccionado = false;
  regularizacion: Array<any> = [
    {
      codigo: '',
      descripcion: 'Sin seleccion'
    },
    {
      codigo: 'S',
      descripcion: 'Si'
    },
    {
      codigo: 'N',
      descripcion: 'No'
    },
  ];
  constructor(
    private modalService: BsModalService,
    private cajaCobranzaService: CajaCobranzaService,
    private utilService: UtilService,
    public cargador: NgxSpinnerService,
    private authService: AuthService,
    public bsModalRef: BsModalRef,
  ) {
    this.forma = new FormGroup({
      rutCliente: new FormControl({ value: '', disabled: true }),
      nombreCliente: new FormControl({ value: '', disabled: true }),
      fecha: new FormControl({ value: '', disabled: true }),
      tipoPagoCod: new FormControl({ value: '', disabled: true }),
      tipoPagoDesc: new FormControl({ value: '', disabled: true }),
      monto: new FormControl({ value: '', disabled: true }),
      sRegularizar: new FormControl(''),
    });
  }

  ngOnInit() {

    this.user = this.authService.getDatosUser();

    this.forma.setValue({
      rutCliente: this.parametros.dato.cte_nrutcliente + '' + this.parametros.dato.cli_sdigverificador,
      nombreCliente: this.parametros.dato.cli_snombre,
      fecha: this.convertirFecha(this.parametros.dato.fecha),
      tipoPagoCod: this.parametros.formulario.tipoPagoCod,
      tipoPagoDesc: this.parametros.formulario.tipoPagoDesc,
      monto: this.parametros.dato.cte_nabono,
      sRegularizar: '',
    });
    this.buscar(this.parametros.dato.cte_nidoperacion);

  }

  ngOnDestroy() { }

  validaRut() {
    if (this.utilService.isValidRUT(this.forma.controls.rutCliente.value)) {
      swal('Bien', 'Los datos han sido ingresados satisfactoriamente.', 'success');
    } else {
      swal('Atención', 'El RUT es incorrecto', 'error');
    }
  }

  openModalCliente() {
    this.origen = 'clientePedido';
    const initialState = {
      titulo: 'Cliente',
      origen: 'clientePedido'
    };
    this.clienteModal = this.modalService.show(RutAsociadoPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
  }

  buscar(nIdOperacion) {
    this.cargador.show();
    const request = {
      nIdoperacion: Number(nIdOperacion),
      nRutOperador: Number(this.user.nrutoperador),
    };

    this.cajaCobranzaService.buscaDocumentosAsociados(request)
      .subscribe((data: any) => {
        this.cargador.hide();
        if (data) {
          if (data.error.codError !== 0) {
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            return;
          }
          if (data.movimientos.length === 0 && data.error.codError === 0) {
            swal('Atención', 'No se encontraron resultados', 'info');
          } else {
            const datos = [];
            data.movimientos.forEach(element => {
              const item = Object.assign(element, { bCheck: false });
              datos.push(item);
            });
            this.datos = datos;
          }
        } else {
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        }
      },
        error => {
          this.cargador.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }

  botonReversaPago() {

    if (this.validarForm()) {
      return;
    }

    const checkedItem = [];
    let nmontoreversa = 0;
    this.datos.forEach(element => {
      if (element.bCheck) {
        checkedItem.push({ nIdOperacionCargo: element.cte_nidoperacion });
        nmontoreversa += element.total;
      }
    });
    if (checkedItem.length > 0) {
      const request = {
        aSRV_EntOperacionCargo: checkedItem,
        nIdOperacionAbono: this.parametros.dato.cte_nidoperacion,
        nMontoPago: this.parametros.dato.cte_nabono,
        nMontoReversa: nmontoreversa,
        nRunUsuario: Number(this.user.nrunusuario),
        nRutCliente: Number(this.parametros.dato.cte_nrutcliente),
        nRutOperador: Number(this.user.nrutoperador),
        sCodEstado: 'PENDI',
        sCodTipo: this.parametros.dato.cte_scodtipodocto,
        sIndReversarPago: this.forma.controls.sRegularizar.value,
      };

      const initialState = {
        titulo: 'Reversar Pago',
        subtitulo: 'Se procederá a efectuar la Reserva de los documentos seleccionados. ¿Desea Continuar?',
        request: request
      };

      this.modalService.show(ConfirmacionReversarPagoPopUpComponent, { initialState, backdrop: 'static', keyboard: false });

    } else {
      swal('Atención', 'Debe seleccionar por lo menos un documento para reservar', 'info');
    }
  }

  validarForm() {
    if (this.forma.controls.sRegularizar.value === '' || this.forma.controls.sRegularizar.value === undefined) {
      swal('Atención', 'Debe seleccionar si desea regularizar saldo pendientes', 'info');
      return true;
    }
    return false;
  }

  validaCheckTitulo() {
    this.checkTituloSeleccionado = !this.checkTituloSeleccionado;
    if (this.checkTituloSeleccionado) {
      // tslint:disable-next-line: forin
      for (const i in this.datos) {
        this.datos[i].bCheck = true;
      }
    } else {
      // tslint:disable-next-line: forin
      for (const i in this.datos) {
        this.datos[i].bCheck = false;
      }
    }
  }

  validaCheck(dato) {
    dato.bCheck = !dato.bCheck;
  }

  convertirFecha(fecha) {
    if (fecha) {
      const regexp = /(\d{4})(\d{2})(\d{2})$/;
      const tag = fecha.match(regexp);
      return tag[1] + '-' + tag[2] + '-' + tag[3];
    }
    return '';
  }

  convertirFechaEspecial(fecha) {
    if (fecha) {
      const regexp = /(\d{4})(\d{2})(\d{2})(\d{6})$/;
      const tag = fecha.match(regexp);
      return tag[3] + '/' + tag[2] + '/' + tag[1];
    }
    return '';
  }

  cancelar() {
    this.bsModalRef.hide();
  }

}
