import { Component, OnInit } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ClientesService } from '../../../core/consultaService/clientes.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { PedidosService } from '../../../core/consultaService/pedidos.service';
const swal = require('sweetalert');

@Component({
  selector: 'app-zonasTodasPopUp',
  templateUrl: './zonasTodasPopUp.component.html',
  styleUrls: ['./zonasTodasPopUp.component.scss']
})
export class ZonasTodasPopUpComponent implements OnInit {
  origen: string;
  param: string;
  titulo: string;
  cargando = false;
  datos: Array <any> = [];
  direccionesClient: any = {};
  localesClient: any = {};
  direcciones: Array <any> = [];
  request: any = {};
  user: any = {};
  parametro: any;
  parametroDos: string;

  constructor(private setearDatos: ConsultasService, public zonasTodasModal: BsModalRef,
    private modalService: BsModalService, private consultaDirecciones: ClientesService,
    private authService: AuthService, private pedidosService: PedidosService) { }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
    this.cargando = true;
    switch (this.origen) {
      case 'zonasTodas': {
        this.titulo =  'Zonas';
        this.request.rutOperador = this.parametro;
        this.request.codEstado = 'VIGEN';
        this.setearDatos.getZonasTodas(this.request)
        .subscribe((data: any) => {
            (this.datos = data.general);
            this.cargando = false;

            if (data.error.codError !== 0) {
              this.zonasTodasModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        },
        error => {
          this.zonasTodasModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
      case 'territorios': {
        this.titulo =  'Territorios';
        this.request.nRutOperador = this.parametro;
        this.request.sCodZona = this.parametroDos;
        this.setearDatos.getTerritoriosTodos(this.request)
          .subscribe((data: any) => {
              (this.datos = data.general);
              this.cargando = false;
              if (data.error.codError !== 0) {
                this.zonasTodasModal.hide();
                swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
              }
        },
        error => {
          this.zonasTodasModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
      case 'territoriosPedidos': {
        this.titulo =  'territoriosPedidos';
        this.request.nRutOperador = this.parametro;
        this.setearDatos.getTerritoriosPedido(this.request)
          .subscribe((data: any) => {
              (this.datos = data.territorio);
              this.cargando = false;
              if (data.error.codError !== 0) {
                this.zonasTodasModal.hide();
                swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
              }
          },
          error => {
            this.zonasTodasModal.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          });
        break;
      }
   }
  }
  enviarInfo(dato) {
    this.setearDatos.setDatosGeo(dato);
    this.zonasTodasModal.hide();
  }

}
