import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { NgxSpinnerService } from 'ngx-spinner';

import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { AdministracionComercialService } from '../../../core/consultaService/administracionComercial.service';

import { CategoriaNegocioPopUpComponent } from '../categoriaNegocioPopUp/categoriaNegocioPopUp.component';
import { BuscarArticuloPopUpComponent } from './../../commonPopUp/buscarArticuloPopUp/buscarArticuloPopUp.component';

const swal = require('sweetalert');

@Component({
  selector: 'app-territorios-pop-up',
  templateUrl: './territoriosPopUp.component.html',
  styleUrls: ['./territoriosPopUp.component.scss']
})
export class TerritoriosPopUpComponent implements OnInit, OnDestroy {

  forma: FormGroup;

  secondModal: BsModalRef;

  territorio: any = {};
  
  codFuerzaVenta = '';
  
  titulo = '';

  origen = '';

  estado: Array<any> = [
    {
      codEstado: 'VIGEN',
      descEstado: 'Vigente' 
    },
    {
      codEstado: 'NOVIG',
      descEstado: 'No Vigente' 
    }
  ];

  request: any = {};

  subData: any;

  datos: Array <any> = [];
  zonaExiste: boolean;


  constructor(public bsModalRef: BsModalRef, private consultasService:ConsultasService, private modalService: BsModalService,
              private authService:AuthService, public cargador: NgxSpinnerService, private administracionComercialService:AdministracionComercialService) {
    this.forma = new FormGroup ({
      codigoZona: new FormControl('', Validators.required),
      codigoZonaAntigua: new FormControl('', Validators.required),
      descZona: new FormControl({value:'', disabled:true }, Validators.required),
      codigoTerritorio: new FormControl('', Validators.required),
      descripcionTerritorio: new FormControl('', Validators.required),
      codigoTerrPadre: new FormControl({value:'', disabled:true }),
      descTerrPadre: new FormControl({value:'', disabled:true }),
      codigoSupervisor: new FormControl({value:'', disabled:true }),
      descSupervisor: new FormControl({value:'', disabled:true }),
      estadoTerritorio: new FormControl('', Validators.required),
    });
  }

  ngOnInit() {
    console.log('territorio', this.territorio);

    this.subData = this.consultasService.datos$.subscribe(resData =>{
      this.setData(resData);
    })

    if(this.titulo === 'Editar'){
      this.forma.controls.codigoTerritorio.disable();
    }

    this.forma.controls.codigoZona.setValue(this.territorio.ter_scodzona);
    this.forma.controls.codigoZonaAntigua.setValue(this.territorio.ter_scodzona);
    this.forma.controls.descZona.setValue(this.territorio.zon_sdeszona);
    this.forma.controls.codigoTerritorio.setValue(this.territorio.ter_scodterritorio);
    this.forma.controls.descripcionTerritorio.setValue(this.territorio.ter_sdesterritorio);

    if(this.territorio.ter_scodterritorio === this.territorio.ter_scodterritoriopadre){
      this.forma.controls.codigoTerrPadre.setValue('');
      this.forma.controls.descTerrPadre.setValue('');
    }
    else{
      this.forma.controls.codigoTerrPadre.setValue(this.territorio.ter_scodterritoriopadre);
      this.forma.controls.descTerrPadre.setValue(this.territorio.ter_sdesterritoriopadre);
    }
   
    this.forma.controls.estadoTerritorio.setValue(this.territorio.ter_scodestado);
  }

  ngOnDestroy() {
    if(this.subData != undefined) {
      this.subData.unsubscribe();
    }
  }

  openModalZona() {
    this.origen = 'zona';
    const initialState = {
      titulo: 'Zonas',
      origen: 'zona',
      codFuerzaVenta: this.codFuerzaVenta
    };
    this.secondModal = this.modalService.show(CategoriaNegocioPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  openModal(origen, titulo){
    this.origen = origen;
    const initialState = {
      titulo: titulo,
      origen: origen,
      codFuerzaVenta: this.codFuerzaVenta
    };
    this.secondModal = this.modalService.show(BuscarArticuloPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  setData(resData){
    console.log(resData);
    if(resData != 0){
      switch(this.origen) { 
        case 'zona': { 
          this.forma.controls.codigoZona.setValue(resData.zon_scodzona);
          this.forma.controls.descZona.setValue(resData.zon_sdeszona);
          if(this.titulo !== 'Editar'){
            this.forma.controls.codigoZonaAntigua.setValue(resData.zon_scodzona);
          }
          break; 
        } 
        case 'territorio': {
          if(resData.ter_scodterritorio === this.forma.controls.codigoTerritorio.value){
            swal('Atención', 'Territorio Padre no puede ser el mismo territorio', 'error');
            this.forma.controls.codigoTerrPadre.setValue('');
            this.forma.controls.descTerrPadre.setValue(''); 
          }
          else if(resData === 'ninguno'){
            this.forma.controls.codigoTerrPadre.setValue('');
            this.forma.controls.descTerrPadre.setValue(''); 
          }else{
            this.forma.controls.codigoTerrPadre.setValue(resData.ter_scodterritorio);
            this.forma.controls.descTerrPadre.setValue(resData.ter_sdesterritorio);
          }
          break; 
        }
      } 
    }
  }

  validaZona(){
    this.zonaExiste = false;
    if(this.forma.controls.codigoZona.value === ''){
      this.forma.controls.codigoZona.setValue('');
      this.forma.controls.descZona.setValue('');
    }else{
      this.request.nRutOperador = this.authService.getDatosUser().nrutoperador;
      this.request.sCodEstado = "VIGEN";
      this.request.sCodFuerzaVta = this.codFuerzaVenta;
      this.cargador.show();
      this.administracionComercialService.getZonasOperador(this.request)
      .subscribe((data:any)=>{
        this.cargador.hide();
        console.log(data);
        if(data){
          if(data.error.codError != 0){
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }else{
              for(let i in data.zonasOperador){
                if(data.zonasOperador[i].zon_scodzona === this.forma.controls.codigoZona.value){
                  this.forma.controls.descZona.setValue(data.zonasOperador[i].zon_sdeszona);
                  this.zonaExiste = true;
                  console.log('this.zonaExiste', this.zonaExiste);
                  break;
                }
              }
              if(!this.zonaExiste){
                this.forma.controls.codigoZona.setValue('');
                this.forma.controls.descZona.setValue('');
                swal('Atención', 'No se Encuentra Datos para Zonas', 'warning');
              }
            }
        }
      },
      error =>{
        this.cargador.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })

    }
  }

  guardarTerritorio(){
    console.log(this.forma.valid, this.zonaExiste);
    if(this.forma.valid){
      console.log('formulario valido');

      this.request.nRunUsuario = Number(this.authService.getDatosUser().nrunusuario);
      this.request.nRutOperador = Number(this.authService.getDatosUser().nrutoperador);
      this.request.sCodEstado = this.forma.controls.estadoTerritorio.value;
      this.request.sCodFuerzaVta = this.codFuerzaVenta;
      this.request.sCodSupervisor = this.forma.controls.codigoSupervisor.value;
      this.request.sCodTerritorio = this.forma.controls.codigoTerritorio.value;
      this.request.sCodTerritorioPadre = this.forma.controls.codigoTerrPadre.value;
      this.request.sDesTerritorio = this.forma.controls.descripcionTerritorio.value;
      this.request.sCodZonaAntigua = this.forma.controls.codigoZonaAntigua.value;
      this.request.sCodZonaNueva = this.forma.controls.codigoZona.value;
      
      if(this.titulo === 'Agregar'){
        this.request.sIndNuevo = 'S';
      }

      if(this.titulo === 'Editar'){
        this.request.sIndNuevo = 'N';
      }

      console.log('request territorio', this.request);
      this.cargador.show();

      this.administracionComercialService.guardarTerritorio(this.request)
        .subscribe((data:any)=>{
          this.cargador.hide();

          if(data.error.codError != 0){
            swal('Atención', data.error.msjError, 'error');
          } else{
            this.consultasService.setDatos('guardar');
            swal('Atención','Territorio guardado correctamente', 'success');
            this.bsModalRef.hide();
          }
        },
        error =>{
          this.cargador.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        })


    }
  }

}
