import { Component, OnInit } from '@angular/core';
import { ArticuloService } from './../../../core/consultaService/articulo.service';
import { ConsultasService } from './../../../core/consultaService/consultas.service';
import { AdministracionComercialService } from './../../../core/consultaService/administracionComercial.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { AdministracionOperativaService } from '../../../core/consultaService/administracionOperativa.service';
import { FormGroup} from '@angular/forms';

import { BsModalRef } from 'ngx-bootstrap/modal';
import { CajaCobranzaService } from '../../../core/consultaService/cajaCobranza.service';
const swal = require('sweetalert');

@Component({
  selector: 'app-buscar-articulo-pop-up',
  templateUrl: './buscarArticuloPopUp.component.html',
  styleUrls: ['./buscarArticuloPopUp.component.scss']
})
export class BuscarArticuloPopUpComponent implements OnInit {

  forma: FormGroup;

  origen: string;
  cargando = false;

  codFuerzaVenta:string;

  request: any = {};

  datos: Array <any> = [];
  titulo: string;

  rutCliente:number;

  constructor( private articuloService: ArticuloService, private consultasService:ConsultasService, private authService:AuthService, 
               public bsModalRef: BsModalRef, private administracionComercialService: AdministracionComercialService,
               private administracionOperativaService:AdministracionOperativaService, private cajaCobranzaService:CajaCobranzaService ) { }

  ngOnInit() {
    this.cargando = true;
  
    switch(this.origen) { 
      case 'linea': { 
        this.request.scodestado = 'VIGEN';
        this.articuloService.getLineas(this.request)
        .subscribe((data:any)=>{
            (this.datos = data.articulo);
            this.cargando = false;

            if(data.error.codError != 0){
              this.bsModalRef.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            } 
        },
        error =>{
          this.bsModalRef.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        })
        break; 
      } 
      case 'categoria': { 
        this.request.scodestado = 'VIGEN';
        this.articuloService.getCategorias(this.request)
        .subscribe((data:any)=>{
            (this.datos = data.articulo);
            this.cargando = false;

            if(data.error.codError != 0){
              this.bsModalRef.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            } 
        },
        error =>{
          this.bsModalRef.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        })
        break; 
      }
      case 'agrupacion': { 
        this.request.scodestado = 'VIGEN';
        this.articuloService.getAgrupacion(this.request)
        .subscribe((data:any)=>{
            (this.datos = data.articulo);
            this.cargando = false;

            if(data.error.codError != 0){
              this.bsModalRef.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            } 
        },
        error =>{
          this.bsModalRef.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        })
        break; 
      }
      case 'prodbase': { 
        this.request.scodestado = 'VIGEN';
        this.articuloService.getProdBase(this.request)
        .subscribe((data:any)=>{
            (this.datos = data.articulo);
            this.cargando = false;

            if(data.error.codError != 0){
              this.bsModalRef.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            } 
        },
        error =>{
          this.bsModalRef.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        })
        break; 
      }
      case 'territorio': { 
        this.request.nRutOperador = this.authService.getDatosUser().nrutoperador;
        this.request.sCodFuerzaVta = this.codFuerzaVenta;

        this.consultasService.getTerritoriosTodos(this.request)
        .subscribe((data:any)=>{
            (this.datos = data.general);
            this.cargando = false;

            if(data.error.codError != 0){
              this.bsModalRef.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            } 
        },
        error =>{
          this.bsModalRef.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        })
        break; 
      }

      case 'fuerzaVenta': {
        this.request = {
          "nNumLocal": 0,
          "nRutCliente": this.forma.controls.rutCliente.value === '' ? 0 :this.forma.controls.rutCliente.value,
          "nRutOperador": this.forma.controls.codOperador.value === '' ? 0 : this.forma.controls.codOperador.value,
          "sCodBusqueda": "FZAVT",
          "sCodCatNegocio": this.forma.controls.codCatNegocio.value,
          "sCodRelacionComer": this.forma.controls.codClienteLocal.value,
          "sCodTerritorio": this.forma.controls.codTerritorio.value,
          "sCodZona": this.forma.controls.codZona.value,
          "sDesRelacionComer": "",
          "sNomCliente": "",
          "scodFuerzavta": this.forma.controls.codFuerzaVenta.value
        }

        this.administracionComercialService.getBuscaEstructuraComercial(this.request)
        .subscribe((data:any)=>{
          (this.datos = data.fuerzaVenta);
          this.cargando = false;

          if(data.error.codError != 0){
            this.bsModalRef.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          } 
      },
      error =>{
        this.bsModalRef.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
        break;
      }

      case 'supervisor': {
        this.request.nRunOperador = this.authService.getDatosUser().nrutoperador;
        this.request.nRunSupervisor = 0;

        this.administracionOperativaService.getSupervisores(this.request)
        .subscribe((data:any)=>{
          (this.datos = data.supervisores);
          this.cargando = false;

          if(data.error.codError != 0){
            this.bsModalRef.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          } 
      },
      error =>{
        this.bsModalRef.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
        break;
      }

      case 'operador': {
        this.request = {
          "nNumLocal": 0,
          "nRutCliente": this.forma.controls.rutCliente.value === '' ? 0 :this.forma.controls.rutCliente.value,
          "nRutOperador": this.forma.controls.codOperador.value === '' ? 0 : this.forma.controls.codOperador.value,
          "sCodBusqueda": "OPERA",
          "sCodCatNegocio": this.forma.controls.codCatNegocio.value,
          "sCodRelacionComer": this.forma.controls.codClienteLocal.value,
          "sCodTerritorio": this.forma.controls.codTerritorio.value,
          "sCodZona": this.forma.controls.codZona.value,
          "sDesRelacionComer": "",
          "sNomCliente": "",
          "scodFuerzavta": this.forma.controls.codFuerzaVenta.value
        }

        this.administracionComercialService.getBuscaEstructuraComercial(this.request)
        .subscribe((data:any)=>{
          (this.datos = data.operadores);
          this.cargando = false;

          if(data.error.codError != 0){
            this.bsModalRef.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          } 
      },
      error =>{
        this.bsModalRef.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
        break;
      }

      case 'zona': {
        this.request = {
          "nNumLocal": 0,
          "nRutCliente": this.forma.controls.rutCliente.value === '' ? 0 :this.forma.controls.rutCliente.value,
          "nRutOperador": this.forma.controls.codOperador.value === '' ? 0 : this.forma.controls.codOperador.value,
          "sCodBusqueda": "ZONAS",
          "sCodCatNegocio": this.forma.controls.codCatNegocio.value,
          "sCodRelacionComer": this.forma.controls.codClienteLocal.value,
          "sCodTerritorio": this.forma.controls.codTerritorio.value,
          "sCodZona": this.forma.controls.codZona.value,
          "sDesRelacionComer": "",
          "sNomCliente": "",
          "scodFuerzavta": this.forma.controls.codFuerzaVenta.value
        }

        this.administracionComercialService.getBuscaEstructuraComercial(this.request)
        .subscribe((data:any)=>{
          (this.datos = data.zonas);
          this.cargando = false;

          if(data.error.codError != 0){
            this.bsModalRef.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          } 
      },
      error =>{
        this.bsModalRef.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
        break;
      }

      case 'territorioAC': {
        this.request = {
          "nNumLocal": 0,
          "nRutCliente": this.forma.controls.rutCliente.value === '' ? 0 :this.forma.controls.rutCliente.value,
          "nRutOperador": this.forma.controls.codOperador.value === '' ? 0 : this.forma.controls.codOperador.value,
          "sCodBusqueda": "TERRI",
          "sCodCatNegocio": this.forma.controls.codCatNegocio.value,
          "sCodRelacionComer": this.forma.controls.codClienteLocal.value,
          "sCodTerritorio": this.forma.controls.codTerritorio.value,
          "sCodZona": this.forma.controls.codZona.value,
          "sDesRelacionComer": "",
          "sNomCliente": "",
          "scodFuerzavta": this.forma.controls.codFuerzaVenta.value
        }

        this.administracionComercialService.getBuscaEstructuraComercial(this.request)
        .subscribe((data:any)=>{
          (this.datos = data.territorios);
          this.cargando = false;

          if(data.error.codError != 0){
            this.bsModalRef.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          } 
      },
      error =>{
        this.bsModalRef.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
        break;
      }

      case 'catNegocio': {
        this.request = {
          "nNumLocal": 0,
          "nRutCliente": this.forma.controls.rutCliente.value === '' ? 0 :this.forma.controls.rutCliente.value,
          "nRutOperador": this.forma.controls.codOperador.value === '' ? 0 : this.forma.controls.codOperador.value,
          "sCodBusqueda": "CTNEG",
          "sCodCatNegocio": this.forma.controls.codCatNegocio.value,
          "sCodRelacionComer": this.forma.controls.codClienteLocal.value,
          "sCodTerritorio": this.forma.controls.codTerritorio.value,
          "sCodZona": this.forma.controls.codZona.value,
          "sDesRelacionComer": "",
          "sNomCliente": "",
          "scodFuerzavta": this.forma.controls.codFuerzaVenta.value
        }

        this.administracionComercialService.getBuscaEstructuraComercial(this.request)
        .subscribe((data:any)=>{
          (this.datos = data.categoriaNegocio);
          this.cargando = false;

          if(data.error.codError != 0){
            this.bsModalRef.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          } 
      },
      error =>{
        this.bsModalRef.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
        break;
      }

      case 'tipoPago': {
        this.request = {
          "nIdRecorrido": 0,
          "nNumLocal": 0,
          "nRutCliente": this.rutCliente,
          "nRutOperador": parseInt(this.authService.getDatosUser().nrutoperador),
          "opcion": "PAGOS"
        }

        this.cajaCobranzaService.buscaTiposPago(this.request)
        .subscribe((data:any)=>{
          (this.datos = data.tipoPago);
          this.cargando = false;

          if(data.error.codError != 0){
            this.bsModalRef.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          } 
      },
      error =>{
        this.bsModalRef.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
        break;
      }

      case 'tipoPagoTransportista': {
        this.request = {
          "nIdRecorrido": 0,
          "nNumLocal": 0,
          "nRutCliente": this.rutCliente,
          "nRutOperador": parseInt(this.authService.getDatosUser().nrutoperador),
          "opcion": "TRANSPORTISTA"
        }

        this.cajaCobranzaService.buscaTiposPago(this.request)
        .subscribe((data:any)=>{
          (this.datos = data.tipoPago);
          console.log('this.datos pago transportista => : ', this.datos);
          this.cargando = false;

          if(data.error.codError != 0){
            this.bsModalRef.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          } 
      },
      error =>{
        this.bsModalRef.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
        break;
      }
   } 
  }

  enviarInfo(dato){
    this.consultasService.setDatos(dato);
    this.bsModalRef.hide();
  }

}
