import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, NgForm } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { NgxSpinnerService } from 'ngx-spinner';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { ClientesService } from '../../../core/consultaService/clientes.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { UtilGeoPopUpComponent } from '../utilGeoPopUp/utilGeoPopUp.component';

const swal = require('sweetalert');
declare var jQuery: any;
declare var $: any;

@Component({
  selector: 'app-localesPopUp',
  templateUrl: './localesPopUp.component.html',
  styleUrls: ['./localesPopUp.component.scss']
})
export class LocalesPopUpComponent implements OnInit, OnDestroy {
  forma: FormGroup;
  local: any;
  subData: any;
  creaLocal: any = {};
  origen: string;
  titulo: string;
  utilGeoModal: BsModalRef;
  rutcliente = this.clientesService.selectedCliente.cli_nrutcliente;
  rutoperador = this.clientesService.selectedCliente.cli_nrutoperador;
  estado: Array<any> = [
    {
      loc_scodestado: 'VIGEN',
      descEstado: 'Vigente'
    },
    {
      loc_scodestado: 'NOVIG',
      descEstado: 'No Vigente'
    }
  ];
  categoriaNegocio: Array <any> = [];
  user: any = {};

  constructor(fb: FormBuilder, private clientesService: ClientesService,
    private setearDatos: ConsultasService, public localesModal: BsModalRef,
    private modalService: BsModalService, public loader: NgxSpinnerService,
    private authService: AuthService) {

    this.forma = new FormGroup ({
      nombre: new FormControl('', Validators.required),
      nombreFanta: new FormControl(''),
      codEstadoLocal: new FormControl('', Validators.required),
      cateNegocio: new FormControl('', Validators.required),
      codDireccion: new FormControl({value: '', disabled: true }, Validators.required),
      desDireccion: new FormControl({value: '', disabled: true }),
      numLocal: new FormControl('')
    });
  }

  ngOnInit() {
    this.subData = this.setearDatos.datosGeo$.subscribe(resData => {
      this.setData(resData);
    });
    this.user = this.authService.getDatosUser();
    this.catNegocio();
    if (this.local === undefined) {
    } else {
      this.titulo = `${'Detalle Local Número ' + this.local.loc_nnumlocal + '"' + this.local.loc_snombre + '"'}`;
      this.forma.controls.nombre.setValue(this.local.loc_snombre);
      this.forma.controls.nombreFanta.setValue(this.local.loc_snombrefantasia);
      this.forma.controls.codEstadoLocal.setValue(this.local.loc_scodestado);
      this.forma.controls.cateNegocio.setValue(this.local.loc_scodcategorianegocio);
      this.forma.controls.codDireccion.setValue(this.local.loc_niddireccion);
      this.forma.controls.desDireccion.setValue(this.local.dic_sdireccion);
      this.forma.controls.numLocal.setValue(this.local.loc_nnumlocal);
    }
  }
  ngOnDestroy() {
    if (this.subData !== undefined) {
      this.subData.unsubscribe();
    }
  }
  catNegocio() {
    this.setearDatos.getClienteCategoriaNegocio()
        .subscribe((data: any) => {
            (this.categoriaNegocio = data.categoriaNegocio);
            this.forma.controls.cateNegocio.setValue(this.categoriaNegocio[0].can_scodcatnegocio);
        });
  }
  openModalDireBanco() {
    this.origen = 'direccionBanco';
    const initialState = {
      titulo: 'Dirección',
      origen: 'direccionBanco'
    };
    this.utilGeoModal = this.modalService.show(UtilGeoPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }
  setData(resData) {
    if (resData !== 0) {
      switch (this.origen) {
        case 'direccionBanco': {
          this.forma.controls.codDireccion.setValue(resData.dic_niddireccion);
          this.forma.controls.desDireccion.setValue(resData.dic_sdireccion);
          break;
        }
      }
    }
    $('body').removeClass('modal-open');
  }
  guardarNuevoLocal ($event: any, succesLocales: string) {
    this.forma.controls.codDireccion.enable();
    if (this.forma.valid) {
      $('*').removeClass('error-form');
      this.loader.show();

      this.creaLocal.codCategoriaNegocio = this.forma.controls.cateNegocio.value;
      this.creaLocal.codEstado = this.forma.controls.codEstadoLocal.value;
      this.creaLocal.nidDireccion = this.forma.controls.codDireccion.value;
      this.creaLocal.nombre = this.forma.controls.nombre.value;
      this.creaLocal.nombreFantasia = this.forma.controls.nombreFanta.value;

      if ((this.forma.controls.numLocal.value === undefined) || (this.forma.controls.numLocal.value === '') ) {
        this.creaLocal.numLocal = 0;
      } else {
        this.creaLocal.numLocal = Number(this.forma.controls.numLocal.value);
      }

      this.creaLocal.rutCliente = Number(this.rutcliente);
      this.creaLocal.rutOperador = Number(this.rutoperador);
      this.creaLocal.rutUsuarioActualiza = Number(this.user.nrunusuario);
      console.log(this.creaLocal);
      
      this.clientesService.saveLocales(this.creaLocal)
            .subscribe((data: any) => {
              console.log(data);
              
              if (data.error.codError === 0) {
                this.loader.hide();
                swal('Bien', 'Los datos han sido ingresados satisfactoriamente.', 'success' );
                this.setearDatos.setDatos(succesLocales);
                this.localesModal.hide();
              } else {
                swal('Atención', data.error.msjError, 'error');
                this.loader.hide();
              }
              this.forma.controls.codDireccion.disable();
          },
          error => {
            this.loader.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          });
    } else {
      swal('Atención', 'Existen campos requeridos no ingresados', 'error' );
      if (!this.forma.controls.nombre.valid) {
        $('[name=nombre]').addClass('error-form');
      } else {
        $('[name=nombre]').removeClass('error-form');
      }

      if (!this.forma.controls.codEstadoLocal.valid) {
        $('[name=codEstadoLocal]').addClass('error-form');
      } else {
        $('[name=codEstadoLocal]').removeClass('error-form');
      }

      if (this.forma.controls.cateNegocio.value === '') {
        $('[name=cateNegocio]').addClass('error-form');
      } else {
        $('[name=cateNegocio]').removeClass('error-form');
      }

      if (this.forma.controls.codDireccion.value === '') {
        $('[name=codDireccion]').addClass('error-form');
      } else {
        $('[name=codDireccion]').removeClass('error-form');
      }
      this.forma.controls.codDireccion.disable();
      this.loader.hide();
    }
  }

}
