import { Component, OnInit } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ConsultasService } from '../../../core/consultaService/consultas.service';

const swal = require('sweetalert');

@Component({
  selector: 'app-tipoClientePopUp',
  templateUrl: './tipoClientePopUp.component.html',
  styleUrls: ['./tipoClientePopUp.component.scss']
})
export class TipoClientePopUpComponent implements OnInit {

  titulo: string;
  origen: string;
  cargando = false;
  request: any = {};
  filtro: any;

  datos: Array <any> = [];

  constructor(private setearDatos: ConsultasService, public tiposClienteModal: BsModalRef, private modalService: BsModalService) { }

  ngOnInit() {
    this.cargando = true;
    switch (this.origen) {
      case 'tipoCliente': {
        this.titulo =  'Tipo CLiente';
        this.request.parnumparametro = 23;
        this.setearDatos.getTipoCliente(this.request)
        .subscribe((data: any) => {
            (this.datos = data.general);
            this.cargando = false;

            if (data.error.codError !== 0) {
              this.tiposClienteModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        },
        error => {
          this.tiposClienteModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
      case 'cupo': {
        this.titulo =  'Cupos';
        this.request.parnumparametro = 30;
        this.request.parcodparametro01 = this.filtro;
        this.setearDatos.getTipoCliente(this.request)
        .subscribe((data: any) => {
            (this.datos = data.general);
            this.cargando = false;

            if (data.error.codError !== 0) {
              this.tiposClienteModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        },
        error => {
          this.tiposClienteModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }

      }
    }

  enviarInfo(dato){
    this.setearDatos.setDatos(dato);
    this.tiposClienteModal.hide();
  }

}
