import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, NgForm } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { NgxSpinnerService } from 'ngx-spinner';
import { AdministracionOperativaService } from '../../../core/consultaService/administracionOperativa.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { ConductororesPopUpComponent } from '../conductororesPopUp/conductororesPopUp.component';
import { ConsultasService } from '../../../core/consultaService/consultas.service';

const swal = require('sweetalert');
declare var jQuery: any;
declare var $: any;

@Component({
  selector: 'app-vehiculosPopUp',
  templateUrl: './vehiculosPopUp.component.html',
  styleUrls: ['./vehiculosPopUp.component.scss']
})
export class VehiculosPopUpComponent implements OnInit, OnDestroy {
  forma: FormGroup;
  vehiculo: any;
  subData: any;
  creaVehiculo: any = {};
  origen: string;
  titulo: string;
  edit: string;
  conductorModal: BsModalRef;
  ruttransporte = this.transportistaService.selectedTransportista.tra_nruttransportista;
  rutoperador = this.transportistaService.selectedTransportista.tra_nrutoperador;
  estado: Array<any> = [
    {
      cam_scodestado: 'VIGEN',
      descEstado: 'Vigente'
    },
    {
      cam_scodestado: 'NOVIG',
      descEstado: 'No Vigente'
    }
  ];
  tipoVehiculos: Array<any> = [];
  request: any = {};
  consultaVehiculo: any = {};
  user: any = {};

  constructor(fb: FormBuilder, private transportistaService: AdministracionOperativaService,
    public vehiculosTransporteModal: BsModalRef,
    private modalService: BsModalService, public loader: NgxSpinnerService,
    private authService: AuthService, private setearDatos: ConsultasService) {
      this.forma = new FormGroup ({
        patente: new FormControl('', Validators.required),
        tipoVehiculo: new FormControl('', Validators.required),
        volumenVehiculo: new FormControl('', Validators.required),
        cargaVehiculo: new FormControl('', Validators.required),
        runConductor: new FormControl(''),
        conductorVehiculo: new FormControl({value: '', disabled: true }),
        estado: new FormControl('', Validators.required)
      });
    }

  ngOnInit() {
    this.subData = this.transportistaService.datos$.subscribe(resData => {
      this.setData(resData);
    });
    this.tipoVehiculo();
    this.user = this.authService.getDatosUser();
    console.log(this.vehiculo);
    if (this.vehiculo === undefined ) {
      console.log('es nuevo');
      console.log(this.vehiculo);
      this.edit = 'S';
    } else {
      this.edit = 'N';
      console.log(this.vehiculo);
      this.forma.controls.patente.setValue(this.vehiculo.cam_spatente);
      this.forma.controls.patente.disable();
      this.forma.controls.tipoVehiculo.setValue(this.vehiculo.cam_stipocamion);
      this.forma.controls.volumenVehiculo.setValue(this.vehiculo.cam_nvolumen);
      this.forma.controls.cargaVehiculo.setValue(this.vehiculo.cam_ncarga);
      this.forma.controls.runConductor.setValue(this.vehiculo.cho_nrunchofer);
      this.forma.controls.conductorVehiculo.setValue(this.vehiculo.cho_snombre);
      this.forma.controls.estado.setValue(this.vehiculo.cam_scodestado);
    }
  }
  ngOnDestroy() {
    if (this.subData !== undefined) {
      this.subData.unsubscribe();
    }
  }
  tipoVehiculo() {
    this.request.parnumparametro = 19;
    this.setearDatos.getTipoCliente(this.request)
        .subscribe((data: any) => {
          console.log(data);
            (this.tipoVehiculos = data.general);
        });
  }
  openModalConductores() {
    this.origen = 'conductores';
    const initialState = {
      titulo: 'Conductores',
      origen: 'conductores'
    };
    this.conductorModal = this.modalService.show(ConductororesPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }
  setData(resData) {
    if (resData !== 0) {
      switch (this.origen) {
        case 'conductores': {
          this.forma.controls.runConductor.setValue(resData.cho_nrunchofer);
          this.forma.controls.conductorVehiculo.setValue(`${resData.cho_snombre} ${resData.cho_sapepaterno} ${resData.cho_sapematerno}`);
          break;
        }
      }
    }
    $('body').removeClass('modal-open');
  }
  guardarNuevoVehiculo ($event: any, succesVehiculo: string) {
    if (this.forma.valid) {
      $('*').removeClass('error-form');
      this.loader.show();

      this.creaVehiculo.carga = Number(this.forma.controls.cargaVehiculo.value);
      this.creaVehiculo.codEstado = this.forma.controls.estado.value;
      this.creaVehiculo.codTipocamion = this.forma.controls.tipoVehiculo.value;
      this.creaVehiculo.indNuevo = this.edit;
      this.creaVehiculo.patenteVehiculo = this.forma.controls.patente.value;
      if ((this.forma.controls.runConductor.value !== 0) &&
        (this.forma.controls.runConductor.value !== '') &&
        (this.forma.controls.runConductor.value !== null) &&
        (this.forma.controls.runConductor.value !== undefined) ) {
          this.creaVehiculo.runChofer = Number(this.forma.controls.runConductor.value);
      } else {
        this.creaVehiculo.runChofer = 0;
      }
      /* this.creaVehiculo.runChofer = this.forma.controls.runConductor.value; */
      this.creaVehiculo.runCreador = Number(this.user.nrunusuario);
      this.creaVehiculo.rutOperador = Number(this.rutoperador);
      this.creaVehiculo.rutTransportista = Number(this.ruttransporte);
      this.creaVehiculo.volumen = Number(this.forma.controls.volumenVehiculo.value);
      console.log(this.creaVehiculo);
      this.transportistaService.saveVehiculos(this.creaVehiculo)
          .subscribe((data: any) => {
            console.log(data);
            if (data) {
              if (data.error.codError === 0) {
                this.transportistaService.setDatos(succesVehiculo);
                this.vehiculosTransporteModal.hide();
                swal('Bien', 'Los datos han sido ingresados satisfactoriamente.', 'success' );
                this.loader.hide();
              } else {
                swal('Atención', data.error.msjError, 'error');
                this.loader.hide();
              }
            } else {
              this.loader.hide();
              swal('Atención', 'Ha ocurrido un error, vuelva a intentarlo', 'error');
            }
          },
          error => {
            this.loader.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          });
    } else {
      swal('Atención', 'Existen campos requeridos no ingresados', 'error' );
      if (!this.forma.controls.cargaVehiculo.valid) {
        $('[name=cargaVehiculo]').addClass('error-form');
      } else {
        $('[name=cargaVehiculo]').removeClass('error-form');
      }

      if (!this.forma.controls.estado.valid) {
        $('[name=estado]').addClass('error-form');
      } else {
        $('[name=estado]').removeClass('error-form');
      }

      if (!this.forma.controls.tipoVehiculo.valid) {
        $('[name=tipoVehiculo]').addClass('error-form');
      } else {
        $('[name=tipoVehiculo]').removeClass('error-form');
      }
      if (!this.forma.controls.patente.valid) {
        $('[name=patente]').addClass('error-form');
      } else {
        $('[name=patente]').removeClass('error-form');
      }

      if (!this.forma.controls.volumenVehiculo.valid) {
        $('[name=volumenVehiculo]').addClass('error-form');
      } else {
        $('[name=volumenVehiculo]').removeClass('error-form');
      }
      this.loader.hide();
    }
  }

}
