import { Component, OnInit } from "@angular/core";
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl,
  NgForm
} from "@angular/forms";
import { BsModalService, BsModalRef } from "ngx-bootstrap/modal";
import { ConsultasService } from "../../../core/consultaService/consultas.service";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "../../../core/consultaService/auth.service";
import { AdministracionOperativaService } from "../../../core/consultaService/administracionOperativa.service";
import { UtilService } from '../../../services/util.service';


const swal = require("sweetalert");
declare var jQuery: any;
declare var $: any;

@Component({
  selector: "app-operadoresPopUp",
  templateUrl: "./operadoresPopUp.component.html"
})
export class OperadoresPopUpComponent implements OnInit {
  origen: string;
  forma: FormGroup;
  param: string;
  titulo: string;
  dia: string;
  cargando = false;
  datos: Array<any> = [];
  operadores: Array<any> = [];
  request: any = {};
  user: any = {};
  busquedaCompleta: boolean = false;

  constructor(
    private setearDatos: AdministracionOperativaService,
    public operadoresModal: BsModalRef,
    private modalService: BsModalService,
    private authService: AuthService,
    public loader: NgxSpinnerService,
    private utilService: UtilService
  ) {
    this.forma = new FormGroup({
      nRutOperador: new FormControl(""),
      sCodEstado: new FormControl("VIGEN"),
      sNombre: new FormControl(""),
      sNombreFantasia: new FormControl("")
    });
  }

  ngOnInit() {
    this.user = this.authService.getDatosUser();

    if (this.busquedaCompleta) {
      this.buscarTodo(event);
    }
    
  }

  buscar($event: any) {
    this.loader.show();
    this.forma.controls.nRutOperador.setValue(this.user.nrutoperador);
    this.request = this.forma.value;
    this.setearDatos.getOperadoresUsuarios(this.request).subscribe(
      (data: any) => {
        this.loader.hide();
        if (data.operadores.length === 0 && data.error.codError === 0) {
          swal("Atención", "No se encontraron resultados", "info");
        } else {
          if (data.operadores === null) {
            swal("Atención", "Ha ocurrido un error inesperado!", "error");
          } else {
            this.operadores = data.operadores;
          }
        }
        if (data.error.codError !== 0) {
          swal("Atención", "Ha ocurrido un error inesperado!", "error");
        }
      },
      error => {
        this.loader.hide();
        swal("Atención", "Ha ocurrido un error inesperado!", "error");
      }
    );
  }

  buscarTodo($event: any) {
    this.loader.show();
    this.operadores= [
    {
      "ope_nrutoperador": 0,
      "ope_sdigverificador": "",
      "ope_snombre": "(Todas)"
    }

    ];
    this.forma.controls.nRutOperador.setValue(0);
    this.request = this.forma.value;
    this.setearDatos.getOperadoresUsuarios(this.request).subscribe(
      (data: any) => {
        this.loader.hide();
        if (data.operadores.length === 0 && data.error.codError === 0) {
          swal("Atención", "No se encontraron resultados", "info");
        } else {
          if (data.operadores === null) {
            swal("Atención", "Ha ocurrido un error inesperado!", "error");
          } else {
            data.operadores.forEach(e => {
              this.operadores.push(e);
            });
          }
        }
        if (data.error.codError !== 0) {
          swal("Atención", "Ha ocurrido un error inesperado!", "error");
        }
      },
      error => {
        this.loader.hide();
        swal("Atención", "Ha ocurrido un error inesperado!", "error");
      }
    );
  }

  enviarInfo(operador) {
    this.setearDatos.setDatos(operador);
    this.operadoresModal.hide();
  }
}
