import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';

import { registerLocaleData, Time } from '@angular/common';
import es from '@angular/common/locales/es';

import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { PedidosService } from '../../../core/consultaService/pedidos.service';

import { NgxSpinnerService } from 'ngx-spinner';

const swal = require('sweetalert');

@Component({
  selector: 'app-cliente-caja-cobranza-pop-up',
  templateUrl: './clienteCajaCobranzaPopUp.component.html',
  styleUrls: ['./clienteCajaCobranzaPopUp.component.scss']
})
export class ClienteCajaCobranzaPopUpComponent implements OnInit {
  
  forma: FormGroup;
  request: any = {};
  clientes: Array <any> = [];
  cargando = false;
  titulo: string;

  constructor(public bsModalRef: BsModalRef, private consultasService:ConsultasService, private authService: AuthService,
              private pedidosService:PedidosService, public cargador: NgxSpinnerService) {
    this.forma = new FormGroup ({
      nombreCliente: new FormControl('')
    });
  }

  ngOnInit() {
    registerLocaleData( es );
  }

  buscar(){
    this.clientes = [];
    
    this.request.nombre = this.forma.controls.nombreCliente.value;
    this.request.rutCliente = 0;
    this.request.rutOperador = this.authService.getDatosUser().nrutoperador;

    console.log(this.request);

    this.cargando = true;
    this.pedidosService.getClientesPedido(this.request)
    .subscribe((data:any)=>{
        this.cargador.hide();
        console.log('data', data);
        this.cargando = false;
        if(data.buscaClientes){
          console.log('existe data');
          if(data.buscaClientes.length == 0 && data.error.codError == 0 ){
            swal('Atención', 'No se encontraron resultados', 'info');
          }else{
            this.clientes = data.buscaClientes;
          }
        }  
        
        if(data.error.codError != 0){
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        }
    },
    error =>{
        this.cargador.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
    })


  }

  enviarInfo(dato){
    this.consultasService.setDatos(dato);
    this.bsModalRef.hide();
  }

}
