import { Component, OnInit } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

import { LoginService } from './../../../core/consultaService/login.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { ConfirmacionCierreCajaPopUpComponent } from '../../../routes/commonPopUp/confirmacionCierreCajaPopUp/confirmacionCierreCajaPopUp.component';

import { NgxSpinnerService } from 'ngx-spinner';
import { Md5 } from "md5-typescript";

const swal = require('sweetalert');

@Component({
  selector: 'app-validacion-usuario-pop-up',
  templateUrl: './validacionUsuarioPopUp.component.html',
  styleUrls: ['./validacionUsuarioPopUp.component.scss']
})
export class ValidacionUsuarioPopUpComponent implements OnInit {

  form: FormGroup;
  request: any = {};
  cargando = false;
  password: string;
  usuario: string;
  submitted = false;
  user: any = {};
  origen: string;
  param: string;
  titulo: string;

  constructor(
    public bsModalRef: BsModalRef,
    private modalService: BsModalService,
    private authService: AuthService,
    public cargador: NgxSpinnerService,
    private formBuilder: FormBuilder,
    private loginService: LoginService
  ) {
    this.form = this.formBuilder.group({
      usuario: ['', Validators.required],
      password: ['', Validators.required],
    });
  }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
  }

  cancelar() {
    this.bsModalRef.hide();
  }

  get f() { return this.form.controls; }

  submit() {
    this.submitted = true;
    // Verifica si los campos ingresados son válidos
    if (this.form.invalid) {
      return;
    }
    this.cargador.show();

    const hashedPassword = Md5.init(this.form.value.password).toUpperCase();

    const request = {
      scodaplicacion: 'NES',
      scontrasena: hashedPassword,
      snomusuario: this.form.value.usuario
    };

    this.loginService.getValidaLogeo(request)
      .subscribe((data: any) => {
        this.cargador.hide();

        if (data.validaLogeo) {
          if (data.validaLogeo.length === 0 && data.error.codError === 0) {
            swal('Atención', 'No se encontraron resultados', 'info');
          } else if (data.error.codError === 0 && data.error.msjError !== ' ') {
            swal('Atención', 'La contraseña ingresada es incorrecta', 'error');
          } else {
            this.bsModalRef.hide();

            const initialState = {
              titulo: 'Cierre Caja',
              subtitulo: 'Se procederá a efectuar el cierre. ¿Desea Continuar?',
              usuario: this.form.value.usuario,
              password: this.form.value.password,
            };

            this.modalService.show(ConfirmacionCierreCajaPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
          }
        } else {
          if (data.error.codError === 78103) {
            swal('Atención', 'No existe el usuario', 'error');
          } else {
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          }
        }

      }, error => {
        this.cargador.hide();
        this.bsModalRef.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      });

  }

}
