import { Component, OnInit } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { FormGroup, FormBuilder, FormControl, NgForm } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';

import { SettingsService } from '../../../core/settings/settings.service';
import { PedidosService } from '../../../core/consultaService/pedidos.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import {Md5} from "md5-typescript";

const swal = require('sweetalert');

@Component({
  selector: 'app-validacionUsuarioPedidoPopUp',
  templateUrl: './validacionUsuarioPedidoPopUp.component.html',
  styleUrls: ['./validacionUsuarioPedidoPopUp.component.scss']
})
export class ValidacionUsuarioPedidoPopUpComponent implements OnInit {
  origen: string;
  param: string;
  titulo: string;
  forma: FormGroup;
  user: any = {};
  cargando = false;
  request: any = {};
  pass: string;


  constructor(private authService: AuthService, public validacionPedidoModal: BsModalRef,
    private modalService: BsModalService, private pedidosService: PedidosService,
    public settings: SettingsService, fb: FormBuilder, public loader: NgxSpinnerService) {
      this.forma = new FormGroup ({
        email: new FormControl(),
        pass: new FormControl(),
      });
     }

  ngOnInit() {
    this.user = this.authService.getDatosUser();

  }
  cerrar(succesCierre: string) {
    console.log(succesCierre);
    this.loader.show();
    this.pass = Md5.init(this.forma.value.pass);
    this.request.nRunUsuario =  Number(this.user.nrunusuario);
    this.request.nRutOperador = Number(this.user.nrutoperador);
    this.request.sContrasena = this.forma.value.pass;
    this.request.sNomUsuario = this.forma.controls.email.value;
    console.log(this.request);
    this.pedidosService.updateCierreProceso(this.request)
          .subscribe((data: any) => {
            if (data) {
              if (data.error.codError !== 0) {
                this.loader.hide();
                swal('Atención', data.error.msjError, 'error');
                } else {
                  this.loader.hide();
                  swal('Bien', 'Ha sido finalizado satisfactoriamente.', 'success' );
                  this.pedidosService.setDatos(succesCierre);
                  this.validacionPedidoModal.hide();
                }
            }
          },
          error => {
            this.loader.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          });
  }

  salir() {
    this.validacionPedidoModal.hide();
  }

}
