import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from './../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class GestionAccionComercialService {

  constructor(private http: HttpClient) { }
  private tiposInversion = [{
    nTipoInversion: 1,
    sTipoInversion: 'Inversión de negociaciones'
  },
  {
    nTipoInversion: 2,
    sTipoInversion: 'Diferencias de precio'
  },
  {
    nTipoInversion: 3,
    sTipoInversion: 'Inversión Propia Distribuidor'
  }];
  private baseInversionUrl = environment.baseInversionUrl;

  private httpHeaders = new HttpHeaders({
    'Content-Type': 'application/json',
    'Accept': '*/*'
  });

  public listarInversiones(request: any) {
    return this.http.post(`${this.baseInversionUrl}inversion/busca/inversion`, request, { headers: this.httpHeaders });
  }

  public listarControlInversiones(request: any) {
    return this.http.post(`${this.baseInversionUrl}inversion/busca/inversionDescuento`, request, { headers: this.httpHeaders });
  }

  public listarTiposInversion() {
    return this.http.post(`${this.baseInversionUrl}inversion/carga/tipoInventario`, { headers: this.httpHeaders });
  }

  public listarEstadosInversion() {
    return this.http.post(`${this.baseInversionUrl}inversion/carga/estadoInversion`, { headers: this.httpHeaders });
  }

  public crearInversion(request: any) {
    return this.http.post(`${this.baseInversionUrl}inversion/crea/inversion`, request, { headers: this.httpHeaders });
  }

  public crearControlInversion(request: any) {
    return this.http.post(`${this.baseInversionUrl}inversion/guarda/inversionMovimiento`, request, { headers: this.httpHeaders });
  }

  public crearTransferenciaInversion(request: any) {
    return this.http.post(`${this.baseInversionUrl}inversion/actualiza/transferenciaInversion`, request, { headers: this.httpHeaders });
  }
}
