import { Component, OnInit } from '@angular/core';

import { BsModalRef } from 'ngx-bootstrap/modal';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';

import { registerLocaleData, Time } from '@angular/common';
import es from '@angular/common/locales/es';

import { AdministracionOperativaService } from '../../../core/consultaService/administracionOperativa.service';
import { AdministracionComercialService } from '../../../core/consultaService/administracionComercial.service';
import { UtilService } from './../../../services/util.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';

const swal = require('sweetalert');

@Component({
  selector: 'app-cliente-acpop-up',
  templateUrl: './clienteACPopUp.component.html',
  styleUrls: ['./clienteACPopUp.component.scss']
})
export class ClienteACPopUpComponent implements OnInit {

  forma: FormGroup;
  formaCli: FormGroup;
  origen: string;
  request: any = {};
  requestBuscar: any = {};
  datos: Array <any> = [];
  datosCliente: Array <any> = [];
  titulo: string;

  rutValido: boolean;

  verTodos = true;

  cargando = false;

  constructor(public bsModalRef: BsModalRef, fb: FormBuilder, private administracionComercialService:AdministracionComercialService,
              private utilService:UtilService, private consultasService:ConsultasService) {
    this.forma = new FormGroup ({
      rutCliente: new FormControl(''),
      nombreCliente: new FormControl(''),
      operador: new FormControl('')
    });
  }

  ngOnInit() {
    console.log('origen en modal', this.origen);
    console.log('formaCliente', this.formaCli);

    registerLocaleData( es );

    switch(this.origen) {
      case 'cliente': {

        if(this.formaCli.controls.codOperador.value === ''){
          this.verTodos = false;
        }

        if(this.formaCli.controls.rutCliente.value !== ''){
          this.forma.controls.rutCliente.setValue(this.formaCli.controls.rutCliente.value + this.formaCli.controls.dvRutCliente.value);
        }

        if(this.formaCli.controls.codOperador.value !== ''){
          this.forma.controls.operador.setValue(this.formaCli.controls.codOperador.value);
        }

        this.request = {
          "nNumLocal": 0,
          "nRutCliente": 0,
          "nRutOperador": this.formaCli.controls.codOperador.value === '' ? 0 : this.formaCli.controls.codOperador.value,
          "sCodBusqueda": "OPERA",
          "sCodCatNegocio": "",
          "sCodRelacionComer": "",
          "sCodTerritorio": "",
          "sCodZona": "",
          "sDesRelacionComer": "",
          "sNomCliente": "",
          "scodFuerzavta": ""
        }
      console.log('llama al servicio con el request', this.request);
      this.administracionComercialService.getBuscaEstructuraComercial(this.request)
        .subscribe((data:any)=>{
          console.log('data',data);
          (this.datos = data.operadores);

          if(data.error.codError != 0){
            this.bsModalRef.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          } 
      },
      error =>{
        this.bsModalRef.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
        break;
      }
    }
  }
  
  validaRut() {
    this.rutValido = false;

    if(!this.utilService.isValidRUT(this.forma.controls.rutCliente.value)){
      this.forma.controls.rutCliente.setValue('');
      this.forma.controls.nombreCliente.setValue('');
      this.forma.controls.operador.setValue('');
      swal('Atención', 'El RUT es incorrecto', 'error');
      this.datosCliente = [];
    }else{
      this.rutValido =  true;
    }
  }

  buscar(){
    // this.validaRut();
    console.log('forma',this.forma);
    console.log('formaCli', this.formaCli);

    this.datosCliente = [];

    if(this.rutValido || this.forma.controls.rutCliente.value === '' || this.forma.controls.rutCliente.pristine ){
      this.cargando = true;
      let rutStr = this.forma.controls.rutCliente.value;

      this.requestBuscar = {
        "nNumLocal": 0,
        "nRutCliente": rutStr === '' ? 0 : parseInt(rutStr.substr(0, rutStr.length - 1)),
        "nRutOperador": this.forma.controls.operador.value === '' ? 0 : this.forma.controls.operador.value,
        "sCodBusqueda": "CLIEN",
        "sCodCatNegocio": this.formaCli.controls.codCatNegocio.value,
        "sCodRelacionComer": this.formaCli.controls.codClienteLocal.value,
        "sCodTerritorio": this.formaCli.controls.codTerritorio.value,
        "sCodZona": this.formaCli.controls.codZona.value,
        "sDesRelacionComer": "",
        "sNomCliente": this.forma.controls.nombreCliente.value,
        "scodFuerzavta": this.formaCli.controls.codFuerzaVenta.value
      }
      console.log('request busqueda', this.requestBuscar);

      this.administracionComercialService.getBuscaEstructuraComercial(this.requestBuscar)
      .subscribe((data:any)=>{
        console.log('data',data);
        
        if(data.clientes){
          if(data.clientes.length === 0 && data.error.codError === 0){
            swal('Atención', 'No se encontraron resultados', 'info');
            this.cargando = false;
          }else{
            (this.datosCliente = data.clientes);
            this.cargando = false;
          }
        }     
        if(data.error.codError != 0){
          this.bsModalRef.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        } 
      },
      error =>{
        this.bsModalRef.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })

    }

  }

  enviarInfo(dato){
    this.consultasService.setDatos(dato);
    this.bsModalRef.hide();
  }

}
