import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, NgForm } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ClientesService } from '../../../core/consultaService/clientes.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { UtilGeoPopUpComponent } from '../utilGeoPopUp/utilGeoPopUp.component';
import { PedidosService } from '../../../core/consultaService/pedidos.service';
import { log } from 'util';

const swal = require('sweetalert');
declare var jQuery: any;
declare var $: any;
@Component({
  selector: 'app-moverGestionPopUp',
  templateUrl: './moverGestionPopUp.component.html',
  styleUrls: ['./moverGestionPopUp.component.scss']
})
export class MoverGestionPopUpComponent implements OnInit {
  forma: FormGroup;
  subData: any;
  origen: string;
  titulo: string;
  utilGeoModal: BsModalRef;
  seleccionRecorrido = 0;
  seleccionRuta = 0;
  parametro: string;
  user: any = {};
  sucursal: any = {};
  request: any = {};
  datos: Array<any> = [];
  datosOriginal: Array<any> = [];
  combo: string;
  cargando = false;
  moverPedidos = 0;
  codigoSucursal: string;
  recorridoSeleccionado: any;
  diaFiltro: any;

  constructor(fb: FormBuilder, private clientesService: ClientesService,
    private setearDatos: ConsultasService, public moverGestionModal: BsModalRef,
    private modalService: BsModalService, public loader: NgxSpinnerService,
    private authService: AuthService, private pedidosService: PedidosService) { }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
    this.sucursal = JSON.parse(localStorage.getItem("SucursalUsuario"));
    this.setearModal();
  }
  setearModal() {
    switch (this.origen) {
      case 'recorridoRuta': {
        this.titulo =  'Recorridos';
        this.codigoSucursal = this.sucursal.sus_scodsucursal;
        this.request.nRutOperador = Number(this.user.nrutoperador);
        this.request.sCodSucursal = this.sucursal.sus_scodsucursal;
        this.pedidosService.getMoverRutas(this.request)
          .subscribe((data: any) => {
              (this.datos = data.buscaRecorridosDefinicionDTO);
              this.cargando = false;
              
              if (data.error.codError !== 0) {
                this.moverGestionModal.hide();
                swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
              }
          },
          error => {
            this.moverGestionModal.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          });
        break;
      }
      case 'moverPedidos': {
        this.titulo =  'Recorridos';
        this.request.nRutOperador = Number(this.user.nrutoperador);
        this.request.sCodSucursal = this.sucursal.sus_scodsucursal;
        console.log(this.request);
        this.pedidosService.getMoverPedidos(this.request)
        .subscribe((data: any) => {
          console.log(data);
            this.cargando = false;
            if (this.parametro === 'pedido' && this.combo === 'ruta') {
              this.datosOriginal = data.buscaRecorridosRutaDefinicion2;
              console.log(this.datosOriginal);
              this.datosOriginal.forEach(datosItem => {
                if (datosItem.rut_scodrecorrido === this.recorridoSeleccionado) {
                  let previo = {};
                  previo = datosItem;
                  this.datos.push(previo);
                }
              });
              console.log(this.datos);
            } else {
              this.datosOriginal = data.buscaRecorridosRutaDefinicion;
              this.datosOriginal.forEach(datosItem => {
                if (datosItem.dre_sdia === this.diaFiltro) {
                  let previo = {};
                  previo = datosItem;
                  this.datos.push(previo);
                }
              });
              console.log(this.datos);
            }
            if (data.error.codError !== 0) {
              this.moverGestionModal.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        },
        error => {
          this.moverGestionModal.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
        break;
      }
   }
  }
  enviarInfo(dato) {
    this.pedidosService.setDatos(dato);
    this.moverGestionModal.hide();
  }
}
