import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';

import { CajaCobranzaService } from '../../../core/consultaService/cajaCobranza.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { ActivatedRoute, Router } from '@angular/router';

import { NgxSpinnerService } from 'ngx-spinner';

const swal = require('sweetalert');

@Component({
  selector: 'app-confirmacion-reversar-pago-pop-up',
  templateUrl: './confirmacionReversarPagoPopUp.component.html',
  styleUrls: ['./confirmacionReversarPagoPopUp.component.scss']
})
export class ConfirmacionReversarPagoPopUpComponent implements OnInit {

  titulo: string;
  subtitulo: string;
  request: any = {};
  cargando = false;
  password: string;
  usuario: string;
  submitted = false;
  user: any = {};

  constructor(
    public bsModalRef: BsModalRef,
    private cajaCobranzaService: CajaCobranzaService,
    private authService: AuthService,
    public cargador: NgxSpinnerService,
    private setearDatos: ConsultasService,
    private router: Router
  ) {

  }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
  }

  cancelar() {
    this.bsModalRef.hide();
  }

  submit() {
    this.submitted = true;
    this.cargador.show();
    this.cajaCobranzaService.creaReversaPago(this.request)
      .subscribe((data: any) => {
        this.bsModalRef.hide();
        this.cargador.hide();
        if (data) {
          if (data.error.codError === 500) {
            swal('Atención', ' No se puede reversar porque uno o mas documentos estan Pendientes.', 'error');
            return;
          }
          if (data.error.codError !== 0) {
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            return;
          }
          swal('Bien', 'La reversa de pagos se ha guardado correctamente.', 'success');
          //this.router.navigate(['/cajaCobranza/agregarReversaPago']);
        } else {
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        }
        this.bsModalRef.hide();
      },
        error => {
          this.cargador.hide();
          this.bsModalRef.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });

  }

}
