import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, NgForm } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ClientesService } from '../../../core/consultaService/clientes.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { PedidosService } from '../../../core/consultaService/pedidos.service';


const swal = require('sweetalert');

@Component({
  selector: 'app-rutAsociadoPopUp',
  templateUrl: './rutAsociadoPopUp.component.html',
  styleUrls: ['./rutAsociadoPopUp.component.scss']
})
export class RutAsociadoPopUpComponent implements OnInit {

  forma: FormGroup;
  request: any = {};
  nombre: string;
  titulo: string;
  origen: string;
  rutClientes: string;
  rutOperador: string;
  clientes: Array <any> = [];
  rutoperador: string;
  rutCliente: string;
  pageActual = 1;
  user: any = {};

  constructor(fb: FormBuilder, private clientesService: ClientesService,
    private setearDatos: ConsultasService, public rutAsociadoModal: BsModalRef,
    private modalService: BsModalService, public loader: NgxSpinnerService,
    private authService: AuthService, private pedidosService: PedidosService) {

      this.forma = new FormGroup ({
        nombreCliente: new FormControl(''),
      });
   }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
  }

  buscar($event: any) {
    if (!this.forma.invalid) {
        this.loader.show();
        if (this.origen === 'rutAsociado') {
          this.request.nombre = this.forma.controls.nombreCliente.value;
          this.request.rutClientes = 0;
          this.request.rutOperador = this.user.nrutoperador;
          this.clientesService.getClientes(this.request)
              .subscribe((data: any) => {
                  this.loader.hide();
                  if (data) {
                    if (data.cliente.length === 0 && data.error.codError === 0 ) {
                      swal('Atención', 'No se encontraron resultados', 'info');
                    } else {
                        if (data.cliente === null) {
                            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                        } else {
                          (this.clientes = data.cliente);
                      }
                    }
                    if (data.error.codError !== 0) {
                      swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                    }
                  } else {
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                  }
              },
              error => {
                  this.loader.hide();
                  swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
              }
          );
        } else {
          if (this.origen === 'clientePedido') {
            console.log('PEDIDOS');
            this.request.nombre = this.forma.controls.nombreCliente.value;
            this.request.rutCliente = 0;
            this.request.rutOperador = this.user.nrutoperador;
            this.pedidosService.getClientesPedido(this.request)
                .subscribe((data: any) => {
                    this.loader.hide();
                    if (data) {
                      if (data.buscaClientes.length === 0 && data.error.codError === 0 ) {
                        swal('Atención', 'No se encontraron resultados', 'info');
                      } else {
                          if (data.buscaClientes === null) {
                              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                          } else {
                            (this.clientes = data.buscaClientes);
                        }
                      }
                      if (data.error.codError !== 0) {
                        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                      }
                    } else {
                      swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                    }
                },
                error => {
                    this.loader.hide();
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                }
            );
          }
        }
    }
  }
  enviarInfo(cliente) {
    console.log('envio');
    this.setearDatos.setDatos(cliente);
    this.rutAsociadoModal.hide();
  }
}
